// Base Theme Chiclets
.chiclets-container {
	font-size: 0; // maintain chiclet spacing
	padding: 0.5rem 0;
	min-height: 2.875rem; // 30px chiclets + 16px padding = 46px
}

.chiclets-label {
	// when outside of the .chiclets-container, this behaves as a header
	// when inside, it gets attributes to align with the chiclets
	@include table-header();
	line-height: 1;
	margin: 0.5rem 0 -0.25rem 0;
}

.chiclet,
.chiclets-container .chiclets-label {
	display: inline-block;
	vertical-align: middle;
	line-height: 1rem;
	margin: 0.25rem 0.5rem 0.25rem 0;
}

.chiclets-container .chiclets-label {
	padding: 0.1875rem 0; // 2px chiclet padding + 1px border = 3px
}

.chiclet {
	font-size: 0.875rem; // 14px
	padding: 0.125rem 0.625rem;
	background-color: $white;
	color: $gray-4;
	border: 1px solid $gray-4;
	@include border-radius($badge-pill-border-radius);
	position: relative;
	animation: chicletEnter 0.15s ease-out forwards;
	@include transition(color 0.15s linear, border-color 0.15s linear);

	@include hover-focus {
		color: $gray-2;
		border-color: $gray-2;

		.chiclet-close {
			background-image: url($chiclet-close-hover-bg);
		}
	}

	&:focus {
		outline: 0;
		box-shadow: $btn-box-shadow-focus;
	}

	&:active {
		top: 1px;
	}

	&.disabled,
	&:disabled {
		cursor: not-allowed;
		color: $gray-5;
		border-color: $gray-6;

		.chiclet-close {
			display: none;
		}
	}
}

.chiclet-close {
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin: 2px 0 0 0.5rem;
	background: transparent url($chiclet-close-bg) no-repeat;
}

@include media-breakpoint-down(sm) {
	.chiclets-scroller-sm {
		position: relative;
		margin-left: -$spacer-grid;
		margin-right: -$spacer-grid;

		.chiclets-container {
			padding-left: $spacer-grid;
			padding-right: $spacer-grid * 2;
			white-space: nowrap;
			overflow-y: hidden;
			overflow-x: auto;
			-ms-overflow-style: none;
			-webkit-overflow-scrolling: touch;

			&::-webkit-scrollbar {
				display: none;
			}
		}

		&::after {
			content: '';
			position: absolute;
			top: 0.5rem;
			bottom: 0.5rem;
			right: 0;
			width: $spacer-grid * 2;
			z-index: 1;
			background: linear-gradient(to left, rgba($white, 1) 25%, rgba($white, 0.1) 100%);
			pointer-events: none;
		}
	}
}

// FIXES for LEGACY MARKUP
.chiclet:not(button) {
	padding-right: 2rem;

	.chiclet-close {
		text-indent: -9999px;
		position: absolute;
		top: -1px;
		left: -1px;
		width: calc(100% + 2px);
		height: calc(100% + 2px);
		margin: 0;
		border: 0;
		@include border-radius($badge-pill-border-radius);
		background-position: right 0.75rem center;

		&:focus {
			outline: 0;
			box-shadow: $btn-box-shadow-focus;
		}
	}

	&.disabled {
		padding-right: 0.625rem;
	}
}

@keyframes chicletEnter {
	0% {
		transform: translate3d(-8px, 0, 0);
		opacity: 0;
	}

	100% {
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}
