// Base class
.tooltip {
	position: absolute;
	z-index: $zindex-tooltip;
	display: block;
	// Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.
	// So reset our font and text properties to avoid inheriting weird values.
	@include reset-text();
	font-size: $font-size-sm;
	opacity: 0;

	&.tooltip-modal {
		z-index: $zindex-tooltip-modal;
	}

	&.in {
		opacity: $tooltip-opacity;
	}

	&.tooltip-top,
	&.bs-tether-element-attached-bottom {
		padding: $tooltip-arrow-width $spacer-grid;
		margin-top: -$tooltip-space;

		.tooltip-arrow {
			bottom: 0;
			left: 50%;
			margin-left: -$tooltip-arrow-width;
			border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
			border-top-color: $tooltip-arrow-color;
		}
	}

	&.tooltip-bottom,
	&.bs-tether-element-attached-top {
		padding: $tooltip-arrow-width $spacer-grid;
		margin-top: $tooltip-space;

		.tooltip-arrow {
			top: 0;
			left: 50%;
			margin-left: -$tooltip-arrow-width;
			border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
			border-bottom-color: $tooltip-arrow-color;
		}
	}

	&.tooltip-right,
	&.bs-tether-element-attached-left {
		padding: 0 $tooltip-arrow-width;
		margin-left: $tooltip-space;

		.tooltip-arrow {
			top: 50%;
			left: 0;
			margin-top: -$tooltip-arrow-width;
			border-width: $tooltip-arrow-width $tooltip-arrow-width $tooltip-arrow-width 0;
			border-right-color: $tooltip-arrow-color;
		}
	}

	&.tooltip-left,
	&.bs-tether-element-attached-right {
		padding: 0 $tooltip-arrow-width;
		margin-left: -$tooltip-space;

		.tooltip-arrow {
			top: 50%;
			right: 0;
			margin-top: -$tooltip-arrow-width;
			border-width: $tooltip-arrow-width 0 $tooltip-arrow-width $tooltip-arrow-width;
			border-left-color: $tooltip-arrow-color;
		}
	}
}

// Arrows
.tooltip-arrow,
.tooltip-toggle::after {
	position: absolute;
	width: 0;
	height: 0;
	border-color: transparent;
	border-style: solid;
}

.tooltip-toggle {
	position: relative;

	&::after {
		content: '';
		opacity: 0;
		transition: opacity 0.15s linear;
		left: 50%;
		margin-left: -$tooltip-arrow-width;
	}

	&.tooltip-active::after {
		opacity: $tooltip-opacity;
	}
}

.tooltip-toggle-top::after {
	top: -($tooltip-arrow-width + $tooltip-space);
	border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
	border-top-color: $tooltip-arrow-color;
}

.tooltip-toggle-bottom::after {
	bottom: -($tooltip-arrow-width + $tooltip-space);
	border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
	border-bottom-color: $tooltip-arrow-color;
}

// Wrapper for the tooltip content
.tooltip-inner {
	max-width: $tooltip-max-width;
	padding: $spacer-grid;
	color: $tooltip-color;
	text-align: left;
	background-color: $tooltip-bg;
	@include border-radius($border-radius);

	a {
		@include inverse-links;
	}
}


// Accessible Toggletips

.toggletip {
	position: relative;
}

.toggletip-btn {
	background-color: transparent;
	border: 1px solid $aqua;
	border-radius: 50%;
	color: $aqua;
	font-size: 0.875rem;
	font-weight: 700;
	height: 18px;
	line-height: 1;
	padding: 0;
	width: 18px;
}

// The bubble element, added inside the toggletip live region
// The position of the bubble and arrow are handled via javascript
.toggletip-bubble {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	z-index: $zindex-tooltip;
	width: 17rem; // Allow bubble to be as wide as .toggletip-bubble-inner

	.toggletip-bubble-inner {
		background: $tooltip-bg;
		border-radius: $border-radius;
		color: $white;
		display: inline-block;
		font-size: 0.875rem;
		padding: $spacer-grid;
		position: absolute;
		max-width: 17rem;
	}

	.tooltip-arrow {
		border-color: transparent;
		border-style: solid;
		// Default size so js can calculate dimensions
		border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
		height: 0;
		position: absolute;
		width: 0;
		z-index: $zindex-tooltip;
	}

	// One of these will always be applied
	&.arrow-b {

		.tooltip-arrow {
			border-top-color: $tooltip-arrow-color;
		}
	}

	&.arrow-t {

		.tooltip-arrow {
			border-bottom-color: $tooltip-arrow-color;
			border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
		}
	}
}

