// Pagination

.pagination {
	display: inline-block;
	font-weight: 500;
	margin: 0 auto;
	padding: 0;
}

.page-item {
	display: inline; // Remove list-style and block-level defaults

	&.arrow {

		.page-link { // Adjust padding for the < and > icon font arrows
			padding-right: 0.5em;
			padding-left: 0.5em;
		}
	}

	&:first-child {
		.page-link {
			@include border-left-radius($border-radius);
			margin-left: 0;
		}
	}

	&:last-child {
		.page-link {
			@include border-right-radius($border-radius);
		}
	}

	.page-link {
		[class^='ico-'],
		[class*=' ico-'] {
			line-height: 0;
		}
	}

	&.active .page-link {
		@include plain-hover-focus {
			background-color: $pagination-active-bg;
			border-color: $pagination-active-border;
			color: $pagination-active-color;
			cursor: default;
			z-index: 2;
		}
	}

	&.disabled .page-link {
		@include plain-hover-focus {
			border-color: $pagination-disabled-border;
			color: $pagination-disabled-color;
			cursor: $cursor-disabled;
		}
	}
}

.page-link,
.page-gap { // The gap is the  that separates a page in a series with the last page
	background-color: $pagination-bg;
	border: $pagination-border-width solid $pagination-border-color;
	float: left; // Collapse white-space
	line-height: $line-height;
	margin-left: 1px;
	padding: $pagination-padding-y $pagination-padding-x;
	position: relative;
	text-decoration: none;
}

.page-link {
	color: $pagination-color;
	transition: all 0.2s linear;

	@include hover-focus {
		background-color: $pagination-hover-bg;
		border-color: $pagination-hover-border;
		color: $pagination-hover-color;
	}
}

// Inversed

.pagination-inverse {

	.page-item {

		&.active .page-link {
			@include plain-hover-focus {
				background-color: $pagination-inverse-active-bg;
				border-color: $pagination-inverse-active-border;
				color: $pagination-inverse-active-color;
			}
		}

		&.disabled .page-link {
			@include plain-hover-focus {
				border-color: $pagination-inverse-disabled-border;
				color: $pagination-inverse-disabled-color;
			}
		}
	}

	.page-link,
	.page-gap {
		background-color: $pagination-inverse-bg;
	}

	.page-link {

		@include hover-focus {
			background-color: $pagination-inverse-hover-bg;
			color: $pagination-hover-color;
		}
	}
}

// Sizing

.pagination-lg {
	@include pagination-size($pagination-padding-y-lg, $pagination-padding-x-lg, $font-size-lg, $line-height-lg, $border-radius-lg);
}

.pagination-sm {
	@include pagination-size($pagination-padding-y-sm, $pagination-padding-x-sm, $font-size-sm, $line-height-sm, $border-radius-sm);
}
