// General Text Defaults
body {
	position: relative;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	// line-height, font-family, and font-size are handled in _reboot.scss
}

// Font Stack Classes

.sans-serif {
	font-family: $font-family-sans-serif;
}

.sans-serif-light {
	font-weight: 200;
}

.sans-serif-heavy {
	font-weight: 900;
}


// 	Headings
// scss-lint:disable SingleLinePerSelector
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6,
.display-1, .display-2, .display-3, .display-4 {
	color: $headings-color;
	text-rendering: optimizelegibility;
}
// scss-lint:enable SingleLinePerSelector

h1 {
	@include responsive-headers($h1-properties);
}

h2 {
	@include responsive-headers($h2-properties);
}

h3 {
	@include responsive-headers($h3-properties);
}

h4 {
	@include responsive-headers($h4-properties);
}

h5 {
	@include responsive-headers($h5-properties);
}

h6 {
	@include responsive-headers($h6-properties);
}
// scss-lint:enable SingleLinePerSelector

// These declarations are kept separate from and placed after
// the previous tag-based declarations so that the classes beat the tags in
// the CSS cascade, and thus <h1 class="h2"> will be styled like an h2.
.h1 {
	@include responsive-headers($h1-properties);
}

.h2 {
	@include responsive-headers($h2-properties);
}

.h3 {
	@include responsive-headers($h3-properties);
}

.h4 {
	@include responsive-headers($h4-properties);
}

.h5 {
	@include responsive-headers($h5-properties);
}

.h6 {
	@include responsive-headers($h6-properties);
}


// Static Headings
// These heading leverage the xs breakpoint values and do no change with media queries
// scss-lint:disable QualifyingElement
h1.h-static,
.h1.h-static {
	font-size: map-deep-get($h1-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h1-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h1-properties, $bp-xs, 'margin-bottom');
}

h2.h-static,
.h2.h-static {
	font-size: map-deep-get($h2-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h2-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h2-properties, $bp-xs, 'margin-bottom');
}

h3.h-static,
.h3.h-static {
	font-size: map-deep-get($h3-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h3-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h3-properties, $bp-xs, 'margin-bottom');
}

h4.h-static,
.h4.h-static {
	font-size: map-deep-get($h4-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h4-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h4-properties, $bp-xs, 'margin-bottom');
}

h5.h-static,
.h5.h-static {
	font-size: map-deep-get($h5-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h5-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h5-properties, $bp-xs, 'margin-bottom');
}

h6.h-static,
.h6.h-static {
	font-size: map-deep-get($h6-properties, $bp-xs, 'font-size');
	line-height: map-deep-get($h6-properties, $bp-xs, 'line-height');
	margin-bottom: map-deep-get($h6-properties, $bp-xs, 'margin-bottom');
}

// Adaptive Headings
// These headings scale differently than the responsive headings in that they may only
// change size once or twice to make sure they fit in the context of their layout
h1.h-scaled,
.h1.h-scaled {
	@include responsive-headers($h1-scaled-properties);
}

h2.h-scaled,
.h2.h-scaled {
	@include responsive-headers($h2-scaled-properties);
}

h3.h-scaled,
.h3.h-scaled {
	@include responsive-headers($h3-scaled-properties);
}

// scss-lint:enable QualifyingElement

.h-th { // header styled like a table head
	@include table-header();
	line-height: $line-height;
	margin-bottom: $spacer-half;
}

.section-header {
	text-align: center;
	margin-bottom: $spacer;

	&::after {
		background: lighten($gray, 35%);
		content: '';
		display: block;
		height: 2px;
		margin: 0.5em auto 0;
		width: 60px;
	}
}

.display-1 {
	@include responsive-headers($display1-properties);
}

.display-2 {
	@include responsive-headers($display2-properties);
}

.display-3 {
	// undefined
}

.display-4 {
	@include responsive-headers($display4-properties);
}


// Display Numbers
// Used for visually enhanced numeric lockups

.display-number {
	display: inline-block;
	white-space: nowrap;

	// Superscript characters like $ or %, additionally for decimal values like cents
	&-sup {
		display: inline-block;
		font-size: 0.5em;
		vertical-align: super;
	}

	&-units {
		font-size: 0.5em;
		font-weight: 600;
	}

	&-lg {
		font-size: 1.5rem;
	}

	&-lg &-value,
	&-xl &-value {
		font-weight: normal;
	}

	&-xl {
		font-size: 3rem;
	}

	&-xl &-units {
		font-size: 0.333em;
	}

	// Adjust spacing between value/units when nothing superscript is in between
	&-value + &-units {
		margin-left: -0.125em;
	}

	// Adjust spacing when units directly follows superscript
	&-sup + &-units {
		margin-left: -0.25em;
	}

	// weight adjustment within display-2 light headings
	.display-2 & {
		font-weight: 700;
	}
}


// Standard Element Spacing
// TO DO - build utilitiy classes that manipulate element spacing
// scss-lint:disable SingleLinePerSelector
p, ul, ol, dl, pre, blockquote, .figure, .table, .table-simple, .btn-wrap {
	margin-bottom: $spacer;
}

// scss-lint:enable SingleLinePerSelector

// lead paragraphs
.lead {
	font-size: $lead-font-size;
	font-weight: $lead-font-weight;
}


// 	Horizontal rules

hr {
	border: 0;
	border-top: $hr-border-width solid $hr-border-color;
	height: 0;
	margin-top: $spacer-y;
	margin-bottom: $spacer-y;
}

.hr-short { // matches look of .section-header rule, but follows text alignment and responds to color classes
	border: 0;
	height: 2px;
	color: lighten($gray, 35%);

	&::after {
		display: inline-block;
		vertical-align: top;
		content: '';
		background-color: currentColor;
		width: 60px;
		height: 2px;
	}
}


// Text Sizing
.base-font-size { // for reset purposes
	font-size: $font-size-base;
	line-height: $line-height;
}

small,
.small {
	font-size: 0.875rem; // 14px
	font-weight: normal;
}

.large {
	font-size: 1.125rem; // 18px
	line-height: 1.33333; // 24px
}

.micro {
	font-size: 0.8125rem; // 13px
}

.nano {
	font-size: 0.75rem; // 12px
}

mark,
.mark {
	padding: 0.2em;
	background-color: lighten($brand-primary, 50%);
}

// TO DO - .p-sm and .list-sm support should be dropped in favor of .small
.p-sm,
.list-sm {
	font-size: 0.875em;
}


// 	Blockquote
.blockquote {
	font-weight: $blockquote-font-weight;
	font-size: $blockquote-font-size;

	@include media-breakpoint-up(md) {
		font-size: $blockquote-font-size-lg;
	}

	p {
		margin-bottom: 0.75rem;
	}
}

.blockquote-footer {
	color: $blockquote-small-color;
	display: block;
	font-size: 1.125rem;
	font-style: normal;

	&::before {
		content: '\2014 \00A0'; // em dash, nbsp
	}

	@include media-breakpoint-up(md) {
		font-size: 1.375rem;
	}
}


// Opposite alignment of blockquote
.blockquote-reverse {
	text-align: right;
}

.blockquote-reverse .blockquote-footer {
	&::before {
		content: '';
	}

	&::after {
		content: '\00A0 \2014'; // nbsp, em dash
	}
}


// Lists
.list-unstyled {
	@include list-unstyled;
}

// Inline turns list items into inline-block
.list-inline {
	@include list-unstyled;
}

.list-inline-item {
	display: inline-block;

	&:not(:last-child) {
		margin-right: $list-inline-padding;
	}
}

// Description Lists
dt {
	color: $dt-font-color;
}

// Horizontal description lists w/ grid classes
.dl-horizontal {
	@include make-row(); // fixes BS4 bug
}

// List item spacing (ordered & unordered)
.list-bulleted,
.list-dashed,
.list-ordered {
	li {
		margin-bottom: 0.5em;
	}

	> li:last-child {
		margin-bottom: 0;
	}
}

.list-bulleted ul,
.list-dashed ul {
	margin-top: 0.5em;
}

// Unordered Lists

// Bulleted & Dashed
.list-bulleted,
.list-dashed,
.list-bulleted ul,
.list-dashed ul {
	padding-left: 1.25em;
}

.list-dashed,
.list-dashed ul {
	list-style: none;
}

.list-dashed {
	li {
		&::before {
			content: '-';
			font-size: 1.125em;
			line-height: 1;
			margin: 0 0.5em 0 -1em;
		}
	}
}

// Centered
.list-centered,
.list-centered-grid {
	list-style: none;
	padding-left: 0;
	text-align: center;

	li:nth-child(n+2)::before {
		background: $gray-light;
		content: '';
		display: block;
		height: 1px;
		width: 24px;
		margin: 0.5em auto;
	}
}

// Centered using the grid
.list-centered-grid {

	@include media-breakpoint-up(sm) {
		@include make-row();
		display: flex;
		text-align: left;

		li {
			@include make-col();
			@include make-col-span(6);
			text-align: center;

			// Override previous nth-child state
			&:nth-child(2)::before {
				content: none;
			}
		}
	}

	@include media-breakpoint-up(lg) {

		li {
			@include make-col-span(4);

			// Override previous nth-child state
			&:nth-child(3)::before {
				content: none;
			}
		}
	}
}

// Ordered Lists
.list-ordered {
	display: table;
	list-style: none;
	counter-reset: item;
	padding-left: 0;

	li {
		display: table-row; // table-row automatically removes margin-bottom
		counter-increment: item;
		position: relative;

		// Ordered sub-lists will have a larger number (accounting for the decimal)
		// so they will require more padding (e.g. 3.1, 3.2, 3.3)
		ol {
			counter-reset: item;
			padding-left: 0;
		}

		&::before {
			content: counters(item, '.') ' ';
			display: table-cell;
			font-size: 90%;
			font-weight: 700;
			padding-right: 0.5em;
			text-align: right;
			border-top: 0.5556em solid transparent; // reverses 90% factor
		}
	}

	> li::before {
		content: counters(item, '.') '. ';
	}

	> li:first-child,
	&.centered li {
		&::before {
			border-top: 0;
		}
	}

	&.centered {
		margin-left: auto;
		margin-right: auto;
		text-align: center;

		li {
			display: block;

			&::before {
				display: inline;
			}
		}
	}
}

// Disabled text
.text-disabled {
	opacity: 0.3;
}


// 	Misc

// Builds on `abbr`
.initialism {
	font-size: 0.875rem;
	text-transform: uppercase;
}

// Make sure strikethrough is applied by default
s {
	text-decoration: line-through;
}
