$planner-datebar-height-sm: 3.75rem;
$planner-datebar-height-lg: 4.5rem;

$planner-daypart-height: 2rem;
$planner-daypart-border-width: 0.25rem;

$planner-time-width-sm: 4.25rem;
$planner-time-width-lg: 9.75rem;
$planner-time-gutter: 0.25rem;

$planner-entry-min-height: 3.25rem;

$planner-row-bps: ('md','lg');
$planner-row-gutter-width: $spacer;

.planner {
	background-color: $gray-12;
	padding-bottom: 1.25rem;
}

.planner-datebar {
	color: $white;
	height: $planner-datebar-height-sm;
	padding-top: 0.25rem;
	background-color: rgba($gray-3, 0.91);
	backdrop-filter: blur(2px);
	position: sticky;
	top: 0;
	z-index: 5;
}

// datebar internals
.planner-date-controls {
	display: flex;
	align-items: flex-end;
	margin: 0 -0.75rem;
}

.planner-date-btn {
	@include inverse-links-white;
	background-color: transparent;
	padding: 0.25rem 0.4375rem;
	border-color: transparent !important;

	&:active {
		top: 0;
	}

	&:disabled {
		opacity: 1;
		color: $gray-5 !important;
	}
}

.planner-date-week {
	display: flex;
	justify-content: space-evenly;
	align-items: flex-end;
	flex-grow: 1;

	&:focus-within {
		box-shadow: $btn-box-shadow-focus;
	}

	html[data-whatintent="mouse"] &,
	html[data-whatintent="touch"] & {
		box-shadow: none;
	}
}

.planner-date {
	flex: 1 0 0;
	font-size: 0.625rem;
	text-align: center;
	text-transform: uppercase;
	font-weight: bold;
	line-height: 1;
}

.planner-date-current {
	color: $link-rev-color;
}

.planner-date-month {
	color: $gray-8;
	padding-bottom: 0.25rem;
}

.planner-date-radio {
	position: relative;

	.planner-date-radio-input {
		position: absolute;
		z-index: -1;
		opacity: 0;
	}
}

.planner-date-day {
	display: block;
	border-radius: 50%;
	padding-top: 0.25rem;
	width: 2.25rem;
	height: 2.25rem;
	cursor: pointer;

	&:hover {
		background-color: rgba($gray-2, 0.75);
	}

	.planner-date-no-results:not(.planner-date-current) & {
		color: $gray-8; 
	}

	.planner-date-radio-input:checked + & {
		color: $gray-3;
		background-color: $link-rev-color;

		.planner-date-current & {
			background-color: $white;
		}
	}

	.planner-date-radio-input:disabled + & {
		cursor: not-allowed;
		color: $gray-5;
		background-color: transparent;
	}
}

.planner-date-dayname {
	display: block;
	padding-bottom: 0.125rem;
}

.planner-date-number {
	font-size: 0.875rem;
}

html[data-whatintent="mouse"], // remove custom focus styles unless keyboard
html[data-whatintent="touch"] {
	.planner-date-day {
		box-shadow: none !important;
	}
}

// dayparts 
.planner-daypart {
	display: flex;
	align-items: baseline;
	height: $planner-daypart-height;
	background-color: rgba($gray-12,0.88);
	backdrop-filter: blur(2px);
	position: sticky;
	top: $planner-datebar-height-sm;
	z-index: 3;
	border-top-width: $planner-daypart-border-width;
	padding: $spacer-sm 0;
	border-top-style: solid;
	text-transform: uppercase;
	font-size: 0.75rem;
	font-weight: 600;
	line-height: 1;
	margin: 0 -#{$spacer-grid};

	&::before {
		content: '';
		position: absolute;
		background-color: $gray-12;
		top: -$planner-daypart-border-width;
		width: 1px;
		height: $planner-daypart-border-width;
		left: calc(#{($planner-time-width-sm + $spacer-grid)} + 1px);
		z-index: 4;
	}
}

.planner-daypart-morning {
	border-top-color: #fdc10a;
}

.planner-daypart-midday {
	border-top-color: #49c6ff;
}

.planner-daypart-evening {
	border-top-color: #103baf;
}

.planner-daypart-leadin {
	flex: 0 0 #{($planner-time-width-sm + $spacer-grid)};
	padding-left: $spacer-grid;
	font-size: 0.5625rem;
}

.planner-daypart-label {
	padding: 0 #{($planner-row-gutter-width / 2)};
}

.planner-times {
	padding-left: $planner-time-width-sm;
}

.planner-time {
	position: sticky;
	z-index: 2;
	top: $planner-datebar-height-sm + $planner-daypart-height;
	float: left;
	width: $planner-time-width-sm - $planner-time-gutter;
	margin-left: -$planner-time-width-sm;
	font-size: 0.875rem;
	font-weight: 600;
	box-shadow: inset 0 1px 0 0 $gray-9;
	padding-top: 0.375rem; // 6px
	max-height: $planner-entry-min-height; // keeps float from wrapping incorrectly

	&.planner-time-soon {
		box-shadow: inset 0 2px 0 0 $gray-3;
	}
}

.planner-time-label {
	color: $gray-5;
	font-size: 0.625rem;
	text-transform: uppercase;
	line-height: 1.1;
	margin-top: 0.125rem;
}

.planner-entry {
	position: relative;
	font-size: 0.8125rem;
	min-height: $planner-entry-min-height;
	background-color: $white;
	padding: 0.5rem #{($planner-row-gutter-width / 2)};
	margin-bottom: $spacer-xs;
	border-radius: $border-radius;
}

.planner-entry-title {
	font-size: 0.875rem;
	font-weight: 600;
}

.planner-entry-cta {
	text-align: right;
}

// responsive changes

@include media-breakpoint-up(md) {
	.planner-entry {
		padding: 1rem #{($planner-row-gutter-width / 2)};
	}
}

@include media-breakpoint-up(lg) {
	.planner-datebar {
		height: $planner-datebar-height-lg;
		padding-top: 0.4375rem;
	}

	.planner-date {
		font-size: 0.875rem;
	}

	.planner-date-day {
		padding-top: 0.4375rem;
		width: 2.625rem;
		height: 2.625rem;
	}

	.planner-date-dayname {
		font-size: 0.75rem;
	}

	.planner-date-number {
		font-size: 1rem;
	}

	.planner-daypart {
		top: $planner-datebar-height-lg;

		&::before {
			left: calc(#{($planner-time-width-lg + $spacer-grid)} + 1px);
		}
	}

	.planner-daypart-leadin {
		flex-basis: $planner-time-width-lg + $spacer-grid;
	}

	.planner-times {
		padding-left: $planner-time-width-lg;
	}

	.planner-time {
		top: $planner-datebar-height-lg + $planner-daypart-height;
		width: $planner-time-width-lg - $planner-time-gutter;
		margin-left: -$planner-time-width-lg;
	}
}

@include media-breakpoint-up(xl) {
	.planner-daypart {
		margin: 0;
	}

	.planner-daypart::before {
		left: calc(#{($planner-time-width-lg + $spacer-grid)} + 1px);
	}

	.planner-times {
		padding-left: $planner-time-width-lg + $spacer-grid;
	}
}

// CTA placement for XS and SM breakpoints
@include media-breakpoint-down(sm) {
	.planner-entry-title {
		padding-right: 4.5rem;
	}

	.planner-entry-cta {
		position: absolute;
		top: 0.625rem;
		right: 0.75rem;
		width: 4.5rem;
	}
}

// custom columns for MD and LG breakpoints
@include media-breakpoint-up(nth($planner-row-bps, 1)) {
	.planner-row {
		display: flex;
		align-items: baseline;
		margin-left: ($planner-row-gutter-width / -2);
		margin-right: ($planner-row-gutter-width / -2);
	}

	.planner-col, // generic column to allow for custom flex properties
	[class^='planner-col-'],
	[class*=' planner-col-'] { 
		position: relative;
		min-height: 1px;
		max-width: 100%;
		padding-left: ($planner-row-gutter-width / 2);
		padding-right: ($planner-row-gutter-width / 2);
	}
}

@each $breakpoint in $planner-row-bps {
	@include media-breakpoint-up($breakpoint) {
		@for $i from 1 through $grid-columns {
			.planner-col-#{$breakpoint}-#{$i} {
				@include make-col-span($i, $grid-columns);
			}
		}
	}
}
