//
// Base Theme Spinners

.loading-spinner,
.loading-spinner-lg {
	background-position: 50%;
	background-repeat: no-repeat;
	display: block;
	margin: 0 auto;
}

.loading-spinner {
	background-image: url('#{$image-path}theme-base/spinner.gif');
	background-size: 30px;
	min-height: 30px;
}

.loading-spinner-lg {
	background-image: url('#{$image-path}theme-base/spinner-lg.gif');
	background-size: 60px;
	min-height: 60px;
}

