// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit

// Kill the scroll on the body
.modal-open {
	overflow: hidden;
}

// .modal-full adds this fix for the iOS overflow:hidden scrolling issue
// since the body is not visible behind the modal in this case, the scrolling
// move position:fixed causes is not visible
.modal-body-fixed {
	position: fixed;
	width: 100%;
}

// Container that the modal scrolls within
.modal {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: $zindex-modal;
	display: none;
	overflow: hidden;
	// Prevent Chrome on Windows from adding a focus outline. For details, see
	// https://github.com/twbs/bootstrap/pull/10951.
	outline: 0;
	-webkit-overflow-scrolling: touch;

	// Force-wrap any long content that doesn't have spaces
	word-wrap: break-word;
	overflow-wrap: break-word;

	// When fading in the modal, animate it to slide down
	&.fade .modal-dialog {
		transition: transform 0.3s ease-out;
		transform: translate(0, -25%);
	}

	&.fade .modal-dialog.modal-full {
		transform: translate(0, 100vh);
	}

	&.in .modal-dialog,
	&.in .modal-dialog.modal-full {
		transform: translate(0, 0);
	}

	&.fade .modal-dialog.modal-lightbox {
		transform: translate(-50%, -75%);
	}

	&.in .modal-dialog.modal-lightbox {
		transform: translate(-50%, -50%);
	}
}

.modal-open .modal {
	overflow-x: hidden;
	overflow-y: auto;
}

// Shell div to position the modal with bottom padding
.modal-dialog {
	position: relative;
	width: auto;
	margin: 10px;

	// .modal-full is a modal that fills the full screen, layering up from below
	&.modal-full {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $modal-content-bg;
		margin: 0;
	}

	&.modal-lightbox {
		position: absolute;
		top: 50%;
		left: 50%;
		max-width: 67.5rem;
		width: calc(100% - (#{$spacer-grid} * 2));
		margin: 0;
	}
}

// Actual modal
.modal-content {
	position: relative;
	background-color: $modal-content-bg;
	background-clip: padding-box;
	border: 1px solid $modal-content-border-color;
	@include box-shadow($modal-content-xs-box-shadow);
	// Remove focus outline from opened modal
	outline: 0;

	.modal-full & {
		border: 0;
		box-shadow: none;
	}

	.modal-lightbox & {
		background-color: transparent;
		border: 0;
		box-shadow: none;
	}
}

// Modal background
.modal-backdrop {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: $zindex-modal-bg;
	background-color: $modal-backdrop-bg;

	// Fade for backdrop
	&.fade {
		opacity: 0;
	}

	&.in {
		opacity: $modal-backdrop-opacity;
	}

	// this class added to body only if .modal-lightbox is shown
	.body-lightbox & {
		opacity: 0.80;
	}
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
	padding: $modal-title-padding;
	@include clearfix;
}

.modal-lightbox .modal-header {
	padding: 0;
}

.modal-header-inverse {
	background-color: $modal-header-inverse-bg;
}

.modal-header-inverse,
.modal-lightbox .modal-header {
	.modal-title,
	.close {
		color: $modal-header-inverse-color;
	}

	.close {
		opacity: 1;

		&:hover {
			opacity: 0.75;
		}
		&:active {
			opacity: 0.5;
		}
	}
	
	& + .modal-body {
		padding-top: $spacer;
	}
}

// Close icon
.modal-header .close {
	margin-top: -6px;
	text-shadow: none;
}

.modal-full .close {
	opacity: 1;

	@include hover-focus {
		color: $link-color;
	}
}

// Title text within header
.modal-title {
	margin: 0;
	line-height: $modal-title-line-height;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
	position: relative;
	padding: $modal-inner-padding;

	.modal-lightbox & {
		padding: 0.75rem 0 0;
	}
}

// Footer (for actions)
.modal-footer {
	letter-spacing: 0.5rem; // space out the buttons
	margin-top: -1rem;
	padding: $modal-footer-padding;
	text-align: right; // right align buttons

	.btn {
		margin-top: 1rem;
	}
}

// Measure scrollbar width for padding body during modal show/hide
.modal-scrollbar-measure {
	position: absolute;
	top: -9999px;
	width: 50px;
	height: 50px;
	overflow: scroll;
}

// Scale up the modal
@include media-breakpoint-up(sm) {
	// Automatically set modal's width for larger viewports
	.modal-dialog {
		width: $modal-md;
		margin: 30px auto;
	}

	.modal-content {
		@include box-shadow($modal-content-sm-up-box-shadow);
	}

	// Modal sizes
	.modal-sm {
		width: $modal-sm;
	}

	.modal-lg {
		max-width: $modal-lg;
		width: calc(100% - (#{$spacer-grid} * 2));
	}

	.modal-full {
		width: auto;
	}
}


@include media-breakpoint-up(lg) {
	.modal-lg {
		margin: 30px auto;
		width: $modal-lg;
	}
}
