.media {
	display: flex;
	align-items: flex-start;
	padding: $spacer-grid 0;
	border-top: 1px solid $border-default-color;

	&:last-child {
		border-bottom: 1px solid $border-default-color;
	}
}

.media-body {
	flex: 1;
}

.media-img {
	width: 4rem;
	height: 4rem;
	margin-right: $spacer-grid;

	.media-body + & {
		margin: 0 0 0 $spacer-grid;
	}

	@include media-breakpoint-up(lg) {
		width: 6.25rem;
		height: 6.25rem;

		&.img-circle + .media-body { // circle images space awkwardly when larger
			margin-top: $spacer-grid;
		}
	}
}