// Header
// TO DO for Drew other instances of the .header class need different naming
header.header {
	position: relative;
	z-index: $zindex-navbar-fixed;
}

.header-mobile-bar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: $header-mobile-bar-height;
	border-top: $nav-tabs-border-width solid $navbar-accent-color;
	border-bottom: 1px solid $gray-11;
	font-size: 0.875rem;
	line-height: 1;

	> * {
		flex-basis: 33.3333%;
	}

	.header-nav-toggle {
		text-align: left;
	}

	.header-brand-mobile {
		font-size: 0.75rem;
		text-align: center;

		img {
			max-width: none;
		}

		.header-brand-toggle {
			padding-top: 0.25rem;
			color: $gray-4;
			font-weight: bold;
			white-space: nowrap;
		}
	}

	.header-utility-mobile {
		padding-right: $spacer-grid;
		
		.nav {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			flex-wrap: nowrap;
			text-align: center;
			padding-left: 0.375rem;
		}

		.nav-link {
			margin: 0;
			padding: 0.375rem $nav-link-spacer-y;

			&:not(.nav-link-highlight) {
				color: $gray-5;

				@include hover-focus {
					color: $link-color;
				}
			}
		}
	}
}

.header-global-logo { //app icon in global dropdown
	float: left;
	margin: 0.5rem 0 -0.25rem 1.25rem;
}

.header-utility-dropdown {
	position: absolute;
	top: calc(#{$header-mobile-bar-height} - 1px); // account for border-bottom
	width: 100%;
	z-index: $zindex-navbar-fixed + 1;

	.dropdown-menu,
	.dropdown-arrow {
		left: auto;
		right: 0;
	}

	.dropdown-arrow {
		transform: translateX(50%);
	}

	.dropdown-menu {
		transform: none;
	}
}

.header-collapse {
	position: absolute;
	top: 100%;
	left: 0; right: 0;
	background-color: $gray-12;
	z-index: $zindex-dropdown - 1;
}

.header-collapse-open {
	.dropdown-toggle,
	&.dropdown-toggle {
		&::after {
			content: '\e621';
		}
	}
}

@include media-breakpoint-down(sm) {
	.header-utility-dropdown .dropdown-menu {
		left: 0;
	}
}

@include media-breakpoint-down(md) {
	.header-hide-mobile,
	.header-utility-major {
		display: none;
	}

	.header-nav-open-body,
	.header-collapse-open-body { // force body to top and prevent scrolling when nav or collapse open - mobile only
		overflow: hidden;
		position: fixed;
		width: 100%;
	}

	.header-collapse {
		position: fixed;
		top: $header-mobile-bar-height;
		visibility: visible;
	}
	
	.header-collapse-content { // force collapse to fill remainder of screen
		height: calc(100vh - #{$header-mobile-bar-height});
		overflow: auto;
	}

	.header-nav,
	.header-nav .dropdown-menu {
		position: fixed;
		left: 0;
		right: 0;
		overflow: auto;
		background-color: $white;
		visibility: hidden;
	}

	.header-nav {
		top: $header-mobile-bar-height;
		bottom: 0;
		transform: translateX(-100%);
		padding-bottom: $spacer-grid;

		&.header-nav-animating,
		.dropdown-menu {
			transition: transform 0.2s ease-out, visibility 0.2s ease-out;
		}

		&.header-nav-open {
			visibility: visible;
			transform: translateX(0);
		}

		.dropdown-toggle {
			&::after {
				content: '\e620';
				float: right;
				line-height: 1.5rem;
			}
		}

		.dropdown-menu {
			top: 0;
			height: 100%;
			padding: 0 0 $spacer-grid;
			display: block;
			transform: translateX(100%);
		}

		.open {
			> .dropdown-arrow {
				display: none;
			}

			> .dropdown-menu {
				visibility: visible;
				transform: translateX(0);
			}
		}

		.dropdown-divider {
			height: 1px;
			width: auto;
			background-color: $gray-11;
		}

		.dropdown-item,
		.dropdown-header,
		.nav .nav-link {
			color: $gray-4;
			margin: 0;
			padding: 0.5rem $spacer-grid 0.5rem 1.25rem;
			white-space: normal;

			@include hover-focus {
				color: $link-color;
			}
		}

		.dropdown-columns {
			width: auto;
		}

		.header-dropdown-back {
			color: $body-color;
			font-size: 0.875rem;
			line-height: 1.5rem;
			border-bottom: 1px solid $gray-11;
			margin-bottom: 0.5rem;
			font-weight: bold;
			background-color: $white;
		}

		.nav {
			display: block;
			margin: 0;
		}

		.nav-link {
			background-color: $white; // fixes scroll bug in chrome
		}
	}

	.header-primary {
		border-bottom: 0;

		.nav-link,
		.dropdown-menu {
			background-color: $gray-12;
		}

		.nav-link,
		.dropdown-item {
			&.active {
				background-color: $gray-11;
				border-right: $nav-tabs-border-width solid $navbar-accent-color;
				padding-right: calc(#{$spacer-grid} - #{$nav-tabs-border-width});
			}
		}
	}

	.header-utility-minor {
		border-top: 1px solid $gray-11;
		border-bottom: 1px solid $gray-11;

		.nav-link:not(.nav-link-highlight) {
			background-color: $gray-12;

			@include hover-focus {
				color: $link-color;
			}
		}

		.nav-link.nav-link-highlight {
			color: $link-color;

			@include hover-focus {
				color: $link-hover-color;
			}
		}
	}

	.header-global {

		.nav-link {
			font-size: 0.875rem;
			line-height: 1.5rem;

			&.active {
				border-left: $nav-tabs-border-width solid $navbar-accent-color;
				padding-left: calc(1.25rem - #{$nav-tabs-border-width});
			}
		}

		.dropdown-toggle::after {
			color: $gray-5;
		}
	}
}

@include media-breakpoint-up(lg) {
	.header-hide-desktop,
	.header-dropdown-back,
	.header-mobile-bar {
		display: none;
	}

	.header-nav {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		background-color: $gray-4;
	}

	.header-primary {
		.btn,
		.navbar-brand {
			margin-top: 0.625rem;
			margin-bottom: 0.625rem;
		}

		.dropdown-menu {
			padding: 1.5rem;
		}
		
		.dropdown-item.active::before {
			content: '';
			float: left;
			height: $nav-tabs-border-width;
			width: $nav-tabs-border-width;
			background-color: $navbar-accent-color;
			margin: 0.75em 0 0 -0.5rem;
		}
	}

	.header-global,
	.header-utility {
		font-size: 0.875rem;

		.dropdown-menu {
			padding: $navbar-padding-y $navbar-padding-x;
		}
	}

	.header-global {
		flex: 0 0 auto;
		order: -1;

		.nav,
		.nav-link {
			margin: 0;
		}

		.nav-link {
			color: $white;
			border-right: 1px solid $gray-5;
			padding: 0.625rem $navbar-padding-x;

			@include hover-focus {
				background-color: $gray-3;
			}

			&.active {
				color: $gray-4;
				background-color: $gray-11;
				border-right: 0;
				border-top: $nav-tabs-border-width solid $navbar-accent-color;
				padding-top: calc(0.625rem - #{$nav-tabs-border-width});
				&:not(:first-child) {
					margin-left: -1px;
				}

				@include hover-focus {
					background-color: $gray-10;
				}
			}
		}

		.dropdown-toggle {
			&::after {
				width: 1em;
			}
		}

		.open .dropdown-toggle {
			background-color: $dropdown-inverse-bg;
		}

		.dropdown-menu {
			left: 0;
		}

		.dropdown-divider {
			height: 1px;
			width: auto;
			background-color: $gray-4;
			margin-left: -$spacer-grid;
			margin-right: -$spacer-grid;
		}

		.header-global-logo { 
			margin: 0.5rem $spacer-grid 0 0;
		}
	}

	.header-utility {
		display: flex;
		justify-content: flex-end;
		flex: 1 1 auto;
		padding: 0 $navbar-padding-x;
		min-height: 2.5rem;
		align-items: center;
	}

	.header-utility-major {
		padding-left: $nav-link-spacer-x * 2;
	}

	.header-primary {
		flex: 0 0 100%;
		max-width: 100%;
		order: 1;

		.nav-link {
			line-height: 2.25rem;
		}
	}

	.header-collapse {
		border-bottom: 1px solid $gray-11;
		@include box-shadow($dropdown-box-shadow);
	}
}
