.table-gridlist {
	thead th {
		border: 0;
	}

	tbody {
		tr:nth-of-type(odd) {
			background-color: $table-bg-accent;
		}

		th,
		td {
			border-top: 0;
		}
	}

	@include media-breakpoint-custom(0, ($bp-md - 1)) {
		display: block;

		caption {
			display: block;
		}

		thead {
			display: none;
		}

		tbody {
			display: block;

			tr {
				display: flex;
				flex-wrap: wrap;
				padding: ($table-cell-padding / 2) 0;
			}
		}

		th,
		td {
			display: block;
			@include make-col($table-cell-padding * 2);

			&::before {
				@include table-header();
				display: block;
				content: attr(data-content);
			}

			&.gridlist-add-rule::after {
				content: '';
				position: absolute;
				height: $border-width;
				top: ($table-cell-padding / 2);
				left: $table-cell-padding;
				right: $table-cell-padding;
				background-color: $table-border-color;
			}

		}

		.gridlist-add-rule {
			padding-top: ($table-cell-padding * 1.5) + 0.625rem; // 1px border
		}

		@for $i from 1 through $grid-columns {
			.gridlist-xs-#{$i} {
				flex: 0 0 percentage($i / $grid-columns);
			}
		}

	}

	@include media-breakpoint-custom($bp-sm, ($bp-md - 1)) {
		@for $i from 1 through $grid-columns {
			.gridlist-sm-#{$i} {
				flex: 0 0 percentage($i / $grid-columns);
			}
		}
	}

}
