// Contextual backgrounds
// scss-lint:disable ImportantRule

// Color contrast evaluation from
// https://gist.github.com/jlong/f06f5843104ee10006fe

$red-magic-number: 241;
$green-magic-number: 691;
$blue-magic-number: 68;
$brightness-divisor: $red-magic-number + $green-magic-number + $blue-magic-number;

@function sqrt ($r) {
	$x0: 1;
	$x1: $x0;

	@for $i from 1 through 10 {
		$x1: $x0 - ($x0 * $x0 - abs($r)) / (2 * $x0);
		$x0: $x1;
	}

	@return $x1;
}

@function brightness($color) {
	// Extract color components
	$red-component: red($color);
	$green-component: green($color);
	$blue-component: blue($color);

	// Calculate a brightness value in 3d color space between 0 and 255
	$number: sqrt((($red-component * $red-component * $red-magic-number) + ($green-component * $green-component * $green-magic-number) + ($blue-component * $blue-component * $blue-magic-number)) / $brightness-divisor);

	// Convert to percentage and return
	@return 100% * $number / 255;
}

@function contrasting-color($color, $light, $dark) {
	@if brightness($color) < 65% {
		@return $light;
	} @else {
		@return $dark;
	}
}

@mixin bg-variant($parent, $color) {
	#{$parent} {
		@if brightness($color) < 65% {
			color: $white;
		}
		background-color: $color !important;
	}

	a#{$parent} {
		@include hover-focus {
			background-color: darken($color, 10%);
		}
	}
}

// Repeating Background
// @include background-repeat('filename.svg', '47px 81px', $gray-light);
// $size and $color are optional
@mixin background-repeat($name, $size: false, $color: false, $theme-folder:'theme-base') {
	@if($color) {
		background-color: $color;
	}
	background-image: url('#{$image-path}#{$theme-folder}/#{$name}');
	background-repeat: repeat;
	@if($size) {
		background-size: $size;
	}
}
