@mixin inverse-content() {
	color: $white;
	
	// scss-lint:disable SingleLinePerSelector
	h1, h2, h3, h4, h5, h6,
	.h1, .h2, .h3, .h4, .h5, .h6,
	.display-1, .display-2, .display-3, .display-4 {
		color: inherit;
	}
	// scss-lint:enable SingleLinePerSelector
	.section-header::after,
	.hr-short::after {
		background: $white;
	}

	a:not(.btn) {
		@include inverse-links;
	}

	a.link-underlined,
	.link-underlined a:not(.btn),
	.link-underlined & a:not(.btn) {
		border-bottom-color: rgba($link-rev-color, 0.4);
	}

	.text-muted {
		color: $text-muted-inverse;
	}

	.btn-primary-outline {
		@include button-transparent-outline-variant($link-rev-color, $link-rev-color);
	}

	.btn-secondary-outline {
		@include button-transparent-outline-variant($btn-reversed-border, $btn-reversed-border);
	}
}