// this is all alterations to element states when what-input.js is included
// this is packaged within js/shared

html[data-whatintent="mouse"],
html[data-whatintent="touch"] {

	a:focus {
		outline: 0;
	}

	.btn,
	.c-select {
		&:focus {
			box-shadow: none;
		}
	}

	.c-input > input:focus ~ .c-indicator {
		box-shadow: none;
	}
}

html[data-whatintent="touch"] {
	body {
		cursor: pointer; // forces iOS to fire events on non-interactive elements
	}
}

