.split-banner {
	position: relative;
	background-color: $gray-10;

	&.content-inverse {
		background-color: $gray-2;
	}

	&.split-overlap {
		margin-bottom: -2.25rem; // 36px
		z-index: 0;
	}
}

.split-primary,
.split-secondary {
	position: relative;
	width: 100%;
	text-align: center;
}

.split-bg {
	// scss-lint:disable ImportantRule
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: 1;

	img {
		position: absolute;
		left: 50%;
		top: 0;
		height: 100%;
		width: auto !important;
		max-width: none;
		transform: translateX(-50%);

		@include media-breakpoint-custom(1600px) {
			width: 101% !important; // make sure the edges hit
			height: auto;
			min-height: 100%;
			object-fit: cover;
		}
	}
}

.split-content {
	position: relative;
	z-index: 2;
}

@include media-breakpoint-down(sm) {
	.split-primary,
	.split-secondary {
		padding-top: 2.25rem;
		padding-bottom: 0.75rem;
	}

	.split-primary {
		min-height: 15rem; // 240px
		border-bottom: 1px solid $gray-dark;
	}

	.split-overlap .split-secondary {
		padding-bottom: 3rem; // additional space for negative element
	}
}

@include media-breakpoint-up(md) {
	.split-banner {
		padding-top: 2.25rem;
		padding-bottom: 0.75rem;
		min-height: 21.25rem; // 340px

		&.split-overlap {
			padding-bottom: 3rem; // additional space for negative element
		}
	}

	.split-primary {
		position: static;
	}
}


// Fixed left variation 
// add in CI banner from 'theme-mylt' for backwards compatability

.split-fix-left,
.ci-banner {

	.split-primary,
	.split-secondary {
		@include make-col;
	}

	@include media-breakpoint-down(sm) {
		.split-primary {
			min-height: 15rem; // 240px specificity issue to fix
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}

	@include media-breakpoint-up(sm) {
		.split-secondary {
			flex: 0 0 66.6667%;
			max-width: 66.6667%;
			margin-left: 16.6667%;
		}
	}

	@include media-breakpoint-up(md) {
		&.split-banner {
			display: flex;
			align-items: center;

			> .container {
				width: 100%;
			}
		}

		.split-primary {
			position: static;
			flex: 0 0 auto;
			width: auto;
			max-width: 25%;
		}

		.split-secondary {
			flex: 0 0 50%;
			max-width: 50%;
			margin-left: 4rem;

			&::before {
				position: absolute;
				content: '';
				top: 1.5rem;
				left: -2rem;
				bottom: 3rem;
				border-left: 1px solid currentColor;
				z-index: 2;
			}
		}
	}

	@include media-breakpoint-up(lg) {
		.split-secondary {
			margin-left: 6rem;

			&::before {
				left: -3rem;
			}
		}
	}
}

