
// regular billboard sizes
$bb-height-xs: 11.25rem; //180px
$bb-height-sm: 18.75rem; //300px
$bb-height-md: 27.5rem; //440px

// small billboard sizes
$bbsm-height-xs: 11.25rem; //180px
$bbsm-height-md: 21.25rem; //340px

// large billboard sizes
$bblg-height-xs: 17.5rem; //280px
$bblg-height-sm: 23.75rem; //380px
$bblg-height-md: 30rem; //480px

.billboard {
	position: relative;
	background-color: $gray-lighter;

	&.billboard-bordered {
		box-sizing: content-box;
		border-bottom: 12px solid $brand-primary;
	}
}

.billboard-content {
	box-sizing: border-box;
	width: 100%;
	position: relative;
	z-index: 2;
}

.billboard-img {
	position: relative;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
	min-height: $bb-height-xs;
	overflow: hidden;

	.billboard-sm & {
		min-height: $bbsm-height-xs;
	}

	.billboard-lg & {
		min-height: $bblg-height-xs;
	}

	.billboard-layer-xs & {
		position: absolute;
	}

	img,
	video {
		position: absolute;
		left: 50%;
		top: 0;
		height: 100%;
		width: auto !important;
		max-width: none;
		transform: translateX(-50%);	
	}

	video { // video is not compressive size (ie 2x), so this can be added to it
		min-width: 100%;
		min-height: 100%;
		height: auto;
	}

	&.billboard-img-left-xs img,
	&.billboard-img-left-xs video {
		left: 33%;
		transform: translateX(-33%);
	}

	&.billboard-img-right-xs img,
	&.billboard-img-right-xs video {
		left: 67%;
		transform: translateX(-67%);
	}
}

@include media-breakpoint-up(sm) {
	.billboard-img {
		min-height: $bb-height-sm;

		.billboard-sm & {
			min-height: $bbsm-height-xs;
		}

		.billboard-lg & {
			min-height: $bblg-height-sm;
		}
	}
}

@include media-breakpoint-up(md) {
	.billboard  {
		min-height: $bb-height-md;
		display: flex;
		align-items: center;

		&.billboard-sm  {
			min-height: $bbsm-height-md;
		}

		&.billboard-lg  {
			min-height: $bblg-height-md;
		}

		// TO DO : delete this when video that fits in correct dimensions is produced
		&.billboard-poc  {
			min-height: 32.5rem;
		}

		&.billboard-content-top {
			align-items: flex-start;
		}

		&.billboard-content-bottom {
			align-items: flex-end;
		}
	}

	.billboard-img {
		position: absolute;

		&.billboard-img-center-md img {
			left: 50%;
			transform: translateX(-50%);
		}

		&.billboard-img-left-md img {
			left: 33%;
			transform: translateX(-33%);
		}

		&.billboard-img-right-md img {
			left: 67%;
			transform: translateX(-67%);
		}
	}
}

@include media-breakpoint-custom(1280px) {
	.billboard-content {
		padding-left: 3%;
		padding-right: 3%;

		.row & {
			padding-left: $spacer-grid;
			padding-right: $spacer-grid;
		}
	}
}

@include media-breakpoint-custom(1600px) {
	.billboard-img img {
		width: 101% !important; // make sure the edges hit
		height: auto;
		min-height: 100%;
		object-fit: cover;
	}
}

// Billboard Group
.billboard-group {
	@include make-container;

	> .row > [class^='col-'],
	> .row > [class*=' col-'] {
		padding: 0;
		display: flex;
	}

	.billboard,
	.billboard-content {
		width: 100%;
	}
}

@media screen and (min-width: 768px) and (-ms-high-contrast: active),
screen and (min-width: 768px) and (-ms-high-contrast: none) {
	// IE11 only fix. This adds another flex item with the parent's min-height value as its height
	// https://github.com/philipwalton/flexbugs#3-min-height-on-a-flex-container-wont-apply-to-its-flex-items
	.billboard::before {
		content: '';
		display: block;
		width: 0;
		height: $bb-height-md;
	}

	.billboard.billboard-sm::before {
		height: $bbsm-height-md;
	}

	.billboard.billboard-lg::before {
		height: $bblg-height-md;
	}
}
