//
// Base Theme Cards

// Card Defaults

.card {
	position: relative;
	display: block;
	margin-bottom: $card-spacer-y;
	background-color: $card-bg;
	border: $card-border-width solid $card-border-color;
	word-wrap: break-word; // old syntax
	overflow-wrap: break-word;
}

.card-block {
	padding: $card-spacer-y $card-spacer-x 0;
}

.card-title {
	// margin-bottom: $card-spacer-y;
}

.card-subtitle {
	margin-top: -($card-spacer-y / 2);
	margin-bottom: $card-spacer-y;
}

.card-link + .card-link {
	margin-left: $card-spacer-x;
}

// scss-lint:disable QualifyingElement
a[href].card,
button.card {
	@include card-hover-focus-border;
	color: $body-color;
	text-decoration: none;

	.card-title,
	.card-link,
	.card-link-underlined {
		color: $link-color;
	}

	.card-link-underlined {
		border-bottom: 2px solid rgba($link-color, 0.4);
		transition: border-color 0.15s linear, color 0.15s linear;
	}

	&:focus,
	&:hover {
		.card-title,
		.card-link,
		.card-link-underlined {
			color: $link-hover-color;
		}

		.card-link-underlined {
			border-bottom: 2px solid $link-hover-color;
		}
	}

	&:active {
		.card-title,
		.card-link,
		.card-link-underlined {
			color: $link-active-color;
		}

		.card-link-underlined {
			border-bottom: 2px solid $link-active-color;
		}
	}
}


// Make .card <button> elements look like <a>

// scss-lint:disable QualifyingElement
button.card {
	width: 100%;
	text-align: left;
}


// Card Images

.card-img-top,
.card-img-bottom,
.card-img {
	margin-bottom: $card-spacer-0;
}

.card-img-overlay {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding: $card-spacer-y $card-spacer-x 0;
}


// Card Header & Footer

.card-header {
	padding: $card-spacer-y $card-spacer-x 0 $card-spacer-x;
	background-color: $card-cap-bg;
	border-bottom: $card-border-width solid $card-border-color;
}

.card-footer {
	padding: $card-spacer-y $card-spacer-x 0 $card-spacer-x;
	background-color: $card-cap-bg;
	border-top: $card-border-width solid $card-border-color;
}


// Blockquote

.card-blockquote {
	padding: 0;
	border-left: 0;
}


// Background Variants

.card-primary {
	@include card-variant($brand-primary, $brand-primary);
}

.card-secondary {
	@include card-variant($brand-secondary, $brand-secondary);
}

.card-success {
	@include card-variant($brand-success, $brand-success);
}

.card-info {
	@include card-variant($brand-info, $brand-info);
}

.card-warning {
	@include card-variant($brand-warning, $brand-warning);
}

.card-danger {
	@include card-variant(mix($card-bg, $brand-danger, 96%), $card-border-color);
}

// Remove all backgrounds
.card-primary-outline {
	@include card-outline-variant($btn-primary-bg);
}

.card-secondary-outline {
	@include card-outline-variant($btn-secondary-border);
}

.card-info-outline {
	@include card-outline-variant($btn-info-bg);
}

.card-success-outline {
	@include card-outline-variant($btn-success-bg);
}

.card-warning-outline {
	@include card-outline-variant($btn-warning-bg);
}

.card-danger-outline {
	@include card-outline-variant($btn-danger-bg);
}


// Inverse text within a card for use with dark backgrounds

.card-inverse {
	@include card-inverse;
}


// Card set
.card-deck {
	@include media-breakpoint-between(sm, md) {
		// maintain regular card spacing after deck
		margin-bottom: ($card-spacer-y - $spacer-grid);

		.card {
			margin-bottom: $spacer-grid;
		}

		.card-block {
			padding: $spacer-grid $card-spacer-x 0;
		}
	}

	@include media-breakpoint-up(sm) {
		display: flex;
		flex-flow: row wrap;
		margin-right: -($spacer-grid / 2);
		margin-left: -($spacer-grid / 2);

		.card {
			display: flex;
			flex-direction: column;
			flex-basis: calc(50% - #{$spacer-grid});
			min-width: 1px;
			margin-right: $spacer-grid / 2;
			margin-left: $spacer-grid / 2;
		}

		.card-block {
			flex: 1 0 auto;
		}
	}

	@include media-breakpoint-up(md) {
		margin-right: -$spacer-grid;
		margin-left: -$spacer-grid;

		.card {
			flex-basis: calc(50% - #{$spacer-grid * 2});
			margin-right: $spacer-grid;
			margin-left: $spacer-grid;
		}
	}

	@include media-breakpoint-up(lg) {
		&.card-deck-three-up .card,
		&.card-deck-four-up .card {
			flex-basis: calc(33.33333% - #{$spacer-grid * 2});
		}
	}

	@include media-breakpoint-up(xl) {
		&.card-deck-four-up {
			margin-right: -($spacer-grid / 2);
			margin-left: -($spacer-grid / 2);

			.card {
				flex-basis: calc(25% - #{$spacer-grid});
				margin-right: $spacer-grid / 2;
				margin-left: $spacer-grid / 2;
			}
		}
	}
}

// IE11 fixes
@media screen and (min-width: 544px) and (-ms-high-contrast: active),
screen and (min-width: 544px) and (-ms-high-contrast: none) {
	.card-deck .card {
		flex-basis: auto;
		width: calc(50% - #{$spacer-grid * 2});
	}
}

@media screen and (min-width: 992px) and (-ms-high-contrast: active),
screen and (min-width: 992px) and (-ms-high-contrast: none) {
	.card-deck.card-deck-three-up .card {
		flex-basis: auto;
		width: calc(33.33333% - #{$spacer-grid * 2})
	}

	.card-deck.card-deck-four-up .card {
		flex-basis: auto;
		width: calc(25% - #{$spacer-grid * 2});
	}
}

@media screen and (-ms-high-contrast: active),
screen and (-ms-high-contrast: none) {
	.card-deck .card img {
		flex: 0 0 auto;
	}
}


// Card groups

.card-group {

	.card {
		margin-bottom: 0;

		+ .card {
			margin-top: -1px;
		}
	}

	@include media-breakpoint-up(md) {
		display: flex;
		flex-flow: row wrap;

		.card {
			flex: 1 0 0%;
			min-width: 1px;

			+ .card {
				margin-top: 0;
				margin-left: 0;
				border-left: 0;
			}
		}
	}
}

.card-group-center {
	.card-block {
		display: flex;
		flex: 1;
		align-self: stretch;
		flex-flow: column nowrap;
		justify-content: center;
	}
}




// Card Columns

@include media-breakpoint-up(sm) {
	.card-columns {
		column-count: 3;
		column-gap: 1.25rem;

		.card {
			display: inline-block;
			width: 100%; // Don't let them exceed the column width
		}
	}
}

// Card Container
// Grid variation that allows for cards with border orientation variations and responsive padding 
.card-container {
	@include make-container;
	padding: $card-padding-sm;
	
	@include media-breakpoint-up(lg) {
		padding: $card-padding;
	}

	> *:last-child:not(.card-row) {
		margin-bottom: 0;
	}

	.lf-mosaic-wrap & {
		padding: 0;// fixes conflict with third-party LiveFyre component
	}
}

.card-row {
	@include make-row;
	padding: ($card-padding-sm / 2) 0;
	margin: -$card-padding-sm;

	@include media-breakpoint-up(lg) {
		padding: ($card-padding / 2) 0;
		margin: -$card-padding;
	}
}

.card-col {
	padding: ($card-padding-sm / 2) $card-padding-sm;

	> *:last-child:not(.card-row) {
		margin-bottom: 0;
	}

	@include media-breakpoint-up(lg) {
		padding: ($card-padding / 2) $card-padding;
	}
}

.card-row-block { // use in conjuction with border or bg utilites; col fills full space (doubling up spacing)
	padding: 0;

	.card-col {
		padding: $card-padding-sm;

		@include media-breakpoint-up(lg) {
			padding: $card-padding;
		}
	}
}

// Index Cards

.card-index {

	// Index card variables
	$card-tab-size: 2.5rem !default; // 39px
	$card-tab-color: $gray-lighter !default;

	> [class^='ico-'] {
		position: absolute;
		top: $card-tab-size / 4;
		left: $card-tab-size / 4;
	}

	.card-header {
		border-left: $card-tab-size solid $card-tab-color;
		height: $card-tab-size;
		padding: $card-spacer-0 $card-spacer-sm;
	}

	.card-title {
		line-height: $card-tab-size;
	}

	.card-block {
		padding: $card-spacer-lg $card-spacer-sm 0;
	}

	@include media-breakpoint-up(lg) {
		border-left: $card-tab-size solid $card-tab-color;

		> [class^='ico-'] {
			left: ($card-tab-size / 4) - $card-tab-size;
		}

		.card-header {
			border-left: 0;
		}
	}
}

