.card-notecard {
	font-size: 0.8125rem;
	line-height: 1.2;
	margin-bottom: 0.3125rem;

	// reset standard element margin
	p, ul, ol, dl, pre, blockquote, .figure, .table, .table-simple, .btn-wrap {
		margin-bottom: 0.25rem;
	}

	.card-block,
	&.card-block {
		padding: 0.5rem 0.75rem 0.25rem;
	}

	.card-title {
		font-size: 0.875rem;
		font-weight: 700;
	}
}

.card-notecard-icon {
	> [class^='ico-'],
	> [class*=' ico-'] {
		position: absolute;
		top: 2px;
		right: 0;
	}
}