// Wrapper for the slide container and indicators
.carousel {
	position: relative;
	margin-bottom: ($carousel-indicator-size * 4) + $spacer;
}

.carousel-inner {
	position: relative;
	width: 100%;
	overflow: hidden;

	> .carousel-item {
		visibility: hidden;
		position: absolute;
		top: 0;
		width: 100%;
		transition: 0.6s ease-in-out left;
		z-index: 1;

		// Account for jankitude on images
		> img,
		> a > img {
			// scss-lint:disable PlaceholderInExtend
			@extend .img-fluid;
			width: 100%;
			line-height: 1;
			margin-bottom: 0;
		}
	}

	> .active,
	> .next,
	> .prev {
		visibility: visible;
	}

	> .active {
		position: relative;
		left: 0;
		z-index: 2;
	}

	> .next,
	> .prev {
		z-index: 3;
	}

	> .next {
		left: 100%;
	}

	> .prev {
		left: -100%;
	}

	> .next.left,
	> .prev.right {
		left: 0;
	}

	> .active.left {
		left: -100%;
	}

	> .active.right {
		left: 100%;
	}
}

//
// Left/right controls for nav
//

.carousel-control {
	position: absolute;
	top: 50%;
	left: 0;
	width: $carousel-control-width;
	height: $carousel-control-width;
	transform: translateY(-50%);
	font-size: $carousel-control-font-size;
	color: $carousel-control-color !important;
	text-align: center;
	text-shadow: $carousel-text-shadow;
	opacity: $carousel-control-opacity;
	z-index: 15;

	// We can't have this transition here because WebKit cancels the carousel
	// animation if you trip this while in the middle of another animation.

	&.right {
		right: 0;
		left: auto;
	}

	// Hover/focus state
	@include hover-focus {
		text-decoration: none;
		opacity: 1;
	}

	// Toggles
	.icon-prev,
	.icon-next {
		position: absolute;
		top: 50%;
		z-index: 5;
		display: inline-block;
		width: $carousel-control-font-size * 2;
		height: $carousel-control-font-size * 2;
		margin-top: -($carousel-control-font-size);
		border-radius: 50%;
		font-family: $font-family-icons;
		text-align: center;
		background-color: $carousel-control-bg-color;
		line-height: $carousel-control-font-size * 2;
	}

	.icon-prev {
		left: 15px;

		&::before {
			content: '\e61b';
		}
	}

	.icon-next {
		right: 15px;

		&::before {
			content: '\e61c';
			position: relative;
			left: 2px;
		}
	}
}

// Optional indicator pips
//
// Add a list with the following class and add a list item for each
// slide your carousel holds.

.carousel-indicators {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	bottom: -($carousel-indicator-size * 4);
	left: 50%;
	z-index: 15;
	padding: $carousel-indicator-size;
	transform: translateX(-50%);
	margin-bottom: 0;
	text-align: center;
	list-style: none;

	li {
		position: relative;
		cursor: pointer;
		width: $carousel-indicator-size;
		height: $carousel-indicator-size;
		background-color: $carousel-indicator-color;
		border-radius: 50%;
		margin: $carousel-indicator-margin;

		&.active {
			background-color: $carousel-indicator-active-color;
		}
	}
}

.carousel-dash { // all carousel controls in single panel
	margin-bottom: $spacer;
	text-align: center;

	.carousel-inner {
		text-align: left; // reset in case content inside
	}
}

.carousel-dash-panel { 
	display: inline-flex;
	border-radius: 1.25rem;

	.carousel-dash-control {
		width: 2.5rem;
		height: 2.5rem;
		padding: 0.4375rem 0.625rem;
	}

	.carousel-indicators {
		position: static;
		transform: none;
		width: 3.75rem;
		padding: 0;

		li {
			cursor: default;
			pointer-events: none;
			width: 0;
			margin: 0;
			transform: scale(0);
			transition: transform 0.4s, width 0.4s, margin 0.4s;

			&[class*="indicate-"],
			&.active,
			&.active:first-child ~ li:nth-child(-n+5):not([class*="indicate-"]),
			&:nth-last-child(-n+5):first-child, // these two for 5 or less, where nothing changes
			&:nth-last-child(-n+5):first-child ~ li {
				width: $carousel-indicator-size;
				margin: $carousel-indicator-margin;
			}
			
			&.indicate-here,
			&.active,
			&.active:first-child ~ li:nth-child(-n+3):not([class*="indicate-"]),
			&:nth-last-child(6):first-child ~ li:nth-child(4):not([class*="indicate-"]),
			&:nth-last-child(-n+5):first-child, // these two for 5 or less, where nothing changes
			&:nth-last-child(-n+5):first-child ~ li {
				transform: none;
			}

			&.indicate-near,
			&:nth-child(4):not([class*="indicate-"]),
			&:nth-last-child(6):first-child ~ li:nth-child(5):not([class*="indicate-"]) {
				transform: scale(0.75);
			}

			&.indicate-far,
			&:nth-child(5):not([class*="indicate-"]) {
				transform: scale(0.5);
			}

		}
	}

	// add backgrounds
	&.carousel-dash-light,
	&.carousel-dash-dark {
		margin-top: 0.75rem;
	}

	&.carousel-dash-light {
		background-color: $light-semi-trans;
	}

	&.carousel-dash-dark {
		background-color: $carousel-brick-indicator-bg;

		li {
			background-color: $carousel-brick-indicator-color;

			&.active {
				background-color: $carousel-brick-indicator-active-color;
			}
		}

		.carousel-dash-control {
			@include inverse-links;
		}
	}
}

// Optional captions
//
// Hidden by default for smaller viewports.

.carousel-caption {
	position: absolute;
	right: 15%;
	bottom: 20px;
	left: 15%;
	z-index: 10;
	padding-top: 20px;
	padding-bottom: 20px;
	color: $carousel-caption-color;
	text-align: center;
	text-shadow: $carousel-text-shadow;

	.btn {
		text-shadow: none; // No shadow for button elements in carousel-caption
	}

	@include media-breakpoint-up(sm) {
		// Show and left align the captions
		right: 20%;
		left: 20%;
		padding-bottom: 30px;
	}
}

// Carousel Variations
// BRICK CAROUSEL (indicators are pulled up into content area)
.carousel-brick {
	margin-bottom: 0;

	.carousel-dash-panel {
		position: absolute;
		bottom: 0.5rem;
		left: 50%;
		transform: translateX(-50%);
		z-index: 15;

		@include media-breakpoint-up(lg) {
			bottom: 1rem;
		}
	}

	&:not(.carousel-dash) {
		.carousel-indicators {
			background-color: $carousel-brick-indicator-bg;
			border-radius: $carousel-indicator-size * 1.5;
			bottom: $carousel-indicator-size;

			li {
				background-color: $carousel-brick-indicator-color;

				&.active {
					background-color: $carousel-brick-indicator-active-color;
				}
			}
		}		
	}
}

// CARD CAROUSEL
.carousel-card {
	animation-duration: 0.001s;
	animation-name: cardCarouselInserted; // to detect if inserted via javascript animation detection

	.carousel-item {
		display: flex;
	}

	.card {
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 0;
		min-height: 1px;
		
		&:not(:first-child) {
			border-left: 0;
		}

		@include media-breakpoint-up(sm) {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include media-breakpoint-up(lg) {
			flex: 0 0 33.33333%;
			max-width: 33.33333%;
		}
	}
}

@keyframes cardCarouselInserted {  
	from { opacity: 0.99; }
	to { opacity: 1; }  
}