// scss-lint:disable all
// Breakpont sniffing via javascript
body {
	&::before {
		display: none;
	}

	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			&::before {
				content: '#{$breakpoint}'
			}
		}
	}
}

// Container widths
// Set the container width, and override it for fixed navbars in media queries.

.container,
.container-wide,
.container-fluid {
	@include make-container();
}

.container {
	max-width: $container-max-width;
}

.container-wide {
	max-width: $container-wide-max-width;
}


// Percentage-based wrapper/container
.p-wrap {
	margin: 0 $spacer-grid;

	@include media-breakpoint-custom($min:1280px) {
		margin: 0 auto;
		max-width: 94%;
	}
}

// Row
// Rows contain and clear the floats of your columns.
.row {
	@include make-row();
}

// Columns
// Common styles for small and large grid columns
@include make-grid-columns();

// Flex variation
// Custom styles for additional flex alignment options.

// Flex column reordering
@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		.col-#{$breakpoint}-first { order: -1; }
		.col-#{$breakpoint}-last  { order: 1; }
	}
}

// Alignment for every column in row
@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		.row-#{$breakpoint}-top    { align-items: flex-start; }
		.row-#{$breakpoint}-center { align-items: center; }
		.row-#{$breakpoint}-bottom { align-items: flex-end; }
		.row-#{$breakpoint}-baseline { align-items: baseline; }
	}
}

// Alignment per column
@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		.col-#{$breakpoint}-top    { align-self: flex-start; }
		.col-#{$breakpoint}-center { align-self: center; }
		.col-#{$breakpoint}-bottom { align-self: flex-end; }
	}
}

// Edging Classes
@each $bp in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($bp) {
		.edge-#{$bp}-up {
			padding-left: 0 !important;
			padding-right: 0 !important;
		}

		.edge-l-#{$bp}-up {
			padding-left: 0 !important;
		}

		.edge-r-#{$bp}-up {
			padding-right: 0 !important;
		}
	}

	@include media-breakpoint-down($bp) {
		.edge-#{$bp}-down {
			padding-left: 0 !important;
			padding-right: 0 !important;
		}

		.edge-l-#{$bp}-down {
			padding-left: 0 !important;
		}

		.edge-r-#{$bp}-down {
			padding-right: 0 !important;
		}
	}
}

// special stretch case
.col-stretch-section {
	padding: 0;
	display: flex;

	> .content-section {
		width: 100%;
	}
}
