// Card variants

@mixin card-variant($background, $border) {
	background-color: $background;
	border-color: $border;
}

@mixin card-outline-variant($color) {
	background-color: transparent;
	border-color: $color;
}

@mixin card-hover-focus-border($layerBelow:false) {
	position: relative;
	@if $layerBelow {
		z-index: 1;
	}

	// Box shadow with opacity is used on a pseudo element for better performance
	// http://tobiasahlin.com/blog/how-to-animate-box-shadow/
	&::after {
		box-shadow: inset 0 0 0 4px $link-color;
		content: '';
		opacity: 0;
		position: absolute;
		// Negative postion values to overlap the card border
		top: -1px;
		right: -1px;
		bottom: -1px;
		left: -1px;
		transition: opacity 0.1s linear;
		@if $layerBelow {
			z-index: -1;
		} @else {
			z-index: 2;
		}
	}

	&:focus,
	&:hover {
		&::after {
			opacity: 1;
		}
	}

	&:active {
		&::after {
			box-shadow: inset 0 0 0 4px darken($link-color, 8%);
			opacity: 1;
		}
	}
}

//
// Inverse text within a card for use with dark backgrounds
//

@mixin card-inverse {
	.card-header,
	.card-footer {
		border-bottom: $card-border-width solid rgba($white, 0.2);
	}

	.card-header,
	.card-footer,
	.card-title,
	.card-blockquote {
		color: $white;
	}

	.card-link,
	.card-text,
	.card-blockquote > footer {
		// scss-lint:disable VariableForProperty
		color: rgba($white, 0.65);
	}

	.card-link {
		@include hover-focus {
			color: $card-link-hover-color;
		}
	}
}
