.hero {
	padding-bottom: $spacer*2;

	@include media-breakpoint-up(lg) {
		padding-bottom: $spacer;
	}
}

.hero-shaded {
	background-color: $gray-12;
}

.hero-carousel .carousel {
	margin-bottom: 0;
}

.hero-video {
	
	.hero-media-video {
		position: relative;
		background-color: transparent !important; // overrides Brightcove background
		min-height: 100%;
		width: 100%;
		padding-bottom: percentage(3 / 4);
		overflow: hidden;

		video {
			position: absolute;
			left: 50%;
			top: 0;
			height: 100%;
			width: auto;
			transform: translateX(-50%);
		}

		.vjs-loading-spinner { // For Brightcove; hide the load spinner when used as a background video
			display: none !important;
		}

		.vjs-poster {
			background-size: cover !important; // For Brightcove; make sure poster image fills screen
		}

		@include media-breakpoint-up(md) {
			padding-bottom: percentage(9 / 16);
		}

		@include media-breakpoint-up(xl) {
			padding-bottom: percentage(9 / 21);

			video {
				width: 100%;
				height: auto;
				min-width: 100%;
				min-height: 100%;
				top: 50%;
				transform: translate(-50%,-50%);
			}
		}
	}

	video.hero-media-video {
		visibility: hidden; // Hide before Brightcove transformation
		height: 0;
	}
}

.hero-media {
	position: relative;
	z-index: 2;
	margin-bottom: -3rem;

	img {
		width: 100%;
	}

	@include media-breakpoint-down (sm) {
		.hero-carousel &,
		.hero-video & {
			margin-bottom: -8rem;
		}		
	}

	@include media-breakpoint-up(md) {
		margin-bottom: -5rem;
	}
}

.hero-body {
	@include make-container;
	width: 100%;
	max-width: $container-max-width;
	position: relative;
	z-index: 3;
}

.hero-body-center {
	text-align: center;
}

.hero-content,
.hero-controls {
	min-height: 1px;
	padding-right: $spacer-grid;
}

.hero-content {
	@include make-col-span(12);

	@include media-breakpoint-down(md) {
		.hero-body-center & {
			padding-left: $spacer-grid;
		}
	}

	@include media-breakpoint-up(md) {
		@include make-col-span(8);

		.hero-body-center & {
			@include make-col-offset(2);
		}
	}

	@include media-breakpoint-up(lg) {
		padding-left: $spacer-grid;
	}
}

.hero-controls {
	@include make-col-span(12);
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	padding-left: $spacer-grid;
	height: 5rem;
	text-align: right;



	@include media-breakpoint-down(md) {
		.hero-carousel & {
			text-align: center;
		}

	}

	@include media-breakpoint-up(md) {
		@include make-col-span(4);
		order: 1;

		.hero-carousel & {
			@include make-col-span(2);
			margin-right: -16.66667%;
			text-align: left;
		}
		
		.hero-body-center & {
			@include make-col-span(2);
		}
	}
}

.hero-content-block {
	background-color: $white;
	padding: $spacer $spacer-grid 0;
	overflow: hidden; // prevent margin collapse

	.hero-shaded & {
		background-color: $gray-12;
	}

	@include media-breakpoint-up(lg) {
		padding: ($spacer*2) ($spacer*2) $spacer;
	}
}

.hero-content-border {
	border-top: 8px solid $brand-primary;
}

.hero-controls-block {
	padding-bottom: $spacer-grid;
}

// Overlay
@supports (display: grid) {
	@include media-breakpoint-up(md) {
		.hero-overlay {
			display: grid;
			padding-bottom: 0;

			> * {
				grid-column: col 1;
				grid-row: row 1 ;
			}

			.hero-media {
				margin-bottom: 0;

				img {
					width: 100vw;
					height: 100%;
					object-fit: cover;
				}
			}

			.hero-body {
				display: flex;
				padding-top: $spacer;
				padding-bottom: $spacer;
				flex-direction: column;
				justify-content: center;
			}

			.hero-body-right .row {
				justify-content: flex-end;
			}

			.hero-body-bottom {
				justify-content: flex-end;
			}

			.hero-content {
				padding-left: $spacer-grid;
			}

			.hero-content-block {
				background-color: $white; // redefining bgc in case of .hero-shaded
			}

			.hero-content-trans {
				background-color: $light-trans;
			}

			.hero-content-semi-trans {
				background-color: $light-semi-trans;
			}

			.hero-body-right .hero-controls {
				order: -1;
				text-align: left;
			}

			// Video adjustments
			&.hero-video {
				.hero-media {
					width: 100vw;
				}

				.hero-controls {
					align-self: flex-end;
				}

				.hero-controls-block {
					padding-bottom: 0;
				}
			}
		}
	}

	@include media-breakpoint-up(lg) {
		.hero-overlay {
			.hero-content,
			.hero-controls {
				@include make-col-span(6);
			}
			
			.hero-body-center {
				.hero-content {
					@include make-col-offset(3);
				}

				.hero-controls {
					@include make-col-span(3);
				}
			}
		}
	}
}
