// deprecated legacy syntax; needed for main header
.attached-btn {
	position: relative;
	z-index: 2; // so all stay above float label
	display: flex;

	&.max-width {
		max-width: 400px;
	}

	&.omnifield {
		margin-bottom: $spacer-half;
	}
}

.attached-btn-input {
	position: relative;
	flex: 1 1 auto;
	min-height: 1px;

	.form-control {
		border-right: 0;
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		position: relative;
		z-index: 3;
	}

	.label-help-error {
		position: relative;
		z-index: 2;
	}
}

.attached-btn-action {
	flex: 0 0 auto;
	min-height: 1px;
	z-index: 9;

	.btn {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
}

// Dark Theme
.attached-btn-dark {

	// Input
	.form-control {
		background-color: $gray-dark;
		border-color: $gray-dark;
		color: $gray-lightest;

		&:hover,
		&:focus,
		&:active {
			color: $white;
		}

		&:focus,
		&:active {
			border-color: $gray-darker;
		}

		// Placeholder
		&::placeholder {
			color: $gray-lighter;
		}

		&.is-invalid {
			border-color: $gray-dark;
			color: $white;

			&:focus {
				background-color: darken($brand-danger, 24%);
				border-color: $brand-danger;
			}
		}
	}

	// Button
	.btn {
		@include transition(background 0.2s ease-in-out);
		background-color: lighten($gray, 16%);
		color: $white;

		&:focus {
			background-color: $btn-primary-bg;
		}

		&:hover {
			background-color: $btn-reversed-attached-bg;
			border-color: transparent;
		}

		&:active,
		&:active:focus {
			background-color: $btn-primary-bg;
		}
	}
}
