// @include font($sans-serif, normal, bold);
@mixin font($family: $sans-serif, $style: normal, $weight: normal) {
	font-family: $family;
	font-style: $style;
	font-weight: $weight;
}

@mixin responsive-headers($settings) {
	@each $property, $attribute in $settings {
		@if type-of($attribute) == 'map' { // property is a breakpoint
			$breakpoint: $property;
			@if $breakpoint == $bp-xs {
				@each $key, $value in $attribute {
					#{$key}: #{$value};
				}
			} @else {
				@media screen and (min-width: $breakpoint) {
					@each $key, $value in $attribute {
						#{$key}: #{$value};
					}
				}
			}
		} @else {
		  #{$property}: #{$attribute};
		}
	}
}

// Table Header Font Styles
@mixin table-header() {
	color: $gray-darker;
	font-size: 0.75rem;
	font-weight: 600;
	letter-spacing: 0.04em;
	text-transform: uppercase;
}

// Inverse Links
@mixin inverse-links() {
	color: $link-rev-color;

	@include hover-focus {
		color: $link-rev-hover;
	}

	&:active {
		color: $link-rev-active;
	}

	&.disabled {
		cursor: $cursor-disabled;
		color: $gray-6;
	}
}

@mixin inverse-links-white() {
	color: $white;

	@include hover-focus {
		color: $link-rev-hover;
	}

	&:active {
		color: $link-rev-active;
	}
}
