// Base Theme Carets

// Note: Not calling these "arrows" since those exist as a separate UI element/icon

// This could be extended in the future for all sorts of carets,
// but keeping it light for now.

[class^='layer-caret-'],
[class*=' layer-caret-'] {
	padding-top: $spacer-grid;
	padding-bottom: $spacer-grid;
	position: relative;

	@include media-breakpoint-up(lg) {
		padding-top: $spacer;
		padding-bottom: $spacer;
	}

	&::after {
		border-left: $caret-width solid transparent;
		border-right: $caret-width solid transparent;
		border-top: $caret-width solid transparent;
		content: '';
		display: block;
		height: 0;
		position: absolute;
		left: 50%;
		bottom: -$caret-width;
		width: 0;
		z-index: 1;
	}

	.summary {
		margin-bottom: 0;

		@include media-breakpoint-up(lg) {
			font-size: 1.125rem;
		}
	}
}

.layer-caret-gray-1 {
	background-color: $gray-1;

	&::after {
		border-top-color: $gray-1;
	}
}

.layer-caret-gray-2 {
	background-color: $gray-2;

	&::after {
		border-top-color: $gray-2;
	}
}

.layer-caret-gray-12 {
	background-color: $gray-12;

	&::after {
		border-top-color: $gray-12;
	}
}

.layer-caret-white {
	background-color: $white;

	&::after {
		border-top-color: $white;
	}
}

