// Border Utilities - Use in conjuntion with padding utilities as necessary
// scss-lint:disable ImportantRule

// old add border system
.b-a { border: $border-width solid $border-default-color !important; }
.b-t { border-top: $border-width solid $border-default-color !important; }
.b-r { border-right: $border-width solid $border-default-color !important; }
.b-b { border-bottom: $border-width solid $border-default-color !important; }
.b-l { border-left: $border-width solid $border-default-color !important; }
.b-x { 
	border-right: $border-width solid $border-default-color !important;
	border-left: $border-width solid $border-default-color !important;
}
.b-y {
	border-top: $border-width solid $border-default-color !important;
	border-bottom: $border-width solid $border-default-color !important;
}

// responsive add border system
@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		// add borders
		.b-a-#{$breakpoint} { border: $border-width solid $border-default-color !important; }
		.b-t-#{$breakpoint} { border-top: $border-width solid $border-default-color !important; }
		.b-r-#{$breakpoint} { border-right: $border-width solid $border-default-color !important; }
		.b-b-#{$breakpoint} { border-bottom: $border-width solid $border-default-color !important; }
		.b-l-#{$breakpoint} { border-left: $border-width solid $border-default-color !important;}

		// remove borders
		.b-a-#{$breakpoint}-0  { border: 0 !important; }
		.b-t-#{$breakpoint}-0 { border-top: 0 !important; }
		.b-r-#{$breakpoint}-0 { border-right: 0 !important; }
		.b-b-#{$breakpoint}-0 { border-bottom: 0 !important; }
		.b-l-#{$breakpoint}-0 { border-left: 0 !important;}
	}
}

// border widths
.b-thick {
	border-width: $border-width-thick !important;
}

// alternative border colors
@each $name, $color in $border-alt-colors {
	.b-clr-#{$name} {
		border-color: #{$color} !important;
	}
}

// border radius
.b-rounded {
	border-radius: $border-radius;
}
