//
// Base Theme Image Sprites


// Credit Card Icons
[class^='ico-cc-'],
[class*=' ico-cc-'] {
	background-image: url('#{$image-path}theme-base/icon-payment-sprite.svg');
	background-size: 155px 55px;
	height: 25px;
	width: 35px;
	transform: none;
}

// Visa
.ico-cc-visa {
	background-position: 0 0;

	&.selected {
		background-position: 0 -30px;
	}
}

// MasterCard
.ico-cc-mastercard {
	background-position: -40px 0;

	&.selected {
		background-position: -40px -30px;
	}
}

// Discover
.ico-cc-discover {
	background-position: -80px 0;

	&.selected {
		background-position: -80px -30px;
	}
}

// AMEX
.ico-cc-amex {
	background-position: -120px 0;

	&.selected {
		background-position: -120px -30px;
	}
}

