// Checkboxes and radios
//
// Base class takes care of all the key behavioral aspects.

.c-input {

	&,
	.form-group & {
		font-size: 1rem;
	}

	@include transition(color 0.2s linear, border-color 0.2s linear, box-shadow 0.2s linear);
	position: relative;
	display: inline-block;
	padding-left: 1.75rem;
	min-height: 1.75rem;
	margin-right: 1rem;
	cursor: pointer;

	> input {
		position: absolute;
		z-index: -1; // Put the input behind the label so it doesn't overlay text
		opacity: 0;

		&:checked ~ .c-indicator {
			color: $white;
			border-color: $input-custom-color;
			@include box-shadow(none);
		}

		&:hover ~ .c-indicator,
		&:focus ~ .c-indicator {
			border-color: $input-custom-color;
		}

		&:focus ~ .c-indicator {
			// the mixin is not used here to make sure there is feedback
			box-shadow: $input-box-shadow-focus;
		}

		&:active ~ .c-indicator {
			border-width: 2px;
			@include box-shadow(none);
		}
	}

	&.disabled {
		cursor: $cursor-disabled;
		color: $gray-6;
	}

	.has-danger & {
		> input {
			~ .c-indicator {
				border-color: $brand-danger;
			}
		}
	}
}

// Custom indicator
//
// Generates a shadow element to create our makeshift checkbox/radio background.

.c-indicator {
	position: absolute;
	top: 2px;
	left: 0;
	display: block;
	width: 1.25rem;
	height: 1.25rem;
	border: 1px solid $gray;
	color: $white;
	text-align: center;
	user-select: none;
	background-color: $white;
}

// Checkboxes
//
// Tweak just a few things for checkboxes.
// Small checkbox settings defined below with small radios.

.c-checkbox,
.c-checkbox-sm {
	.c-indicator {
		border-radius: $border-radius;

		&::before {
			@include transition(all 0.14s ease-in-out);
			position: absolute;
			top: 0;
			right: -1px;
			font-size: 1.25rem;
			line-height: 1;
			font-family: 'mylt-icons';
			content: '\e608';
			font-weight: normal;
			opacity: 0;
			transform: scale(0);
		}
	}

	input:checked ~ .c-indicator {
		background-color: $input-custom-color;

		&::before {
			opacity: 1;
			transform: scale(1);
		}
	}

	input:active:checked ~ .c-indicator {
		border-width: 1px;
	}

	.has-danger & input:checked ~ .c-indicator {
		background-color: $brand-danger;
	}

	input:disabled ~ .c-indicator,
	input.disabled ~ .c-indicator {
		background-color: $gray-light;
		border-color: $gray-light;
	}
}


// toggle switch variations
.c-toggle,
.c-favorite {
	margin-right: 0;

	> input {
		left: 0;
	}

	.c-indicator::before {
		font-family: 'mylt-icons';
	}
}

.c-toggle {
	min-height: 1.375rem; //22px
	padding-left: 2.5rem; //40px
	margin-bottom: 0;

	.c-indicator {
		width: 2.5rem; //40px
		top: 0;
		background-color: $gray-12;

		&,
		&::before {
			@include transition(all 0.14s ease-in-out);
			border-radius: 0.6875rem; //11px
			height: 1.375rem; //22px
		}

		&::before {
			content: '\e608';
			position: absolute;
			top: -1px;
			left: -1px;
			width: 1.375rem; //22px
			border: 2px solid $gray;
			line-height: 1.25;
			background-color: $white;
		}
	}

	input:checked ~ .c-indicator {
		background-color: $input-custom-color;

		&::before {
			left: 1.125rem;
			border-color: $input-custom-color;
			color: $input-custom-color;
		}
	}

	input:hover:not(:checked) ~ .c-indicator,
	input:focus:not(:checked) ~ .c-indicator {
		border-color: $gray;
	}

	input:active ~ .c-indicator {
		top: 0;
		border-width: 1px;
	}
}

.c-favorite {
	.c-indicator {
		left: auto;
		right: 0;
		border: 0;
		background: none;
		color: $gray-9;
		font-size: 1.25rem;
		line-height: 1;

		&::before {
			content: '\e921';
		}
	}

	input:hover ~ .c-indicator,
	input:focus ~ .c-indicator {
		color: $aqua-lighter;
		@include box-shadow(none);
	}

	input:checked ~ .c-indicator {
		color: $aqua-lighter;

		&::before {
			content: '\e922';
		}
	}

	input:checked:hover ~ .c-indicator {
		color: $aqua-lightest;
	}
}

// Radios
//
// Tweak just a few things for radios.

.c-radio,
.c-radio-sm {
	.c-indicator {
		border-radius: 50%;
	}

	input:checked ~ .c-indicator {
		border-width: 5px;
	}

	input:disabled ~ .c-indicator,
	input.disabled ~ .c-indicator {
		background-color: $gray-light;
		border-color: $gray-light;
	}

	// order to handle both regular and disabled checked case
	input:checked ~ .c-indicator {
		background-color: $white;
	}
}

// Small Checkboxes and Radios

.c-checkbox-sm,
.c-radio-sm {

	&,
	.form-group & {
		font-size: 0.875rem;
	}

	padding-left: 1.5rem;
	min-height: 1rem;

	.c-indicator {
		width: 1rem;
		height: 1rem;

		&::before {
			font-size: 1rem;
		}
	}
}


// Layout options
//
// By default radios and checkboxes are `inline-block` with no additional spacing
// set. Use these optional classes to tweak the layout.

.c-inputs-stacked {
	.c-input {
		width: 100%;
		margin-bottom: 0.5em;
		margin-right: 0;
	}
}


// Select

.c-select,
.c-select-sm {
	font-size: 1rem;
	display: block;
	width: 100%;
	padding: $input-padding-y 1.9375rem $input-padding-y $input-padding-x;
	color: $body-color;
	vertical-align: middle;
	// url(../images/theme-base/select-caret-down.svg);
	background: $white url($form-select-down) no-repeat right $input-padding-x center;
	background-size: 15px 15px;
	border: $input-btn-border-width solid $input-border-color;
	@include border-radius($input-border-radius);
	// Use vendor prefixes as `appearance` isn't part of the CSS spec.
	-moz-appearance: none;
	-webkit-appearance: none;

	&:hover,
	&:focus {
		color: $gray-dark;
	}

	&:focus {
		border-color: $input-border-focus;
		box-shadow: $input-box-shadow-focus;
		outline: 0;
	}

	&:disabled,
	&.disabled {
		cursor: $cursor-disabled;
		color: $input-color;
		background-color: $input-bg-disabled;
		opacity: 0.5;
	}

	// Hides the default caret in IE11
	&::-ms-expand {
		opacity: 0;
	}

	// Undo the IE white-text-on-blue focus state
	&:focus::-ms-value {
		background-color: transparent;
		color: $gray-dark;
	}
}

.c-select-sm {
	padding-top: 0.3125rem;
	padding-bottom: 0.3125rem;
	font-size: 0.875rem;
}

// File
//
// Custom file input.

.file {
	position: relative;
	display: inline-block;
	height: $input-height;
	cursor: pointer;

	&,
	.form-group & {
		font-size: 1rem;
	}
}

.file input {
	min-width: 14rem;
	margin: 0;
	filter: alpha(opacity = 0);
	opacity: 0;
}

.file-custom {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: 5;
	height: $input-height;
	padding: $input-padding-y $input-padding-x;
	line-height: $line-height;
	color: $input-color;
	user-select: none;
	background-color: $white;
	border: $input-btn-border-width solid $input-border-color;
	border-radius: $border-radius;

	&::after {
		content: '\0043\0068\006f\006f\0073\0065\00a0\0066\0069\006c\0065\002e\002e\002e';
	}

	&::before {
		content: '\0042\0072\006f\0077\0073\0065';
		position: absolute;
		top: -1px;
		right: -1px;
		bottom: -1px;
		z-index: 6;
		display: block;
		height: $input-height;
		padding: $input-padding-y $btn-padding-x;
		line-height: $line-height;
		color: $btn-primary-color;
		background-color: $btn-primary-bg;
		border: $input-btn-border-width solid $btn-primary-border;
		border-radius: 0 $border-radius $border-radius 0;
	}
}

// Hover/Focus state
.file-custom:hover,
.file input:focus ~ .file-custom {
	border-color: $brand-primary;
	box-shadow: $input-box-shadow-focus;

	&::before {
		border-color: darken($brand-primary, 10%);
		background-color: darken($brand-primary, 10%);
	}
}
