// Time

.time-start,
.time-end {
	white-space: nowrap;
}

.time-ampm {
	font-size: 0.75em;
	margin-left: -0.1em;
	text-transform: uppercase;
}

.time-to {
	display: inline-block;
	overflow: hidden;
	position: relative;
	text-align: left;
	text-indent: -10000px;
	vertical-align: bottom;
	width: 0.5em;

	&::before {
		border-top: $border-width solid currentColor;
		content: '';
		display: block;
		height: $border-width;
		position: absolute;
		top: 50%;
		left: 0;
		width: 0.5em;
	}
}

strong {

	.time-to::before {
		border-top-width: $border-width * 2;
		height: $border-width * 2;
	}
}
