.login-main,
.login-mylt,
.login-panel {
	position: relative;
}

.login-loading,
.login-backdrop {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.login-panel {
	z-index: 3;
}

.login-backdrop {
	z-index: 1;
}

.login-loading {
	visibility: hidden;
	background-color: rgba($black,0.15);
	z-index: 2;

	&.in {
		visibility: visible;
	}
}

@include media-breakpoint-down(xs) {
	.login-backdrop {
		display: none;
	}
}

@include media-breakpoint-up(sm) {
	.login-main,
	.login-mylt {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding: 4rem;
	}

	.login-panel {
		position: relative;
		background-color: $white;
		width: 100%;
		max-width: 24rem;
	}

	.login-panel-inner {
		padding-left: $spacer;
		padding-right: $spacer;
	}
}
