// Linter Fixes
// scss-lint:disable ColorVariable

// Theme Variables
//
// Copy settings from this file into a new theme `_variables.scss` file to override
// these and Bootstrap defaults without modifying key, versioned files.


// Table of Contents
//
// Colors
// Options
// Spacing
// Body
// Links
// Grid breakpoints
// Grid containers
// Grid columns
// Fonts
// Components

// General variable structure
//
// Variable format should follow the `$component-modifier-state-property` order.

// Image Path
$asset-path: '../' !default;
$image-path: '#{$asset-path}images/' !default;
$font-path: '#{$asset-path}fonts/' !default;


// Colors
@import '../palettes/brand-neutrals';
@import '../palettes/brand-blues';

$gray-darkest:              $gray-1 !default;
$gray-darker:               $gray-3 !default;
$gray-dark:                 $gray-4 !default;
$gray:                      $gray-5 !default;
$gray-light:                $gray-9 !default;
$gray-lighter:              $gray-11 !default;
$gray-lightest:             $gray-12 !default;

$brand-primary:             $aqua !default;
$brand-secondary:           $gray-dark !default;
$brand-success:             #76bd23 !default;
$brand-info:                #00cbed !default;
$brand-warning:             #ffb700 !default;
$brand-danger:              #c93325 !default;

$green:                     #437b00 !default; // primarily used for pricing information

// Transparent Colors
$dark-trans:                rgba($black,0.6);
$dark-semi-trans:           rgba($black,0.8);
$light-trans:               rgba($white,0.75);
$light-semi-trans:          rgba($white,0.9);

// Import additional palettes if necessary
// @import '../palettes/basketball'
// etc.

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

// Enabling flexbox means reduced browser and device support:
// Internet Explorer 9 and below do not support flexbox.
// Internet Explorer 10 has a few known quirks (see the "Known issues" tab in Can I use...),
// requires using a prefix, and only supports the syntax from the old 2012 version of the spec.
$enable-flex:               true !default;
$enable-rounded:            true !default;
$enable-shadows:            true !default;
$enable-gradients:          false !default;
$enable-transitions:        true !default;
$enable-hover-media-query:  false !default;
$enable-grid-classes:       true !default;


// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.

$spacer:                     1.5rem !default; // 24px
$spacer-x:                   $spacer !default;
$spacer-y:                   $spacer !default;
$spacer-half:                ($spacer / 2); // 12px

$spacer-grid:                1rem; // 16px
$spacer-sm:                  ($spacer-grid / 2); // 8px
$spacer-xs:                  ($spacer-grid / 4); // 4px

$spacers: (
  0: (
    x:   0,
    y:   0
  ),
  "xs": (
    x:   $spacer-xs, // 4px
    y:   $spacer-xs
  ),
  "sm": (
    x:   $spacer-sm, // 8px
    y:   $spacer-sm
  ),
  1: (
    x:   $spacer-half, // 12px
    y:   $spacer-half
  ),
  "grid": (
    x:   $spacer-grid, // 16px
    y:   $spacer-grid
  ),
  2: (
    x:   $spacer, // 24px
    y:   $spacer
  ),
  3: (
    x:   ($spacer * 1.5), // 36px
    y:   ($spacer * 1.5)
  ),
  4: (
    x:   ($spacer * 2), // 48px
    y:   ($spacer * 2)
  ),
  5: (
    x:   ($spacer * 2.5), // 60px
    y:   ($spacer * 2.5)
  )
) !default;

// Borders
$border-width:               1px !default;
$border-width-thick:         4px !default;
$border-default-color:       $gray-lighter !default;
$border-alt-colors: (
  "current": "currentColor",
  "gray-4": $gray-4,
  "gray-5": $gray-5,
  "gray-6": $gray-6
) !default;

// Body
//
// Settings for the `<body>` element.

$body-bg:                    #fff !default;
$body-color:                 $gray-dark !default;


// Links
//
// Style anchor elements.

$link-color:                 $brand-primary !default;
$link-decoration:            none !default;
// Hover color also used for :focus
$link-hover-color:           darken($link-color, 15%) !default;
$link-hover-decoration:      none !default;
$link-active-color:          darken($link-color, 20%) !default;

// Reversed links

$link-rev-color:            $aqua-lighter !default;
$link-rev-hover:            $aqua-lightest !default;
$link-rev-active:           $brand-primary !default;

// Grid breakpoints
//
// Define the minimum and maximum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
	// Extra small screen / phone
	xs: 0,
	// Small screen / phone
	sm: 544px,
	// Medium screen / tablet
	md: 768px,
	// Large screen / desktop
	lg: 992px,
	// Extra large screen / wide desktop
	xl: 1200px
) !default;


// Breakpoint Variables
$bp-xs: map-get($grid-breakpoints, xs) !default;
$bp-sm: map-get($grid-breakpoints, sm) !default;
$bp-md: map-get($grid-breakpoints, md) !default;
$bp-lg: map-get($grid-breakpoints, lg) !default;
$bp-xl: map-get($grid-breakpoints, xl) !default;


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-width: 1200px !default;
$container-wide-max-width: 1440px !default;


// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:               12 !default;
$grid-gutter-width:          2rem !default; // 32px


// Typography
//
// Font, line-height, and color for body text, headings, and more.

// Safari for OS X and iOS (San Francisco)
// -apple-system

// Chrome >= 56 for OS X (San Francisco), Windows, Linux and Android
// system-ui

// Chrome < 56 for OS X (San Francisco)
// BlinkMacSystem

// Windows
// Segoe UI

// Android
// Roboto

// Basic web fallback
// Helvetica, Arial, sans-serif

// Note that we're including Helvetica Neue and Roboto before Segoe UI in
// the instance that those fonts are loaded on the system. Roboto is loaded
// via @font-face since it's preferred over system in those instances. See
// the Font Stack page in Core for details.
$font-family-sans-serif:          -apple-system, BlinkMacSystemFont, system-ui, 'Helvetica Neue', 'Roboto', 'Segoe UI', "Noto Sans", Helvetica, Arial, sans-serif !default;
$font-family-serif:               $font-family-sans-serif !default; // Mapping this to the sans-serif now
$font-family-sans-serif-light:    $font-family-sans-serif !default;
$font-family-sans-serif-heavy:    $font-family-sans-serif !default;
$font-family-monospace:           Menlo, Monaco, Consolas, 'Courier New', monospace !default;
$font-family-base:                $font-family-sans-serif !default;
$font-family-icons:               'mylt-icons';
$prelo-letter-spacing:            0.03em;


// Vertical Rhythm

// At all breakpoints values (line-height + margins) should add up to
// multiples of 24.

// Responsive Typography
// For the system of .h1-.h6 classes to properly overwrite the base tag, you must include all properties transformed
// at every breakpoint, even if those properties are the default or assigned at an earlier breakpoint.
$h1-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 2rem,         // 32px
    line-height : 1.25,       // 40px
    margin-bottom : 1rem      // 16px
  ),
  $bp-sm : (
    font-size : 2.25rem,      // 36px
    line-height : 1.22223,    // 44px
    margin-bottom : 1rem      // 16px
  ),
  $bp-md : (
    font-size : 2.5rem,       // 40px
    line-height : 1.2,        // 48px
    margin-bottom : 1.5rem    // 24px
  ),
  $bp-lg : (
    font-size : 3rem,         // 48px
    line-height : 1.16667,    // 56px
    margin-bottom : 1.5rem    // 24px
  )
) !default;

$h2-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.625rem,     // 26px
    line-height : 1.23077,    // 32px
    margin-bottom : 1rem      // 16px
  ),
  $bp-sm : (
    font-size : 1.75rem,      // 28px
    line-height : 1.28572,    // 36px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-md : (
    font-size : 2rem,         // 32px
    line-height : 1.25,       // 40px
    margin-bottom : 1.5rem    // 24px
  ),
  $bp-lg : (
    font-size : 2.25rem,      // 36px
    line-height : 1.33334,      // 48px
    margin-bottom : 1.5rem    // 24px
  )
) !default;

$h3-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.375rem,     // 22px
    line-height : 1.091,      // 28px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-sm : (
    font-size : 1.5rem,       // 24px
    line-height : 1.273,      // 28px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-md : (
    font-size : 1.5rem,       // 26px
    line-height : 1.273,      // 32px
    margin-bottom : 1.5rem    // 24px
  ),
  $bp-lg : (
    font-size : 1.875rem,     // 30px
    line-height : 1.2,        // 36px
    margin-bottom : 1.5rem    // 24px
  )
) !default;

$h4-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.125rem,     // 18px
    line-height : 1.33334,    // 24px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-sm : (
    font-size : 1.25rem,      // 20px
    line-height : 1.2,        // 24px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-md : (
    font-size : 1.375rem,     // 22px
    line-height : 1.273,      // 28px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-lg : (
    font-size : 1.5rem,       // 24px
    line-height : 1.25,       // 30px
    margin-bottom : 1.375rem  // 22px
  )
) !default;

$h5-properties: (
  font-weight: 700,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 0.875rem,     // 14px
    line-height : 1.429,      // 20px
    margin-bottom : 0.25rem,  // 4px
  ),
  $bp-sm : (
    font-size : 0.875rem,     // 14px
    line-height : 1.429,      // 20px
    margin-bottom : 0.25rem,  // 4px
  ),
  $bp-md : (
    font-size : 1rem,         // 16px
    line-height : 1.5,        // 24px
    margin-bottom : 0,        // none
  ),
  $bp-lg : (
    font-size : 1rem,         // 16px
    line-height : 1.5,        // 24px
    margin-bottom : 0,        // none
  )
) !default;

$h6-properties: (
  font-weight: 700,
  letter-spacing: 0.05em,
  text-transform: uppercase,
  $bp-xs : (
    font-size : 0.813rem,     // 13px
    line-height : 1.538,      // 20px
    margin-bottom : 0.25rem,  // 4px
  )
) !default;

// Scaled Headings
// Only change at specific breakpoints
$h1-scaled-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.5rem,       // 24px
    line-height : 1,          // 24px
    margin-bottom : 1rem      // 16px
  ),
  $bp-lg : (
    font-size : 2rem,         // 32px
    line-height : 1,          // 32px
    margin-bottom : 1.5rem    // 24px
  )
) !default;

$h2-scaled-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.125rem,     // 18px
    line-height : 1.25,       // 30px
    margin-bottom : 1.125rem  // 18px
  ),
  $bp-lg : (
    font-size : 1.5rem,       // 24px
    line-height : 1.278,      // 46px
    margin-bottom : 1.625rem  // 26px
  )
) !default;

$h3-scaled-properties: (
  font-weight: 300,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.5rem,       // 24px
    line-height : 1.273,      // 28px
    margin-bottom : 1.25rem   // 20px
  ),
  $bp-lg : (
    font-size : 1.75rem,      // 28px
    line-height : 1,          // 28px
    margin-bottom : 1.25rem   // 20px
  )
) !default;

// Display headings
$display1-properties: (
  font-weight: 700,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 3rem,         // 48px
    line-height : 1,
    margin-bottom : 2.5rem
  ),
  $bp-sm : (
    font-size : 3rem,         // 48px
    line-height : 1,
    margin-bottom : 2.5rem
  ),
  $bp-md : (
    font-size : 4rem,         // 64px
    line-height : 1.1,
    margin-bottom : 2.5rem
  ),
  $bp-lg : (
    font-size : 5rem,         // 70px
    line-height : 1.2,
    margin-bottom : 2.5rem
  ),
  $bp-xl : (
    font-size : 6rem,         // 96px
    line-height : 1,
    margin-bottom : 2.5rem
  )
) !default;

$display2-properties: (
  font-weight: 200,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 3rem,         // 48px
    line-height : 1.1,
    margin-bottom : 0.75rem   // 12px
  ),
  $bp-sm : (
    font-size : 3rem,         // 48px
    line-height : 1.1,
    margin-bottom : 0.75rem   // 12px
  ),
  $bp-md : (
    font-size : 3.5rem,       // 56px
    line-height : 1,
    margin-bottom : 1rem      // 16px
  ),
  $bp-lg : (
    font-size : 4rem,         // 64px
    line-height : 1,
    margin-bottom : 1rem
  )
) !default;

$display4-properties: (
  font-weight: 200,
  letter-spacing: normal,
  text-transform: none,
  $bp-xs : (
    font-size : 1.125rem,         // 18px
    line-height : 1.5,
    margin-bottom : 0.75rem      // 24px
  ),
  $bp-sm : (
    font-size : 1.125rem,         // 18px
    line-height : 1.5,
    margin-bottom : 0.75rem     // 24px
  ),
  $bp-md : (
    font-size : 1.375rem,  // 22px
    line-height : 1.35,
    margin-bottom : 0.75rem   // 24px
  ),
  $bp-lg : (
    font-size : 1.5rem,         // 24px
    line-height : 1.35,
    margin-bottom : 0.75rem    // 24px
  )
) !default;

// Pixel value used to responsively scale all typography. Applied to the `<html>` element.
$font-size-base:             1rem !default;
$font-size-lg:               1.25rem !default;
$font-size-sm:               0.875rem !default;
$font-size-xs:               0.75rem !default;

// Heading size variables not in use
$font-size-h1:               3rem !default;
$font-size-h2:               2.25rem !default;
$font-size-h3:               1.75rem !default;
$font-size-h4:               1.25rem !default;
$font-size-h5:               1rem !default;
$font-size-h6:               1rem !default;

// display sizes/weights not in use
$display1-size:               3.5rem !default;
$display2-size:               4.5rem !default;
$display3-size:               5.5rem !default;
$display4-size:               6rem !default;

$display1-weight:             300 !default;
$display2-weight:             300 !default;
$display3-weight:             300 !default;
$display4-weight:             300 !default;

$line-height:                1.5 !default;

$headings-margin-bottom:     ($spacer / 2) !default;
$headings-font-family:       inherit !default;
$headings-font-weight:       500 !default;
$headings-line-height:       1.1 !default;
$headings-color:             $gray-darker !default;

$lead-font-size:             1.25rem !default;
$lead-font-weight:           300 !default;

$text-primary:                $brand-primary !default;
$text-muted:                  $gray-5 !default;
$text-muted-inverse:          $gray-6 !default;

$abbr-border-color:           $gray-light !default;

$blockquote-small-color:      inherit !default;
$blockquote-font-size:        1.375rem !default; // 22px
$blockquote-font-size-lg:     1.75rem !default; // 28px
$blockquote-font-weight:      300 !default;
$blockquote-border-color:     $brand-primary !default;

$hr-border-color:             $gray-10 !default;
$hr-border-width:             $border-width !default;
$hr-border-width-thick:       4px !default;

$dt-font-weight:              700 !default;
$dt-font-color:               $gray-darker !default;

$nested-kbd-font-weight:      700 !default;

$list-inline-padding:         5px !default;

$text-indent-sm:           		1.5rem !default;
$text-indent:              		1.625rem !default;


// Components
//
// Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

$line-height-lg:         (4 / 3) !default;
$line-height-sm:         1.5 !default;

$border-radius:          3px !default;
$border-radius-lg:       3px !default;
$border-radius-sm:       3px !default;

$component-active-color: #fff !default;
$component-active-bg:    $brand-primary !default;

$caret-width:            0.3125rem !default;
$caret-width-lg:         $caret-width !default;


// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.

$table-cell-padding:            0.75rem !default;
$table-sm-cell-padding:         0.35rem !default;
$table-xs-cell-padding:         0.25rem !default;

$table-bg:                      transparent !default;
$table-bg-accent:               $gray-12 !default;
$table-bg-hover:                $gray-12 !default;
$table-bg-active:               $table-bg-hover !default;

$table-border-width:            $border-width !default;
$table-border-color:            $gray-10 !default;

$table-caption-color:           $gray-dark !default;

// Focus Highlights
//
// Used on Buttons and Inputs
$focus-shadow-color: #3eaaf1;

// Buttons
//
// For each of Bootstrap's buttons, define text, background and border color.

$btn-padding-x:                  1rem !default;
$btn-padding-y:                  0.375rem !default;
$btn-font-weight:                500 !default;
$btn-box-shadow-focus:           0 0 0 1px rgba($white, 0.3), 0 0 6px 2px rgba($focus-shadow-color, 0.7) !default;

$btn-primary-color:              $white !default;
$btn-primary-bg:                 $brand-primary !default;
$btn-primary-border:             transparent !default;
$btn-primary-outline:            $brand-primary !default;

$btn-reversed-color:             $gray-dark !default;
$btn-reversed-bg:                $white !default;
$btn-reversed-border:            $white !default;
$btn-reversed-outline:           $white !default;

$btn-secondary-color:            $white !default;
$btn-secondary-bg:               $brand-secondary !default;
$btn-secondary-border:           $brand-secondary !default;
$btn-secondary-outline:          $brand-secondary !default;

$btn-secondary-reversed-color:   $white !default;
$btn-secondary-reversed-bg:      transparent !default;
$btn-secondary-reversed-border:  $white !default;
$btn-secondary-reversed-outline: $white !default;

$btn-series-color:               $gray !default;
$btn-series-bg:                  $white !default;
$btn-series-border:              $gray-light !default;

$btn-info-color:                 $white !default;
$btn-info-bg:                    $brand-info !default;
$btn-info-border:                $btn-info-bg !default;
$btn-info-outline:               $brand-info !default;

$btn-success-color:              $white !default;
$btn-success-bg:                 darken(saturate(adjust-hue($brand-success, 0.0735), 0.0812), 13.7255) !default;
$btn-success-border:             darken(saturate(adjust-hue($brand-success, 0.0735), 0.0812), 13.7255) !default;
$btn-success-outline:            darken(saturate(adjust-hue($brand-success, 0.0735), 0.0812), 13.7255) !default;

$btn-warning-color:              #6b4300 !default;
$btn-warning-bg:                 $brand-warning !default;
$btn-warning-border:             $btn-warning-bg !default;
$btn-warning-outline:            $brand-warning !default;

$btn-danger-color:               $white !default;
$btn-danger-bg:                  $brand-danger !default;
$btn-danger-border:              $btn-danger-bg !default;

$btn-video-hover-color:          $link-rev-color !default;

$btn-reversed-attached-bg:       adjust-hue(lighten($brand-primary, 13%), 353) !default;

$btn-link-disabled-color:        $gray !default;

$btn-padding-x-sm:               0.75rem !default;
$btn-padding-y-sm:               0.25rem !default;

$btn-padding-x-xs:               0.5rem !default;
$btn-padding-y-xs:               0 !default;

$btn-padding-x-lg:               1.25rem !default;
$btn-padding-y-lg:               0.75rem !default;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:              $border-radius !default;
$btn-border-radius-lg:           $border-radius-lg !default;
$btn-border-radius-sm:           $border-radius-sm !default;


// Forms

$input-padding-x:                0.5rem !default;
$input-padding-y:                0.4375rem !default; // 7px

$input-bg:                       #fff !default;
$input-bg-disabled:              $gray-lighter !default;

$input-color:                    $body-color !default;
$input-border-color:             $gray !default;
$input-border-hover:             $gray-darker !default;
$input-btn-border-width:         $border-width !default; // For form controls and buttons
$input-box-shadow:               none !default;

$input-border-radius:            $border-radius !default;
$input-border-radius-lg:         $border-radius-lg !default;
$input-border-radius-sm:         $border-radius-sm !default;

$input-border-focus:             $brand-primary !default;
$input-label-focus:              $brand-primary !default;
$input-box-shadow-focus:         0 0 0 1px rgba($white, 0.3), 0 0 6px 2px rgba($focus-shadow-color, 0.7) !default;

$input-custom-color:             $brand-primary !default;

$input-color-placeholder:        $gray !default;

$input-padding-x-sm:             0.75rem !default;
$input-padding-y-sm:             0.275rem !default;

$input-padding-x-lg:             1.25rem !default;
$input-padding-y-lg:             0.75rem !default;

$input-height:                   (($font-size-base * $line-height) + ($input-padding-y * 2) + 0.125rem) !default;
$input-height-lg:                (($font-size-lg * $line-height-lg) + ($input-padding-y-lg * 2) + 0.125rem) !default;
$input-height-sm:                (($font-size-sm * $line-height-sm) + ($input-padding-y-sm * 2) + 0.125rem) !default;

$form-group-margin-bottom:       0.5rem !default;

$input-group-addon-bg:           $gray-lighter !default;
$input-group-addon-border-color: $input-border-color !default;

$cursor-disabled:                not-allowed !default;

// Form elements and validation icons

// $form-select-down: url(../images/theme-base/select-caret-down.svg);
// $form-select-up: url(../images/theme-base/select-caret-up.svg);
