// Display utilities
// scss-lint:disable ImportantRule
.d-block {
	display: block !important;
}

.d-inline {
	display: inline !important;
}

.d-inline-block {
	display: inline-block !important;
}

// Vertical alignment utilities
.align-top {
	vertical-align: top !important;
}

.align-text-top {
	vertical-align: text-top !important;
}

.align-middle {
	vertical-align: middle !important;
}

.align-text-bottom {
	vertical-align: text-bottom !important;
}

.align-baseline {
	vertical-align: baseline !important;
}

.align-bottom {
	vertical-align: bottom !important;
}

// Floats
.clearfix {
	@include clearfix();
}

.center-block {
	@include center-block();
}

@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		.pull-#{$breakpoint}-left {
			@include pull-left();
		}

		.pull-#{$breakpoint}-right {
			@include pull-right();
		}

		.pull-#{$breakpoint}-none {
			float: none !important;
		}
	}
}

// Screenreaders

.sr-only,
.ext-ref {
	@include sr-only();
}

.sr-only-focusable {
	@include sr-only-focusable();
}

.invisible {
	visibility: hidden !important;
}

.text-hide {
	@include text-hide();
}

// Text

// Alignment

.text-justify {
	text-align: justify !important;
}

.text-hide-overflow {
	overflow: hidden !important;
}

.text-nowrap {
	white-space: nowrap !important;
}

.text-truncate {
	@include text-truncate;
}

// Responsive alignment

@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		.text-#{$breakpoint}-left {
			text-align: left !important;
		}

		.text-#{$breakpoint}-right {
			text-align: right !important;
		}

		.text-#{$breakpoint}-center {
			text-align: center !important;
		}
	}
}

// Transformation

.text-lowercase {
	text-transform: lowercase !important;
}

.text-uppercase {
	text-transform: uppercase !important;
}

.text-capitalize {
	text-transform: capitalize !important;
}

// Weight and italics

.font-weight-normal {
	font-weight: normal;
}

.font-weight-bold {
	font-weight: 600;
}

.font-italic {
	font-style: italic;
}

// Reset
.reset-text {
	@include reset-text();
}

// Contextual colors

.text-muted {
	color: $text-muted;
}

// Neutral text colors
.text-black { color: $black; }
.text-gray-1 { color: $gray-1; }
.text-gray-2 { color: $gray-2; }
.text-gray-3 { color: $gray-3; }
.text-gray-4 { color: $gray-4; }
.text-gray-5 { color: $gray-5; }
.text-gray-6 { color: $gray-6; }
.text-gray-7 { color: $gray-7; }
.text-gray-8 { color: $gray-8; }
.text-gray-9 { color: $gray-9; }
.text-gray-10 { color: $gray-10; }
.text-gray-11 { color: $gray-11; }
.text-gray-12 { color: $gray-12; }
.text-white { color: $white; }

.text-price,
.text-green {
	color: $green;
}

// Text variants for better hover states
@include text-emphasis-variant('.text-primary', $text-primary);

@include text-emphasis-variant('.text-secondary', $brand-secondary);

@include text-emphasis-variant('.text-success', $brand-success);

@include text-emphasis-variant('.text-info', $brand-info);

@include text-emphasis-variant('.text-warning', $brand-warning);

@include text-emphasis-variant('.text-danger', $brand-danger);


