.footer-mylt {
  transition: all 0.2s linear;
  background: #333;
  padding: 2em 0 6em;
}
.footer-mylt ul {
  margin: 0;
  padding: 0;
}
.footer-mylt ul li {
  display: inline-block;
}
.footer-mylt ul li::before {
  content: none;
}

/*----------------------------------------
Social Links */
@media (min-width: 768px) {
  .footer-social {
    float: right;
    margin-left: 1.5em;
    text-align: right;
  }
  .footer-social li {
    margin-left: 4px;
  }
  .footer-social li:last-child {
    margin-right: 0;
  }
}
.footer-social ul {
  font-size: 1em;
  margin-bottom: 1.5em;
  text-align: center;
}
.footer-social li {
  margin: 0 3px;
}
.footer-social a {
  border: 1px solid #fff;
  border-radius: 50%;
  color: #fff;
  display: block;
  height: 34px;
  line-height: 30px;
  text-align: center;
  width: 34px;
}
.footer-social a:visited {
  color: #fff;
}
.footer-social a:focus, .footer-social a:hover {
  background: #fff;
  color: #222;
}
.footer-social a:active {
  background: #aaa;
  border-color: #aaa;
  color: #222;
}

/*----------------------------------------
