// Icon Font

@font-face {
	font-family: 'mylt-icons';
	src: url('#{$font-path}icons/mylt-icons.ttf?bd35lx') format('truetype'),
			 url('#{$font-path}icons/mylt-icons.woff?bd35lx') format('woff'),
			 url('#{$font-path}icons/mylt-icons.svg?bd35lx#mylt-icons') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^='ico-'],
[class*=' ico-'] {
	display: inline-block;
	font-family: 'mylt-icons';
	font-size: 1.25rem;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	position: relative;
	vertical-align: middle;
	transform: translateY(-0.05em);

	// Better Font Rendering ===========
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


