// Icon Size Fixes
.icon-adjust-vert {
	line-height: 0;
}

.icon-adjust-horiz {
	&.ico-arrow-left-sm {
		width: 0.3em;
		text-indent: -0.35em;
	}

	&.ico-arrow-right-sm {
		width: 0.3em;
		text-indent: -0.4em;
	}

	&.ico-cost-sm {
		width: 0.35em;
		text-indent: -0.3em;
	}

	&.ico-facebook,
	&.ico-map-marker-sm {
		width: 0.4em;
		text-indent: -0.3em;
	}

	&.ico-clock-sm,
	&.ico-arrow-up-sm,
	&.ico-arrow-down-sm,
