// Indent text for floating icons
.text-icon-indent {
	padding-left: $text-indent-sm;
	position: relative;

	@include media-breakpoint-up(md) {
		padding-left: $text-indent;
	}

	[class^='ico-'],
	[class*=' ico-'] {
		margin-left: -$text-indent-sm;
		margin-right: 0;
		width: $text-indent-sm;

		@include media-breakpoint-up(md) {
			margin-left: -$text-indent;
			width: $text-indent;
		}
	}
}

.text-icon-indent-sm {
	padding-left: 1rem;
	position: relative;

	[class^='ico-'],
	[class*=' ico-'] {
		margin-left: -1rem;
		margin-right: 0;
		width: 1rem;
	}
}

// social link icons
$facebook-color: #3b5999;
$twitter-color: #55acee;
$instagram-color: #e4405f;
$youtube-color: #cd201f;
$tiktok-color: #ee1d52;
$mail-color: $gray-3;

.icon-social-link,
.icon-social-link-solid {
	border: 1px solid transparent;
	border-radius: 50%;
	padding: 0.3em; // 6px with 20px icon
}

.icon-social-link:hover,
.icon-social-link:focus,
.icon-social-link-solid {
	color: $white !important;

	&.ico-facebook {
		background-color: $facebook-color;
	}

	&.ico-twitter {
		background-color: $twitter-color;
	}

	&.ico-instagram {
		background-color: $instagram-color;
	}

	&.ico-youtube {
		background-color: $youtube-color;
	}

	&.ico-tiktok {
		background-color: $tiktok-color;
	}

	&.ico-mail,
	&.ico-mail-solid {
		background-color: $mail-color;
	}
}

.icon-social-link {
	color: inherit !important;
	border-color: currentColor;
	transition: background-color 0.15s linear;

	@include hover-focus {
		border-color: transparent;
	}
}

.icon-social-link-solid {

	@include hover-focus {
		&.ico-facebook {
			background-color: darken($facebook-color, 10%);
		}

		&.ico-twitter {
			background-color: darken($twitter-color, 10%);
		}

		&.ico-instagram {
			background-color: darken($instagram-color, 10%);
		}

		&.ico-youtube {
			background-color: darken($youtube-color, 10%);
		}

		&.ico-tiktok {
			background-color: darken($tiktok-color, 10%);
		}

		&.ico-mail,
		&.ico-mail-solid {
			background-color: darken($mail-color, 10%);
		}
	}
}

.icon-social-link:active,
.icon-social-link-solid:active {
	&.ico-facebook {
		background-color: darken($facebook-color, 14%);
	}

	&.ico-twitter {
		background-color: darken($twitter-color, 14%);
	}

	&.ico-instagram {
		background-color: darken($instagram-color, 14%);
	}

	&.ico-youtube {
		background-color: darken($youtube-color, 14%);
	}

	&.ico-tiktok {
		background-color: darken($tiktok-color, 14%);
	}

	&.ico-mail,
	&.ico-mail-solid {
		background-color: darken($mail-color, 14%);
	}
}

// this a unique version that uses larger, inlined svgs of these icons
$icon-size: 2.5rem; //40px
$icon-svg-size: 1.75rem; //28px
$icon-offset: 0.625rem;

.svg-icon-wrap {
	padding-left: $icon-size + $spacer-half;
	padding-top: $icon-offset;
	min-height: $icon-size;
	margin-bottom: $spacer;

	.svg-icon {
		display: inline-block;
		float: left;
		margin-left: -($icon-size + $spacer-half);
		margin-top: -$icon-offset;
		margin-bottom: $spacer;
		width: $icon-size;
		height: $icon-size;
		border: 1px solid currentColor;
		padding-top: ($icon-size - $icon-svg-size - 0.125rem) / 2;// extra rem value to account for border
		border-radius: 50%;
		text-align: center;
	}
}
