// Bootstrap button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($color, $background, $border) {
	$active-background: darken($background, 10%);
	$active-border: darken($border, 12%);

	color: $color;
	background-color: $background;
	border-color: $border;
	@include box-shadow(inset 0 1px 0 rgba($white, 0.15), 0 1px 1px rgba($black, 0.075));

	@include hover-focus {
		color: $color;
		background-color: $active-background;
		border-color: $active-border;
	}

	&:active,
	.open > &.dropdown-toggle {
		color: $color;
		background-color: $active-background;
		border-color: $active-border;
		// Remove the gradient for the pressed/active state
		background-image: none;
		@include box-shadow(inset 0 3px 5px rgba($black, 0.125));

		@include hover-focus {
			color: $color;
			background-color: darken($background, 17%);
			border-color: darken($border, 25%);
		}
	}

	&.disabled,
	&:disabled {
		@include hover-focus {
			background-color: $background;
			border-color: $border;
		}
	}
}

@mixin button-outline-variant($color) {
	color: $color;
	background-image: none;
	background-color: transparent;
	border-color: $color;

	&:hover,
	&:focus,
	&:active,
	.open > &.dropdown-toggle {
		color: $white;
		background-color: $color;
		border-color: $color;
	}

	&.disabled,
	&:disabled {
		@include hover-focus {
			border-color: lighten($color, 20%);
		}
	}
}

// Button sizes
@mixin button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {
	padding: $padding-y $padding-x;
	font-size: $font-size;
	line-height: $line-height;
	@include border-radius($border-radius);
}

// Additional button variants

@mixin button-solid-variant($color, $background) {
	$hover-background: darken($background, 10%);
	$active-background: darken($background, 14%);

	color: $color;
	background-color: $background;
	border-color: transparent;
	box-shadow: none;

	&:visited {
		color: $color;
	}

	@include hover-focus {
		border-color: transparent;
		background-color: $hover-background;
		color: $color;
	}

	&:active,
	.open > &.dropdown-toggle {
		background-color: $active-background;
		color: $color;
	}

	&:active,
	.open > &.dropdown-toggle {
		// Remove the gradient for the pressed/active state
		background-image: none;
		//@include box-shadow(inset 0 3px 5px rgba(0,0,0,.125));

		&:hover,
		&:focus {
			background-color: $active-background;
		}
	}

	// scss-lint:disable QualifyingElement
	&.disabled,
	&:disabled,
	fieldset[disabled] & {
		background-color: $gray-light;
		color: $gray;

		// scss-lint:disable DeclarationOrder
		&:focus {
			background-color: $gray-light;
		}

		@include hover {
			background-color: $gray-light;
		}
	}
}

@mixin button-transparent-outline-variant($color, $border) {
	$hover-color: darken($color, 10%);
	$active-color: darken($color, 14%);
	$hover-border: darken($border, 10%);
	$active-background: darken($border, 14%);
	$active-border: darken($border, 14%);

	color: $color;
	background-image: none;
	background-color: transparent;
	border-color: $border;

	@include hover-focus {
		color: $active-color;
		background-color: transparent;
		border-color: $active-border;
	}

	&:active,
	.open > &.dropdown-toggle {
		color: $active-color;
		background-color: transparent;
		border-color: $active-border;
	}

	// scss-lint:disable QualifyingElement
	&.disabled,
	&:disabled,
	fieldset[disabled] & {
		border-color: $gray-light;
		color: $gray;

		// scss-lint:disable DeclarationOrder
		@include hover-focus {
			border-color: $gray-light;
		}
	}
}

