// Textual form controls
// scss-lint:disable QualifyingElement

legend {
	font-size: 0.875rem; // match label style
	margin-bottom: 0.125rem;
	padding-top: 0.125rem;
}

.form-control {
	display: block;
	width: 100%;
	padding: $input-padding-y $input-padding-x;
	font-size: $font-size-base;
	line-height: $line-height;
	color: $input-color;
	background-color: $input-bg;
	// Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214.
	background-image: none;
	border: $input-btn-border-width solid $input-border-color;
	// Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.
	@include border-radius($input-border-radius);
	@include box-shadow($input-box-shadow);
	@include transition(color 0.2s linear, border-color 0.2s linear, box-shadow 0.2s linear);

	// Unstyle the caret on `<select>`s in IE10+.
	&::-ms-expand {
		background-color: transparent;
		border: 0;
	}

	// Customize the `:focus` state to imitate native WebKit styles.
	@include form-control-focus();

	&:hover {
		border-color: $input-border-hover;
		color: $gray-2;
	}

	// Placeholder
	&::placeholder {
		color: $input-color-placeholder;
		// Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
		opacity: 1;
	}

	// Disabled and read-only inputs
	//
	// HTML5 says that controls under a fieldset > legend:first-child won't be
	// disabled if the fieldset is disabled. Due to implementation difficulty, we
	// don't honor that edge case; we style them as disabled anyway.
	&:disabled,
	&[readonly] {
		background-color: $input-bg-disabled;
		// iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.
		opacity: 1;
	}

	&:disabled {
		cursor: $cursor-disabled;
		opacity: 0.5;
	}
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	// IE11 only adjustment
	.form-control:not(textarea),
	.c-select {
		height: $input-height;
	}
	.c-select-sm {
		height: $input-height-sm;
	}
}

// Make file inputs better match text inputs by forcing them to new lines.
.form-control-file,
.form-control-range {
	font-size: $font-size-base;
	display: block;
}

// Labels

label {
	margin-bottom: 0.063rem;
	transition: color 0.15s;

	&.has-focus {
		color: $input-label-focus;
	}

	.form-group & {
		font-size: 0.875rem;
	}

	// we only want font to be sized down in standard vertical stacking of text inputs and selects
	// not when using the .form-group as a row, and not when the label is associated with a checkbox
	// or radio, so order is important here
	.form-group.row &,
	.radio &,
	.checkbox &,
	.radio-inline &,
	.checkbox-inline & {
		font-size: inherit;
	}
}

// adds padding and resizes text to align labels with input text
.form-control-label {
	padding: $input-padding-x;
	margin-bottom: 0; // Override the `<label>` default

	.form-group & {
		font-size: inherit;
	}

	.row > & {
		padding-left: $spacer-grid;
		padding-right: $spacer-grid;
	}
}

// Float Labels
.float-label {
	@include transition(all 0.15s linear);
	transform: translateY(100%);
	opacity: 0;

	// positions the associated input above the transformed label
	+ input,
	+ textarea,
	+ select {
		position: relative;
		z-index: 2;
	}

	&.has-value {
		transform: translateY(0%);
		opacity: 1;
	}
}

// Helper text
.label-help {
	color: $gray;
	font-size: 0.875rem; // 14px
	display: block;
	margin-bottom: 0;
	padding: 0.25rem $spacer-half 0;
}

ul.label-help {
	padding-left: 1.75rem;
}

// Leading Icons
.input-icon {
	background-position: left 10px center;
	background-repeat: no-repeat;
	padding-left: 2.5rem;

	// https://bugs.chromium.org/p/chromium/issues/detail?id=46543
	&:-webkit-autofill,
	&:-webkit-autofill:hover,
	&:-webkit-autofill:focus {
		padding-left: 0.5rem;
	}
}

.input-icon-username {
	background-image: url('#{$image-path}theme-base/icon-input-username.svg');
	background-size: 20px 20px;
}

.input-icon-password {
	background-image: url('#{$image-path}theme-base/icon-input-password.svg');
	background-size: 20px 12px;
}

.input-icon-search {
	background-image: url('#{$image-path}theme-base/icon-input-search.svg');
	background-size: 23px 23px;
}

