// Special styles for iOS temporal inputs
//
// In Mobile Safari, setting `display: block` on temporal inputs causes the
// text within the input to become vertically misaligned. As a workaround, we
// set a pixel line-height that matches the given height of the input, but only
// for Safari. See https://bugs.webkit.org/show_bug.cgi?id=139848
//
// Note that as of 8.3, iOS doesn't support `datetime` or `week`.

@media screen and (-webkit-min-device-pixel-ratio: 0) {
	input[type="date"],
	input[type="time"],
	input[type="datetime-local"],
	input[type="month"] {
		&.form-control {
			line-height: $input-height;
		}

		&.input-sm,
		.input-group-sm &.form-control {
			line-height: $input-height-sm;
		}

		&.input-lg,
		.input-group-lg &.form-control {
			line-height: $input-height-lg;
		}
	}
}


// Static form control text
//
// Apply class to an element to make any string of text align with labels in a
// horizontal form layout.

.form-control-static {
	min-height: $input-height;
	// Size it appropriately next to real form controls
	padding-top: $input-padding-y;
	padding-bottom: $input-padding-y;
	// Remove default margin from `p`
	margin-bottom: 0;

	&.form-control-sm,
	&.form-control-lg {
		padding-right: 0;
		padding-left: 0;
	}
}

// Form control sizing
//
// Build on `.form-control` with modifier classes to decrease or increase the
// height and font-size of form controls.
//
// The `.form-group-* form-control` variations are sadly duplicated to avoid the
// issue documented in https://github.com/twbs/bootstrap/issues/15074.

.form-control-sm {
	padding: $input-padding-y-sm $input-padding-x-sm;
	font-size: $font-size-sm;
	line-height: $line-height-sm;
	@include border-radius($input-border-radius-sm);
}

.form-control-lg {
	padding: $input-padding-y-lg $input-padding-x-lg;
	font-size: $font-size-lg;
	line-height: $line-height-lg;
	@include border-radius($input-border-radius-lg);
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	// IE11 only adjustments
	.form-control-sm {
		height: $input-height-sm;
	}

	.form-control-lg {
		height: $input-height-lg;
	}
}


// Form groups
//
// Designed to help with the organization and spacing of vertical forms. For
// horizontal forms, use the predefined grid classes.

.form-group {
	margin-bottom: $form-group-margin-bottom;

	&.row {
		margin-bottom: $spacer-half;
	}

	&.form-group-spacer {
		margin-bottom: $spacer;
	}
}


// Checkboxes and radios
//
// Indent the labels to position radios/checkboxes as hanging controls.

.radio,
.checkbox {
	position: relative;
	display: block;
	margin-bottom: $spacer;

	label {
		padding-left: 1.5rem;
		margin-bottom: 0;
		font-weight: normal;
		cursor: pointer;
	}
}

.radio input[type="radio"],
.radio-inline input[type="radio"],
.checkbox input[type="checkbox"],
.checkbox-inline input[type="checkbox"] {
	position: absolute;
	margin-top: 0.275rem;
	margin-left: -1.5rem;
}

.radio + .radio,
.checkbox + .checkbox {
	// Move up sibling radios or checkboxes for tighter spacing
	margin-top: -$spacer-half;
}

// Radios and checkboxes on same line
.radio-inline,
.checkbox-inline {
	position: relative;
	display: inline-block;
	padding-left: 1.5rem;
	margin-bottom: 0;
	font-weight: normal;
	vertical-align: middle;
	cursor: pointer;
}

.radio-inline + .radio-inline,
.checkbox-inline + .checkbox-inline {
	margin-top: 0;
	margin-left: 0.75rem;
}

// Apply same disabled cursor tweak as for inputs
// Some special care is needed because <label>s don't inherit their parent's `cursor`.
//
// Note: Neither radios nor checkboxes can be readonly.
input[type="radio"],
input[type="checkbox"] {
	&:disabled,
	&.disabled {
		cursor: $cursor-disabled;
	}
}

// These classes are used directly on <label>s
.radio-inline,
.checkbox-inline {
	&.disabled {
		cursor: $cursor-disabled;
	}
}

// These classes are used on elements with <label> descendants
.radio,
.checkbox {
	&.disabled {
		label {
			cursor: $cursor-disabled;
		}
	}
}


// Form control feedback states
//
// Apply contextual and semantic states to individual form controls.

.form-control-success,
.form-control-warning,
.form-control-danger {
	padding-right: 2.25rem;
	background-repeat: no-repeat;
	background-position: center right ($input-height * 0.25);
	background-size: 18px 16px;

	&.input-icon {
		padding-right: 0.5rem;
		background-position: center left ($input-height * 0.25);
	}
}

// Form validation states
.has-success,
.has-warning,
.has-danger {

	label {
		font-weight: bold;
	}

	.form-control {
		position: relative;
		z-index: 2;
	}

	.label-help {
		color: $gray-dark;
		margin-top: -$input-border-radius;
		padding-top: 4px + $input-border-radius;
		padding-bottom: 4px;
		border-radius: 0 0 $input-border-radius $input-border-radius;
	}
}

.has-success {
	.form-control-success {
		background-image: url($form-icon-success);
		background-size: 20px 20px;
	}

	.label-help {
		background-color: mix($white, $brand-success, 70%);
	}
}

.has-warning {
	.form-control-warning {
		background-image: url($form-icon-warning);
	}

	.label-help {
		background-color: mix($white, $brand-warning, 50%);
	}
}

.has-danger {

	label {
		color: $brand-danger;
	}

	.label-help {
		background-color: $brand-danger;
		color: $white;
	}

	.form-control-danger {
		background-image: url($form-icon-danger);
		color: $brand-danger;
	}

	.text-danger { // handles edge case where danger color might be used for emphasis (e.g. character counter)
		color: $white !important;
	}
}


// Inline forms
//
// Make forms appear inline(-block) by adding the `.form-inline` class. Inline
// forms begin stacked on extra small (mobile) devices and then go inline when
// viewports reach <768px.
//
// Requires wrapping inputs and labels with `.form-group` for proper display of
// default HTML form controls and our custom form controls (e.g., input groups).

.form-inline {
	// Kick in the inline
	@include media-breakpoint-up(sm) {
		// Inline-block all the things for "inline"
		.form-group {
			display: inline-block;
			margin-bottom: 0;
			vertical-align: middle;
		}

		// Allow folks to *not* use `.form-group`
		.form-control {
			display: inline-block;
			width: auto; // Prevent labels from stacking above inputs in `.form-group`
			vertical-align: middle;
		}

		// Make static controls behave like regular ones
		.form-control-static {
			display: inline-block;
		}

		.form-control-label {
			margin-bottom: 0;
			vertical-align: middle;
			line-height: inherit;
		}

		// Remove default margin on radios/checkboxes that were used for stacking, and
		// then undo the floating of radios and checkboxes to match.
		.radio,
		.checkbox {
			display: inline-block;
			margin-top: 0;
			margin-bottom: 0;
			vertical-align: middle;

			label {
				padding-left: 0;
			}
		}

		.radio input[type="radio"],
		.checkbox input[type="checkbox"] {
			position: relative;
			margin-left: 0;
		}

		// Re-override the feedback icon.
		.has-feedback .form-control-feedback {
			top: 0;
		}
	}
}

// DEPRECATED CLASSES in favor of BS4
label {
	&.is-active,
	&.is-success {
		color: $input-label-focus;
	}

	&.is-error,
	.is-required {
		color: $brand-danger;
	}
}

.form-group label.is-error {
	font-weight: bold;
}

.form-control.is-invalid,
.form-control.is-valid {
	padding-right: ($input-padding-x * 3);
	background-repeat: no-repeat;
	background-position: center right ($input-height * 0.25);
	background-size: 18px 16px;
}

.form-control.is-invalid {
	border-color: $brand-danger;
	color: $brand-danger;
	background-image: url($form-icon-danger);
}

.form-control.is-valid {
	border-color: $brand-primary;
	background-image: url($form-icon-success);
}

.label-help-error {
	font-size: 0.875rem;
	display: block;
	margin-bottom: 0;
	padding: 0.25rem $spacer-half;
	margin-top: -0.125rem;
	border-radius: 0 0 3px 3px;
	color: $white;
	font-weight: bold;
	background-color: $brand-danger;
}

.checkbox-container,
.radio-container {
	margin-bottom: $spacer;

	input {
		margin-top: 0.25rem;
		margin-right: 0.25rem;
		display: inline-block;
		vertical-align: text-top;
	}

	label {
		width: calc(100% - 2rem);
		vertical-align: text-top;
		margin-bottom: 0;
	}
}

.checkbox-container + .checkbox-container,
.radio-container + .radio-container {
	margin-top: -$spacer-half;
}
// END deprecated

