// Add buttons internal on text inputs
.form-input-action {
	position: relative;

	.form-control {
		padding-right: 2.5rem; // extra padding to allow for button
		background-image: none;

		// Remove the IE clear field 'X' when button added
		&::-ms-clear {
			display: none;
		}
	}
}

.form-input-btn {
	@include transition(opacity 0.2s ease-in-out);
	position: absolute;
	z-index: 10;
	color: $link-color;
	text-align: center;
	height: $input-height;
	width: 2rem;
	line-height: $input-height;
	top: 0;
	right: 0;
	padding: 0;
	cursor: pointer;
	background: transparent;
	opacity: 1;
	border: 0;
	-webkit-appearance: none;

	@include hover-focus {
		opacity: 0.5;
		outline: 0;
	}
}

.form-input-clear {
	color: $gray-6;
	opacity: 0;
	visibility: hidden;

	&.has-value {
		opacity: .8;
		visibility: visible;

		@include hover-focus {
			opacity: 1;
			color: $aqua; // TODO: Create a nice-looking box-shadow for this
		}
	}
}

// within .input-group
.input-group .form-input-action {
	flex: 1;

	.form-control {
		@include border-radius(0);
	}

	&:first-child .form-control {
		@include border-left-radius($input-border-radius);
	}

	&:last-child .form-control {
		@include border-right-radius($input-border-radius);
	}
}
