.nav {
	display: flex;
	flex-wrap: wrap;
	padding-left: 0;
	margin: 0 -#{$nav-link-spacer-x};
	list-style: none;

	&.link-underlined,
	.link-underlined & {
		margin-bottom: $spacer;
	}
}

.nav-link {
	display: block;
	padding: $nav-link-spacer-y 0;
	margin: 0 $nav-link-spacer-x;

	.link-underlined & {
		display: inline-block;
		padding: 0;
		margin-bottom: $nav-link-spacer-y;
	}
}

.nav-link-highlight {
	font-weight: bold;
}

// nav color variations
.nav-neutral {
	.nav-link:not(.nav-link-highlight) {
		color: $gray-4;

		@include hover-focus {
			color: $link-color;
		}
	}

	.disabled {
		color: $gray-light !important;
	}

	.dropdown-toggle {
		&::after {
			color: $gray-5;
		}

		@include hover-focus {
			&::after {
				color: inherit;
			}
		}
	}
}

.nav-inverse {
	.nav-link {
		color: $white;

		@include hover-focus {
			color: $link-rev-color;
		}
	}

	.nav-link-highlight {
		@include inverse-links;
	}

	.disabled {
		color: $gray-8 !important;
	}
}


// nav justifications 
.nav-center {
	justify-content: center;
}

.nav-right {
	justify-content: flex-end;
}

// nav stacking
.nav-stack-sm {
	@include media-breakpoint-down(sm) {
		flex-direction: column;

		&:not(.nav-btns) { // btns should always stretch
			align-items: flex-start;

			&.nav-center {
				align-items: center;
			}

			&.nav-right {
				align-items: flex-end;
			}
		}
	}
}

// nav tabs
.nav-tabs {
	.nav-link {
		border-bottom: $nav-tabs-border-width solid transparent;

		@include hover-focus {
			border-bottom-color: $nav-tabs-link-hover-border-color;
		}

		&.active {
			border-bottom-color: $nav-tabs-link-active-border-color;

			&:hover {
				border-bottom-color: $nav-tabs-active-link-hover-border-color;
			}
		}
	}
}

.nav-tabs-border {
	border-bottom: 1px solid $nav-tabs-link-hover-border-color;

	.nav-link {
		margin-bottom: -1px;
	}
}

.nav-tabs-wide {
	margin: 0;
	flex-wrap: nowrap;
	
	.nav-item {
		flex: 0 1 11.5rem; // 184px
		text-align: center;
	}
	
	@include media-breakpoint-down(sm) {
		.nav-item {
			flex-grow: 1;
		}
	}
	
	.nav-link {
		margin: 0;
		padding: $nav-link-spacer-y $nav-link-spacer-x;
	}
}

.nav-tabs-alt { // stylized tabs
	font-size: 0.875rem;
	margin: 0;
	flex-wrap: nowrap;
	letter-spacing: 0.04em;
    text-transform: uppercase;
	border-bottom-color: $gray-9;

	@include media-breakpoint-up(md) {
		font-size: 1rem;
	}

	.nav-link {
		margin-left: 0;
		margin-right: 0;
		padding-left: calc(0.35em + 0.75rem);
		padding-right: 0.75rem;
		font-weight: 500;

		&:not(.active) {
			color: $headings-color;

			@include hover-focus {
				color: $link-color;
				border-bottom-color: $gray-9;
			}
		}

		&.disabled {
			color: $gray-light !important;
		}
	}

	.nav-item {
		position: relative;

		&:first-child .nav-link {
			padding-left: 0.75rem;
		}
		
		&:not(:first-child) .nav-link::before {
			content: '/';
			position: absolute;
			top: $nav-link-spacer-y;
			left: 0;
			color: $gray-9 !important;
		}
	}

	@include media-breakpoint-down(sm) {
		margin: 0 -#{$spacer-grid};
		
		.nav-item {
			flex: 1 0 0px;
			text-align: center;
		}
	}
}

.nav-btns {
	margin-left: 0;
	margin-right: 0;

	.btn {
		border-radius: 0;

		&.active {
			background-color: $white;
			border-color: $gray-11;
		}
	}

	> .btn + .btn,
	> .nav-item + .nav-item {
		margin-left: 1px;
	}

	> .btn:first-child,
	> .nav-item:first-child .btn {
		border-top-left-radius: $btn-border-radius;
		border-bottom-left-radius: $btn-border-radius;
	}

	> .btn:last-child,
	> .nav-item:last-child .btn {
		border-top-right-radius: $btn-border-radius;
		border-bottom-right-radius: $btn-border-radius;
	}

	@include media-breakpoint-down(sm) {
		&.nav-stack-sm {
			.btn {
				width: 100%;	
			}

			> .btn + .btn,
			> .nav-item + .nav-item {
				margin-left: 0;
				margin-top: 1px;
			}

			> .btn:first-child,
			> .nav-item:first-child .btn {
				border-bottom-left-radius: 0;
				border-top-right-radius: $btn-border-radius;
			}

			> .btn:last-child,
			> .nav-item:last-child .btn {
				border-top-right-radius: 0;
				border-bottom-left-radius: $btn-border-radius;
			}			
		}
	}
}