// Alerts & Notifications
// scss-lint:disable VariableForProperty

@mixin alert-variant($background, $border, $body-color) {
	background-color: $background;
	border-color: $border;
	color: $body-color;

	hr {
		border-top-color: darken($border, 5%);
	}

	.alert-link {
		color: darken($body-color, 10%);
	}
}

@mixin alert-alternate-variant($color, $character: false) {

	@if($character) {

		&.alert-icon {
			&:not(.alert-notification),
			&.alert-notification .container {
				&::before {
					color: $color;
					content: $character;
				}
			}
		}
	}

	border-color: $color;

	&.alert-notification {
		background-color: mix($white, $color, 90%);
		border-color: mix($white, $color, 50%);
	}

	// Alert Link
	.alert-link {
		// scss-lint:disable VariableForProperty
		color: darken($color, 10%);

		&:hover {
			color: darken($color, 15%);
		}
	}
}

