//
// Base Theme Custom Multiselect

.multiselect {
	position: relative;
}

.multiselect-btn {
	@include transition(color 0.2s linear, border-color 0.2s linear, box-shadow 0.2s linear);
	background: $white url($form-select-down) no-repeat right $input-padding-x center;
	background-size: 15px 15px;
	border: 1px solid $input-border-color;
	border-radius: $border-radius;
	color: $body-color;
	display: block;
	text-align: left;
	width: 100%;
	padding: $input-padding-y 1.9375rem $input-padding-y $input-padding-x;

	&:hover,
	&:focus {
		color: $gray-dark;
	}

	&:focus {
		border-color: $input-border-focus;
		box-shadow: $input-box-shadow-focus;
		outline: 0;
	}

	&.is-active { // open state
		background-color: $component-active-bg;
		background-image: url($form-select-up);
		border-color: $btn-primary-border;
		border-radius: $border-radius $border-radius 0 0;
		color: $component-active-color;
	}

	&:disabled {
		cursor: $cursor-disabled;
		color: $input-color;
		background-color: $input-bg-disabled;
		opacity: 0.5;
	}
}

.multiselect-wrap {
	background-color: $white;
	border: 1px solid $input-border-color;
	border-top: 0;
	border-radius: 0 0 $border-radius $border-radius;

	.multiselect & {
		display: none;
	}

	// Form stacking context
	position: relative;
	z-index: 0;

	.c-checkbox,
	.c-checkbox-sm { // adapted from normal .c-inputs-stacked
		width: 100%;
		margin: 0;
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
	}

	.c-indicator {
		top: 0.625rem;
	}

	.c-input > input {
		~ .c-indicator {
			border-color: $gray;
		}

		&:hover ~ .c-indicator,
		&:focus ~ .c-indicator {
			border-color: $input-custom-color;
		}

		&:focus ~ .c-indicator {
			box-shadow: $input-box-shadow-focus;
		}

		&:disabled ~ .c-indicator {
			background-color: $gray-light;
			border-color: $gray-light;
		}
	}

	.c-checkbox,
	.c-checkbox-sm {
		input:checked:not(:disabled) ~ .c-indicator {
			background-color: $input-custom-color;
		}
	}

	@include media-breakpoint-up(md) {
		@include box-shadow-default();
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		z-index: $zindex-dropdown;
		overflow: hidden;
	}
}

.multiselect-group {
	padding: 0.25rem $spacer-half 0;

	@include media-breakpoint-up(md) {
		max-height: 420px;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
	}
}

.multiselect-subheading {
	@include table-header();
	padding: $spacer-half 0;
	margin-bottom: 0;
	line-height: inherit;
}

.multiselect {
	.multiselect-subheading,
	.c-checkbox:not(:last-child),
	.c-checkbox-sm:not(:last-child) {
		border-bottom: 1px solid $gray-lightest;
	}
}

.has-danger { // TODO: Add .has-warning styles?
	.multiselect-btn {
		background-image: url($form-select-down-danger);
		color: $brand-danger;
		border-color: $brand-danger;

		&:focus {
			box-shadow: 0 0 6px rgba($brand-danger, 0.7);
		}

		&.is-active {
			background-color: $brand-danger;
			color: $white;
			background-image: url($form-select-up);
		}
	}

	.multiselect-wrap {
		border-color: $brand-danger;

		label {
			color: $body-color;
			font-weight: normal;
		}
	}
}








// Typeahead Multiselect

.typeahead-multiselect {
	position: relative;

	// Disabled class to mimic :disabled styles
	&.disabled {
		.multi-panel,
		.multi-label,
		.multi-btn {
			cursor: $cursor-disabled;
		}
		.multi-panel {
			color: $input-color;
			background-color: $input-bg-disabled;
			opacity: 0.5;
		}
	}

	&:focus:not(.disabled) {
		outline: none;

		.multi-panel {
			border-color: $aqua;
			@include box-shadow(0 0 6px rgba($aqua, 0.7));
		}
	}

	// Active (expanded) state
	&.is-active {
		.multi-panel {
			border-radius: $border-radius $border-radius 0 0;
		}
		.multi-btn {
			background-image: url($form-select-up-alt);
		}
	}

	// Warning and Danger States
	.has-warning &,
	.has-danger & {
		label {
			font-weight: normal;
		}
	}

	.has-warning & {
		.multi-panel {
			border-color: $brand-warning;
		}
		&:focus .multi-panel {
			border-color: $brand-warning;
			@include box-shadow(0 0 6px rgba($brand-warning, 0.7));
		}
	}
	.has-danger & {
		.multi-panel {
			border-color: $brand-danger;
		}
		&:focus .multi-panel {
			border-color: $brand-danger;
			@include box-shadow(0 0 6px rgba($brand-danger, 0.7));
		}
	}
	
	.multiselect-wrap {
		@include box-shadow-default();
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		z-index: $zindex-dropdown;
		overflow: hidden;
	}

	.multiselect-group {
		padding: 0.5rem 0;
		display: relative; // Needed for jQuery position()
		max-height: 420px;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
	}

	.multiselect-subheading {
		padding: 0.125rem 0 0.125rem $spacer-half;
	}

	.c-input,
	.no-results {
		padding: 0.25rem 0 0.25rem $spacer;
		line-height: 1.25;
	}

	.no-results {
		font-style: italic;
	}

	.c-input {
		width: 100%;
		margin: 0;
		font-size: 1rem;
		transition: color 0.1s linear;
		position: relative;
		display: inline-block;

		input:checked ~ .c-indicator::after {
			background-color: $gray-12;
		}

		input:not(:disabled):hover ~ .c-indicator::after {
			background-color: $gray-11;
			color: $gray-4;
		}

		// Show an alternate custom "focus outline" if using the keyboard
		input.has-focus ~ .c-indicator {
			html[data-whatinput="keyboard"] & {
				&::after {
					box-shadow: inset 0 0 4px 1px $focus-shadow-color;
				}
			}
		}

		&:active .c-indicator::after {
			background-color: $gray-10;
		}

		.c-indicator {
			top: 0;
			left: 0;
			background-color: transparent !important;
			border: 0 !important;
			color: $aqua !important;
			width: 100%;
			height: 100%;

			&::before {
				top: 0.375rem;
				left: 0.3125rem;
				right: auto;
			}
			&::after {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				content: '';
				background-color: $white;
				z-index: -1;
				transition: background-color 0.1s linear;
			}
		}
	}
}

.multi-panel {
	display: flex;
	align-items: center;
	border: 1px solid $input-border-color;
	border-radius: $border-radius;
	color: $body-color;
	background-color: $white;
	width: 100%;
	transition: color 0.2s linear, border-color 0.2s linear, box-shadow 0.2s linear;
}

.multi-label {
	margin: 0 0 0 $input-padding-x;
}

.multi-search {
	overflow: hidden;
	flex: 1;
	margin-left: 0.5rem;
	
	.form-input-btn {
		width: 18px;
		
		.ico-delete {
			margin: 0 -1px;
		}
	}
}

.multi-input {
	color: $gray-4;
	border: 0;
	padding-left: 0;
	padding-right: 18px !important;
	display: none;

	&:focus {
		box-shadow: none;
	}
}

.multi-focus {
	position: absolute;
	left: 0; right: 0;
	bottom: 3px;
	border-bottom: 1px solid $gray-10;
	transition: border-color 0.15s linear;
	z-index: 5; // stay above other z-indexing
	pointer-events: none;

	*:focus ~ & {
		border-bottom-color: $brand-primary;
	}
}

.multi-btn {
	background-image: url($form-select-down);
	background-repeat: no-repeat;
	background-position: left 0.375rem top 50%;
	background-size: 15px 15px;
	border-radius: $btn-border-radius;
	width: 1.9375rem;
	height: 1.9375rem;
	padding: 0.375rem;
	margin: 0.21875rem 0 0.21875rem 0;
	z-index: 2;
}
