// Links


a {
	@include transition(color 0.15s linear);

	&:active {
		color: $link-active-color;
	}

	&.disabled {
		cursor: $cursor-disabled;
		color: $gray;
	}
}

// TO DO - eventually want to have ability for classes on a parent affect the contents
// to be inversed, i.e. a .txt-inverse that causes all content within to turn white and
// links to turn this blue
.link-reversed {
	@include inverse-links;
}

.link-reversed-white {
	@include inverse-links-white;
}

// External reference indicator
// Used for screen reader only text within a target="_blank" link to add an
// arrow (pointing northeast) for visual indication of an external link
.ext-ref {
	+ span {
		&::after {
			content: '\0020\2197'; // space and arrow
			font-size: 0.875em;
		}
	}
}

// Underlined links
// Can be applied to anchors and parent containers
a.link-underlined,
.link-underlined a:not(.btn) {
	border-bottom: 2px solid rgba($link-color, 0.4);
	transition: border-color 0.15s linear, color 0.15s linear;

	@include hover-focus-active {
		border-bottom-color: currentColor;
	}
}

.link-reversed.link-underlined,
.link-underlined .link-reversed {
	border-bottom-color: rgba($link-rev-color, 0.4);
}

.link-reversed-white.link-underlined,
.link-underlined .link-reversed-white {
	border-bottom-color: rgba($white, 0.4);
}

