img {
	max-width: 100%;
	height: auto;
	-ms-interpolation-mode: bicubic;
	display: inline-block;
	vertical-align: middle;
}

img:not([src]) {
	visibility: hidden; // hide lazy loading images
}

// Bootstrap's fluid image adds display: block
.img-fluid {
	@include img-fluid();
}

// Rounded corners
.img-rounded {
	@include border-radius($border-radius-lg);
}

// Image thumbnails
.img-thumbnail {
	padding: $thumbnail-padding;
	line-height: $line-height;
	background-color: $thumbnail-bg;
	border: $thumbnail-border-width solid $thumbnail-border-color;
	border-radius: $thumbnail-border-radius;
	transition: all 0.2s ease-in-out;
	@include box-shadow(0 1px 2px rgba($black, 0.075));

	// Keep them at most 100% wide
	@include img-fluid(inline-block);
}

// Perfect circle
.img-circle {
	border-radius: 50%;
}

// Stretch to fit
.img-cover {
	width: 100%;
}

// Figures

.figure {
	display: block;
	text-align: center;
}

.figure-img {
	line-height: 1;
}

.figure-caption {
	font-size: 0.875rem;
	color: $body-color;
	margin: $spacer-half 0;
}

// Background Images
.bg-cover {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}

.bg-cover-center {
	background-position: center 33%;
}

.bg-cover-left {
	background-position: 33% 33%;
}

.bg-cover-right {
	background-position: 66% 33%;
}

.bg-cover-bottom {
	background-position: center 66%;

	&.bg-cover-left {
		background-position: 33% 66%;
	}

	&.bg-cover-right {
		background-position: 66% 66%;
	}
}

// Inline SVG
.svg-b-f { // base-fill
	fill: $svg-base-color;
}

.svg-b-s { // base-stroke
	stroke: $svg-base-color;
}

.svg-a-f { // accent-fill
	fill: $svg-accent-color;
}

.svg-a-s { // accent-stroke
	stroke: $svg-accent-color;
}
