html {
	height: 100%;
}

body,
.layout-body {
	display: flex;
	flex-direction: column;

	& > * {
		flex-shrink: 0;
	}
}

body {
	min-height: 100%;	
}

.layout-body {
	min-height: 100vh;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	// fixes IE11 bug where min-height is not respected on flex children
	// https://github.com/philipwalton/flexbugs#3-min-height-on-a-flex-container-wont-apply-to-its-flex-items
	html {
	    display: flex;
	}

	body {
	    flex: 0 0 100%;
	    -ms-overflow-style: scrollbar;
	}

	.layout-body {
		height: 100vh;
	}
}

main,
.layout-main {
	position: relative;
	flex: 1 0 auto;
	overflow: hidden;
}
