//
// Contextual backgrounds
//

// Inverse
// TO DO Deprecate - not known to be used, should use other bg classes,
// and combine with .content-inverse as needed
.bg-inverse {
	color: $gray-lighter;
	background-color: $gray-dark;
}

.bg-faded {
	background-color: $gray-lightest;
}
// end deprication

// Bootstrap BG Colors
@include bg-variant('.bg-primary', $brand-primary);
@include bg-variant('.bg-secondary', $brand-secondary);
@include bg-variant('.bg-success', $brand-success);
@include bg-variant('.bg-info', $brand-info);
@include bg-variant('.bg-warning', $brand-warning);
@include bg-variant('.bg-danger', $brand-danger);

// Neutral BG colors
.bg-black { background-color: $black; }
.bg-gray-1 { background-color: $gray-1; }
.bg-gray-2 { background-color: $gray-2; }
.bg-gray-3 { background-color: $gray-3; }
.bg-gray-4 { background-color: $gray-4; }
.bg-gray-5 { background-color: $gray-5; }
.bg-gray-6 { background-color: $gray-6; }
.bg-gray-7 { background-color: $gray-7; }
.bg-gray-8 { background-color: $gray-8; }
.bg-gray-9 { background-color: $gray-9; }
.bg-gray-10 { background-color: $gray-10; }
.bg-gray-11 { background-color: $gray-11; }
.bg-gray-12 { background-color: $gray-12; }
.bg-white { background-color: $white; }

// Striped backgrounds
// Used to automate content section background striping rather than authoring background colors for
// content that is variable.
.bg-striped {

	&:nth-of-type(even) {
		background-color: $white;
	}

	&:nth-of-type(odd) {
		background-color: $gray-12;
	}

	&-alt { // starts with the tinted background instead of white
		&:nth-of-type(even) {
			background-color: $gray-12;
		}

		&:nth-of-type(odd) {
			background-color: $white;
		}
	}
}

.bg-bio-match { // matches the color on the background of employee bio photos
	background-color: #fbfbfb;
}

// Transparent BG colors
.bg-dark-trans { background-color: $dark-trans; }
.bg-dark-semi-trans { background-color: $dark-semi-trans; }
.bg-light-trans { background-color: $light-trans; }
.bg-light-semi-trans { background-color: $light-semi-trans; }

// Isometric repeating backgrounds
.bg-iso-light {
	@include background-repeat('iso-pattern-light.svg', '47px 81px');
}

.bg-iso-medium {
	@include background-repeat('iso-pattern-medium.svg', '47px 81px', $gray-lightest);
}

.bg-iso-mid-dark {
	@include background-repeat('iso-pattern-mid-dark.svg', '47px 81px', $gray-dark);
}

.bg-iso-dark {
	@include background-repeat('iso-pattern-dark.svg', '47px 81px', $gray-darker);
}

// BG removal
.bg-0-sm-down {
	@include media-breakpoint-down(sm) {
		background: none !important;
	}
}
