.content-section {
	padding-top: 2rem; //32px
	padding-bottom: (2rem - $spacer);

	@include media-breakpoint-up(lg) {
		padding-top: 3rem; //48px
		padding-bottom: (3rem - $spacer);
	}
}

.content-mt {
	margin-top: 2rem;

	@include media-breakpoint-up(lg) {
		margin-top: 3rem;
	}
}

.content-inverse {
	@include inverse-content;

	// Forms
	.c-input {
		> input {

			&:checked ~ .c-indicator,
			&:checked:hover ~ .c-indicator,
			&:checked:focus ~ .c-indicator {
				border-color: $aqua-light;
			}

			&:hover ~ .c-indicator,
			&:focus ~ .c-indicator {
				border-color: $white;
			}
		}
	}

	.c-indicator {
		border-color: $white;
	}
}


.content-section-xs {
    padding-top: $spacer-half;
    padding-bottom: $spacer-half;

    @include media-breakpoint-up(lg) {
        padding-top: $spacer;
        padding-bottom: $spacer;
    }
}

// Tiletrios
[class*='content-tiletrio-'] {
	padding-top: 0;
	padding-bottom: 0;

	> .content-section {
		padding-left: $spacer-grid;
		padding-right: $spacer-grid;
	}

	@include media-breakpoint-down(md) {
		> * {
			margin-bottom: $spacer-grid * 2;
		}
	}
}


@include media-breakpoint-up(lg) {
	[class*='content-tiletrio-'] {
		padding-bottom: $spacer-grid * 2;
		display: grid;
		grid-gap: ($spacer-grid * 2) ($spacer-grid * 2);

		> .content-section {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}

	.content-tiletrio-north,
	.content-tiletrio-south {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: auto auto;
	}

	.content-tiletrio-north {
		> *:first-child {
			grid-column: 1 / span 2;
			grid-row: 1;
		}

		> *:nth-child(2) {
			grid-column: 1;
			grid-row: 2;
		}

		> *:last-child {
			grid-column: 2;
			grid-row: 2;
		}
	}

	.content-tiletrio-south {
		> *:first-child {
			grid-column: 1;
			grid-row: 1;
		}

		> *:nth-child(2) {
			grid-column: 2;
			grid-row: 1;
		}

		> *:last-child {
			grid-column: 1 / span 2;
			grid-row: 2;
		}
	}

	.content-tiletrio-east,
	.content-tiletrio-west {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;

		> picture img,
		> img {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}

	.content-tiletrio-east {
		> *:first-child {
			grid-column: 1;
			grid-row: 1;
		}

		> *:nth-child(2) {
			grid-column: 1;
			grid-row: 2;
		}

		> *:last-child {
			grid-column: 2;
			grid-row: 1 / span 2;
		}
	}

	.content-tiletrio-west {
		> *:first-child {
			grid-column: 1;
			grid-row: 1 / span 2;
		}

		> *:nth-child(2) {
			grid-column: 2;
			grid-row: 1;
		}

		> *:last-child {
			grid-column: 2;
			grid-row: 2;
		}
	}
}

@media screen and (min-width: 992px) and (-ms-high-contrast: active),
screen and (min-width: 992px) and (-ms-high-contrast: none) {
	[class*='content-tiletrio-'] > picture,
	[class*='content-tiletrio-'] > img {
		display: inline-block;
	}

	.content-tiletrio-north {
		> *:first-child {
			margin-bottom: $spacer-grid * 2;
		}

		> *:nth-child(2) {
			margin-right: $spacer-grid;
		}

		> *:last-child {
			margin-left: $spacer-grid;
		}
	}

	.content-tiletrio-south {
		> *:first-child {
			margin-right: $spacer-grid;
			margin-bottom: $spacer-grid * 2;
		}

		> *:nth-child(2) {
			margin-left: $spacer-grid;
			margin-bottom: $spacer-grid * 2;
		}
	}

	.content-tiletrio-east {
		> *:first-child {
			margin-right: $spacer-grid;
			margin-bottom: $spacer-grid * 2;
		}

		> *:nth-child(2) {
			margin-right: $spacer-grid;
		}

		> *:last-child {
			margin-left: $spacer-grid;
		}
	}

	.content-tiletrio-west {
		> *:first-child {
			margin-right: $spacer-grid;
		}

		> *:nth-child(2) {
			margin-left: $spacer-grid;
			margin-bottom: $spacer-grid * 2;
		}

		> *:last-child {
			margin-left: $spacer-grid;
		}
	}	
}
