.jumbotron {
	position: relative;
	background-color: $jumbotron-bg;
	overflow: hidden;
	backface-visibility: hidden; // protects against rendering issues when other animations on page

	> img,
	picture img {
		width: 100%;
		margin-bottom: 0;
	}

	// overlay classes - limiting to jumbotron
	&[class*='overlay-']::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 2;
	}

	&.overlay-15::after {
		background-color: rgba($black, 0.15);
	}

	&.overlay-30::after {
		background-color: rgba($black, 0.3);
	}

	&.overlay-50::after {
		background-color: rgba($black, 0.5);
	}
}

.jumbotron-content {
	position: absolute;
	padding: $spacer $spacer-grid 0;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 3;

	@include media-breakpoint-up(sm) {
		width: 80%;
	}

	@include media-breakpoint-up(md) {
		width: 60%;
	}

	@include media-breakpoint-up(lg) {
		width: 50%;
	}

	@include media-breakpoint-custom($min:1280px) {
		width: 40%;
	}

	@include media-breakpoint-custom($min:1800px) {
		width: 30%;
	}

	// placement classes - limiting to jumbotron-content
	&[class*='middle-'] {
		top: 50%;
		transform: translateY(-50%);
	}

	&[class*='bottom-'] {
		top: auto;
		bottom: 0;
	}

	&[class*='-center'] {
		text-align: center;
		left: 50%;
		transform: translateX(-50%);
	}

	&[class*='-right'] {
		left: auto;
		right: 0;
		text-align: right;
		align-items: flex-end;
	}

	&.middle-center {
		transform: translate(-50%, -50%);
	}
}

.jumbotron-inverse {
	@include inverse-content;
	background-color: $jumbotron-bg-inverse;	
}

.jumbotron-hr {
	border-top-color: darken($jumbotron-bg, 10%);
}

