// Box Shadow

@mixin box-shadow-default($horizontal: 0, $vertical: 2px, $blur: 4px, $color: rgba($black, 0.15)) {
	box-shadow: $horizontal $vertical $blur $color;
}

@mixin inset-box-shadow-default($horizontal: 0, $vertical: 2px, $blur: 4px, $color: rgba($black, 0.15)) {
	box-shadow: inset $horizontal $vertical $blur $color;
}

