// Divided grid extends some grid classes to add vertical lines within the gutter. It must be added to the row
// element to work. It does not get inherited into child grids.
$divided-columns: (2, 3, 4);

.row.divided-grid {

	@each $breakpoint in map-keys($grid-breakpoints) {
		> .col-#{$breakpoint}-12::after,
		> .col-#{$breakpoint}-10::after,
		> .col-#{$breakpoint}-8::after {
			content: '';
			display: block;
			border-top: 1px solid $hr-border-color;
			margin: 2rem 0; // utilizing margin-collpase against preceeding element

			@include media-breakpoint-up(lg) {
				margin: 3rem 0;
			}
		}

		> .col-#{$breakpoint}-12:last-child::after,
		> .col-#{$breakpoint}-10:last-child::after,
		> .col-#{$breakpoint}-8:last-child::after {
			display: none;
		}

		@each $column in $divided-columns {
			$col-class: $grid-columns / $column;

			> .col-#{$breakpoint}-#{$col-class}::before {
				content: '';
				display: none;
				position: absolute;
				border-left: 1px solid $hr-border-color;
				top: 2.25rem;
				bottom: 3.75rem;
				left: 0;
			}
		}
	}

	@each $breakpoint in map-keys($grid-breakpoints) {
		@include media-breakpoint-up($breakpoint) {
			@each $column in $divided-columns {
				$col-class: $grid-columns / $column;

				> .col-#{$breakpoint}-#{$col-class}::after {
					display: none;
				}

				> .col-#{$breakpoint}-#{$col-class}:not(:nth-child(#{$column}n+1))::before {
					display: block;
				}

				> .col-#{$breakpoint}-#{$col-class}:nth-child(#{$column}n+1)::before {
					display: none;
				}
			}
		}
	}
}
