// The dropdown wrapper (`<div>`)
.dropdown {
	position: relative;
}

.dropdown-toggle {
	// Generate the caret automatically
	&::after {
		display: inline-block;
		vertical-align: middle;
		font-family: 'mylt-icons';
		content: '\e622';
		font-size: 1.25rem;
		line-height: 0;
		font-weight: normal;
		width: 0.7em; // cuts off .25em on right
	}
}

// The dropdown menu
.dropdown-arrow,
.dropdown-menu {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	display: none; // none by default, but block on "open" of the menu
}

.dropdown-arrow {
	bottom: 0;
	width: 0;
	height: 0;
	border-color: transparent;
	border-style: solid;
	border-width: 0 $dropdown-arrow-width $dropdown-arrow-width;
	border-bottom-color: $dropdown-bg;
	z-index: $zindex-dropdown + 1;
}

.dropdown-menu {
	top: 100%;
	padding: $dropdown-spacer-y $dropdown-spacer-x;
	margin: 0;
	z-index: $zindex-dropdown;
	min-width: 12rem;
	color: $body-color;
	text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)
	list-style: none;
	background-color: $dropdown-bg;
	background-clip: padding-box;
	@include box-shadow($dropdown-box-shadow);
}

// Menu positioning
.dropdown-menu-right { // right align to trigger
	transform: none;
	left: auto;
	right: 0;
}

.dropdown-menu-left { // left align to trigger
	transform: none;
	left: 0;
}

// Open state for the dropdown
.open {
	.dropdown-toggle::after {
		content: '\e621';
	}
	// Show the menu
	> .dropdown-arrow,
	> .dropdown-menu {
		display: block;
	}
}

// Dividers (basically an `<hr>`) within the dropdown
.dropdown-divider { 
	@include nav-divider($dropdown-divider-bg);
}

// Links, buttons, and more within the dropdown menu
//
// `<button>`-specific styles are denoted with `// For <button>s`
.dropdown-item {
	display: block;
	font-weight: normal;
	line-height: $line-height;
	white-space: nowrap;
	padding: 0.25rem 0;
	color: $dropdown-link-color;

	@include hover-focus {
		color: $dropdown-link-hover-color;
	}
}

.dropdown-columns { // two column creator for long lists in desktop views
	width: 20rem - ($dropdown-spacer-x * 2);
	
	.dropdown-item {
		white-space: normal;
	}

	@include media-breakpoint-up(lg) {
		width: $dropdown-two-col-width;
		column-count: 2;
		column-gap: $dropdown-two-col-gap;

		li {
			break-inside: avoid;
		}
	}
}

.dropdown-inverse {
	.dropdown-arrow {
		border-bottom-color: $dropdown-inverse-bg;
	}

	.dropdown-menu {
		color: $white;
		background-color: $dropdown-inverse-bg;
	}

	.dropdown-item {
		@include inverse-links-white;
	}
}

.dropdown-header {
	font-weight: bold;
	padding: 0.25rem 0;
}
