//
// Base Theme Buttons
// scss-lint:disable QualifyingElement

.btn {
	display: inline-block;
	position: relative;
	font-weight: $btn-font-weight;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	cursor: pointer;
	user-select: none;
	color: $link-color;
	background-color: $gray-12;
	border: ($input-btn-border-width * 2) solid transparent;
	@include button-size($btn-padding-y, $btn-padding-x, $font-size-base, $line-height, $btn-border-radius);
	@include transition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);
	letter-spacing: normal; // Override letter-spacing used to space out buttons in .btn-wrap and .modal-footer (which was inherited in Firefox)

	&:focus {
		outline: 0;
		box-shadow: $btn-box-shadow-focus;
	}

	@include hover-focus {
		border-color: $link-color;
		color: $link-hover-color;
	}

	&,
	&:active {
		&:focus {
			outline: 0;
		}
	}

	&:active {
		top: 1px;
		outline: 0;
	}

	&.disabled,
	&:disabled {
		cursor: $cursor-disabled;
		opacity: 0.65;
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.btn.disabled,
fieldset[disabled] a.btn {
	pointer-events: none;
}


// BUTTON VARIATIONS

.btn-reversed {
	background-color: $white;

	@include hover-focus {
		background-color: $gray-11;
		border-color: transparent;
	}

}

// solid buttons

.btn-primary {
	@include button-solid-variant($btn-primary-color, $btn-primary-bg);
}

.btn-secondary {
	@include button-solid-variant($btn-secondary-color, $btn-secondary-bg);
}

.btn-info {
	@include button-solid-variant($btn-info-color, $btn-info-bg);
}

// Outline buttons - remove all backgrounds
.btn-primary-outline {
	// see _content-sections.scss for inverse version
	@include button-transparent-outline-variant($btn-primary-outline, $btn-primary-outline);
}

.btn-secondary-outline {
	@include button-transparent-outline-variant($btn-secondary-outline, $btn-secondary-outline);
}

.btn-reversed-outline {
	color: $white;
	border-color: $white;
	background-color: transparent;

	@include hover-focus {
		color: $white;
		border-color: $white;
		background-color: rgba($gray-1, 0.5);
	}

	&:active {
		background-color: $gray-1;
	}
}

.btn-info-outline {
	@include button-transparent-outline-variant($btn-info-bg, $btn-info-bg);
}

// form/control buttons
.btn-series {
	@include button-variant($btn-series-color, $btn-series-bg, $btn-series-border);
}

.btn-input,
.btn-input-wide {
	@include button-transparent-outline-variant($link-color, $input-border-color);
	background-color: $white;
	border-width: 1px;
	padding-top: $input-padding-y;
	padding-bottom: $input-padding-y;

	@include hover-focus {
		color: $link-hover-color;
		background-color: $gray-12;
		border-color: $input-border-color;
	}

	&:active {
		color: $link-active-color;
		background-color: $gray-11;
		border-color: $input-border-color;
	}

	&.disabled,
	&:disabled,
	fieldset[disabled] & {
		opacity: 0.5;
		color: $gray-4;
		background-color: $gray-11;
		border-color: $gray-5;
	}
}

.btn-input {
	padding: $input-padding-y $input-padding-x;
}

// Link buttons
.btn-link {
	font-weight: normal;
	border-radius: 0;

	&:focus {
		box-shadow: 0 0 4px 1px $focus-shadow-color;
	}

	&,
	&:active,
	&:disabled {
		background-color: transparent;
	}

	&,
	&:focus,
	&:active {
		border-color: transparent;
	}

	@include hover {
		border-color: transparent;
	}

	@include hover-focus {
		text-decoration: $link-hover-decoration;
		background-color: transparent;
	}

	&:disabled,
	&.disabled {
		color: $btn-link-disabled-color;

		@include hover-focus {
			color: $btn-link-disabled-color;
			text-decoration: none;
		}
	}

	// Options to remove left and right padding to appear to align buttons
	// since this style has a transparent background

	&.btn-flush-l {
		margin-left: -$btn-padding-x;
	}

	&.btn-flush-r {
		margin-right: -$btn-padding-x;
	}

	// Use along with .btn-link; without padding and the hop on :active
	&.btn-link-simple {
		border: 0;
		padding: 0;

		&:active {
			top: 0;
		}
	}
}

// BUTTON UTILITIES

// Button Sizes

.btn-lg {
	// line-height: ensure even-numbered height of button next to large input
	@include button-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-lg, $line-height-lg, $btn-border-radius-lg);
}

.btn-sm {
	// line-height: ensure proper height of button next to small input
	@include button-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $line-height-sm, $btn-border-radius-sm);
}

.btn-xs {
	// line-height: ensure proper height of button next to small input
	@include button-size($btn-padding-y-xs, $btn-padding-x-xs, $font-size-sm, $line-height-sm, $btn-border-radius-sm);
}



// Block button

.btn-block,
.btn-block-edge {
	display: block;
	width: 100%;

	& + & {
		margin-top: 5px;
	}

	// Buttons that are block-level on the xs and sm breakpoints only
	@include media-breakpoint-down(sm) {
		&-xs,
		&-sm {
			display: block;
			width: 100%;

			& + & {
				margin-top: 5px;
			}
		}
	}
}

// Edge button
.btn-block-edge {
	border-left: 0;
	border-right: 0;
	border-radius: 0;
	// Pull the btn left/right to go edge to edge
	margin-left: -$spacer-grid;
	margin-right: -$spacer-grid;
	width: calc(100% + 2rem); // Needed for <button> element to go full-width

	// Buttons that are edge to edge on the xs and sm breakpoints only
	@include media-breakpoint-down(sm) {
		&-xs,
		&-sm {
			border-left: 0;
			border-right: 0;
			border-radius: 0;
			// Pull the btn left/right to go edge to edge
			margin-left: -$spacer-grid;
			margin-right: -$spacer-grid;
			width: calc(100% + 2rem); // Needed for <button> element to go full-width
		}
	}
}

// Video player button
.btn-video {
	position: relative;
	width: 4.5rem;
	height: 4.5rem;
	border: 0;
	border-radius: 50%;
	background-color: $dark-trans;
	text-indent: -9999px;

	&:hover {
		background-color: $btn-video-hover-color;
	}

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: 0.25rem;
		border-style: solid;
		border-color: transparent $white transparent $white;
		border-width: 0.875rem 0 0.875rem 1.75rem;
		transform: translate(-50%,-50%);
	}

	@include media-breakpoint-up(md) {
		width: 6rem;
		height: 6rem;
	}

	&.btn-sm {
		width: 3.75rem;
		height: 3.75rem;
	}

	&.btn-xs {
		width: 2.75rem;
		height: 2.75rem;

		&::before {
			margin-left: 0.14857rem;
			border-width: 0.5rem 0 0.5rem 1rem;
		}
	}

	&.btn-video-pause {
		&::before {
			height: 1.75rem;
			width: 1.5rem;
			margin-left: 0;
			border-width: 0 0.5rem 0 0.5rem;
		}

		&.btn-xs::before {
			height: 1rem;
			width: 0.75rem;
			border-width: 0 0.25rem 0 0.25rem;
		}
	}
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {

	&.btn-block {
		width: 100%;
	}

	// This was originally called .btn-block-xs due to BS3 breakpoints, but we renamed it to align with the fact that it actually applies to both XS and SM in BS4 (keeping ".btn-block-xs" for backward compatibility)
	@include media-breakpoint-down(sm) {
		&.btn-block-xs,
		&.btn-block-sm {
			width: 100%;
		}
	}
}

// Icons in Buttons
.btn {

	// Add padding to the right of the text if it comes before the icon
	.btn-icon-text:first-child {
		padding-right: 8px;
	}

	// Add padding to the left of the text if it comes after the icon
	.btn-icon-text:last-child {
		padding-left: 8px;
	}
}

// fixes 1px height difference with icon
.btn-sm {
	[class^='ico-'],
	[class*=' ico-'] {
		line-height: 12px;
	}
}

// Button Wrapper
// adds margin-bottom, spaces buttons out beyond regular character spacing
// using margin-top on btn to not interfere with margin-bottom
.btn-wrap {
	margin-top: -1rem;
	letter-spacing: 0.5rem; // adds up to 12px on most fonts
	padding-top: 0.1px;

	.btn {
		margin-top: 1rem;
	}
}
