//
// Base Theme Tables

.table {
	width: 100%;
	max-width: 100%;

	th,
	td {
		padding: $table-cell-padding;
		line-height: $line-height;
		vertical-align: top;
		border-top: $table-border-width solid $table-border-color;
	}

	th {
		font-weight: 600;
	}

	thead th {
		@include table-header();
		vertical-align: bottom;
		padding-bottom: 0.5rem;
		padding-top: 0.5rem;
	}

	tbody + tbody {
		border-top: (2 * $table-border-width) solid $table-border-color;
	}

	.table {
		background-color: $body-bg;
	}

	.thead-inverse th {
		border-top: 0;
		color: $white;
	}
}

.table-simple {
	td {
		padding-right: 1rem;
	}
}

caption {
	caption-side: top;
	color: $table-caption-color;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
}

// Table size alterations
.table-xs {
	th,
	td {
		padding: $table-xs-cell-padding;
		font-size: 0.8125rem;
	}
}

.table-sm {
	th,
	td {
		padding: $table-sm-cell-padding;
		font-size: 0.875rem;
	}
}

.table-lg {
	thead th {
		padding-left: 0.45rem;
		padding-right: 0.45rem;
	}

	tbody th,
	td {
		font-size: 1.125rem;
		padding: 0.45rem;
	}
}

// table border variations

.table-bordered {
	border: $table-border-width solid $table-border-color;

	th,
	td {
		border: $table-border-width solid $table-border-color;
	}
}

// table background variations

// zebra-striped rows

.table-striped {
	thead th {
		background-color: $white;
		border-bottom: 1px solid $table-border-color;
	}

	tbody tr:nth-of-type(even) {
		background-color: $white;
	}

	tbody tr:nth-of-type(odd) {
		background-color: $table-bg-accent;
	}

	tbody th,
	td {
		border-top: 0;
	}
}

// Hover effect - Placed here since it has to come after the potential zebra striping

.table-hover {
	tbody tr {
		@include hover {
			background-color: $table-bg-hover;
		}
	}
}

// Table backgrounds
//
// Exact selectors below required to override `.table-striped` and prevent
// inheritance to nested tables.

// Generate the contextual variants
@include table-row-variant(active, $table-bg-active);
@include table-row-variant(success, $state-success-bg);
@include table-row-variant(info, $state-info-bg);
@include table-row-variant(warning, $state-warning-bg);
@include table-row-variant(danger, $state-danger-bg);

.table-inverse {
	color: $gray-lighter;
	background-color: $gray-dark;

	&.table-bordered {
		border: 0;
	}

	th,
	td,
	thead th {
		border-color: $gray;
	}

	thead th {
		border-top: 0;
		color: $white;
	}
}

.thead-inverse {
	th {
		color: $white;
		background-color: $gray-dark;
	}
}

.thead-default {
	th {
		color: $gray;
		background-color: $gray-lighter;
	}
}

// Responsive tables
//
// Wrap your tables in `.table-responsive` and we'll make them mobile friendly
// by enabling horizontal scrolling. Only applies <768px. Everything above that
// will display normally.

.table-responsive {
	display: block;
	width: 100%;
	min-height: 0.01%; // Workaround for IE9 bug (see https://github.com/twbs/bootstrap/issues/14837)
	overflow-x: auto;

	// TODO: find out if we need this still.
	//
	// border: $table-border-width solid $table-border-color;
	// -ms-overflow-style: -ms-autohiding-scrollbar; // See https://github.com/twbs/bootstrap/pull/10057
}

// Table reflow
.table-reflow {
	// scss-lint:disable NestingDepth
	thead {
		float: left;
	}

	tbody {
		display: block;
		white-space: nowrap;
	}

	th,
	td {
		border-top: $table-border-width solid $table-border-color;
		border-left: $table-border-width solid $table-border-color;

		&:last-child {
			border-right: $table-border-width solid $table-border-color;
		}
	}

	thead,
	tbody,
	tfoot {
		&:last-child {
			tr:last-child {
				th,
				td {
					border-bottom: $table-border-width solid $table-border-color;
				}
			}
		}
	}

	tr {
		float: left;

		th,
		td {
			// scss-lint:disable ImportantRule
			display: block !important;
			border: $table-border-width solid $table-border-color;
		}
	}
}

// Table with sticky headers (in thead only)
// Does not work for tables that use the responsive overflow pattern
.table-sticky-header {

	thead tr:nth-child(1) th {
    position: sticky;
    top: 0;
    z-index: 10;
  }
}
