// Base styles
// scss-lint:disable QualifyingElement, PlaceholderInExtend
//

.input-group {
	position: relative;
	display: flex;

	.form-control {
		// Ensure that the input is always above the *appended* addon button for
		// proper border colors.
		flex: 1;
		position: relative;
		z-index: 2;
		margin-bottom: 0;

		&:focus {
			z-index: 3;
		}
	}
}

.input-group-addon,
.input-group-btn,
.input-group .form-control {

	&:not(:first-child):not(:last-child) {
		@include border-radius(0);
	}
}

.input-group-addon,
.input-group-btn {
	white-space: nowrap;
	vertical-align: middle; // Match the inputs
}


// Sizing options
//
// Remix the default form control sizing classes into new ones for easier
// manipulation.

.input-group-lg > .form-control,
.input-group-lg > .input-group-addon,
.input-group-lg > .input-group-btn > .btn {
	@extend .form-control-lg;
}

.input-group-sm > .form-control,
.input-group-sm > .input-group-addon,
.input-group-sm > .input-group-btn > .btn {
	@extend .form-control-sm;
}


// Text input groups

.input-group-addon {
	padding: $input-padding-y $input-padding-x;
	font-size: $font-size-base;
	font-weight: normal;
	line-height: $line-height;
	color: $input-color;
	text-align: center;
	background-color: $input-group-addon-bg;
	border: $input-btn-border-width solid $input-group-addon-border-color;
	@include border-radius($border-radius);

	// Sizing
	&.form-control-sm {
		padding: $input-padding-y-sm $input-padding-x-sm;
		font-size: $font-size-sm;
		@include border-radius($border-radius-sm);
	}

	&.form-control-lg {
		padding: $input-padding-y-lg $input-padding-x-lg;
		font-size: $font-size-lg;
		@include border-radius($border-radius-lg);
	}

	// Nuke default margins from checkboxes and radios to vertically center within.
	input[type="radio"],
	input[type="checkbox"] {
		margin-top: 0;
	}
}


// Reset rounded corners

.input-group .form-control:first-child,
.input-group-addon:first-child,
.input-group-btn:first-child > .btn,
.input-group-btn:first-child > .btn-group > .btn,
.input-group-btn:first-child > .dropdown-toggle,
.input-group-btn:last-child > .btn:not(:last-child):not(.dropdown-toggle),
.input-group-btn:last-child > .btn-group:not(:last-child) > .btn {
	@include border-right-radius(0);
}

.input-group-addon:first-child {
	border-right: 0;
}

.input-group .form-control:last-child,
.input-group-addon:last-child,
.input-group-btn:last-child > .btn,
.input-group-btn:last-child > .btn-group > .btn,
.input-group-btn:last-child > .dropdown-toggle,
.input-group-btn:first-child > .btn:not(:first-child),
.input-group-btn:first-child > .btn-group:not(:first-child) > .btn {
	@include border-left-radius(0);
}

.input-group-addon:last-child {
	border-left: 0;
}

// Button input groups

.input-group-btn {
	position: relative;
	// Jankily prevent input button groups from wrapping with `white-space` and
	// `font-size` in combination with `inline-block` on buttons.
	font-size: 0;
	white-space: nowrap;

	// Negative margin for spacing, position for bringing hovered/focused/actived
	// element above the siblings.
	> .btn {
		position: relative;

		+ .btn {
			margin-left: (-$input-btn-border-width);
		}
		// Bring the "active" button to the front when using input-btn
		.btn-input,
		.btn-input-wide {
			@include hover-focus-active {
				z-index: 3;
			}
		}
	}

	// Negative margin to only have a single, shared border between the two
	&:first-child {
		> .btn,
		> .btn-group {
			margin-right: (-$input-btn-border-width);
		}
	}

	&:last-child {
		> .btn,
		> .btn-group {
			z-index: 2;
			margin-left: (-$input-btn-border-width);
			// Because specificity
			@include hover-focus-active {
				z-index: 3;
			}
		}
	}
}

// Counter
// Custom add-on leveraging input-group styles
.input-counter {
	.form-control {
		text-align: center;
		max-width: 3.875rem;
	}

	&.input-counter-center {
		justify-content: center;
	}

	&.input-counter-lg {
		align-items: center;

		.input-group-btn > .btn {
			margin: 0;
		}

		.form-control {
			font-size: 3rem;
			line-height: 1;
			font-weight: 500;
			margin: 0 1rem;
			padding: 0 0 0.125rem 0;
			border-color: transparent;
		}
	}
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.input-counter .form-control {
		max-width: none;
		flex: 0 0 3.875rem;
	}

	.input-counter.input-counter-lg .form-control {
		height: 3.125rem;
	}
}

.input-counter-focus-help {
	visibility: hidden;

	&.in {
		visibility: visible;
	}
}