//
// Base Theme Alerts & Notifications
//
// These replace the Bootstrap defaults (which are commented out in theme-bame.scss)
// to make .alert more extendable

.alert {
	margin-bottom: $spacer;
	position: relative;
	overflow: hidden;
	border-width: 4px 0 0;
	border-style: solid;
	font-size: 0.875rem;
	padding-top: 0.375rem;

	// Improve alignment and spacing of inner content
	p,
	ul {
		margin-bottom: 0;
	}

	p + p {
		margin-top: 0.5rem;
	}

	&.alert-notification {
		font-size: 1rem;
		border-width: $border-width 0;
		padding-top: $spacer-y;
		padding-bottom: $spacer-y;
	}
}


// Headings for larger alerts
.alert-heading {
	// Specified to prevent conflicts of changing $headings-color
	color: inherit;
}

// Provide class for links that match alerts
.alert-link {
	font-weight: $alert-link-font-weight;
}

// Icons
.alert-icon {
	&:not(.alert-notification),
	&.alert-notification .container {
		&::before {
			display: block;
			font-family: $font-family-icons;
			font-size: 1.25rem;
			position: absolute;
		}
	}

	&:not(.alert-notification) {
		padding-left: 1.625rem;

		&::before {
			top: 2px;
			left: 0;
		}
	}

	&.alert-notification {
		.container {
			padding-left: 2rem + $spacer-grid;
			position: relative;

			&::before {
				top: -3px;
				left: $spacer-grid;
			}
		}
	}
}


// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissible {
	padding-right: ($alert-padding + 10);

	&.alert-notification {
		padding-right: 0;

		.container {
			padding-right: ($alert-padding + 25);
		}
	}

	// Adjust close link position
	.close {
		position: relative;
		top: -4px;
		right: -27px;
	}
}


// Generate contextual modifier classes for colorizing the alert.
// Characters can be passed in (optional) to declare what icon
// should be used from the icon font.

.alert-success {
	@include alert-alternate-variant($alert-success-border, '\e906');
}

.alert-info {
	@include alert-alternate-variant($alert-info-border, '\e907');
}

.alert-warning {
	@include alert-alternate-variant($alert-warning-border, '\e907');
}

.alert-danger {
	@include alert-alternate-variant($alert-danger-border, '\e907');
}

