.navbar {
	display: flex;
	align-items: center;
	padding: 0 $navbar-padding-x;
	font-size: 0.8125rem;
	background-color: $white;
	border-bottom: 1px solid $gray-11;

	@include media-breakpoint-up(sm) {
		font-size: 0.875rem;
	}
	// add active states to nav when within navbar
	.nav {
		flex-wrap: nowrap;
	}

	.nav-link {
		white-space: nowrap;
		border-bottom: $nav-tabs-border-width solid transparent;
		padding-top: $navbar-padding-y;
		padding-bottom: calc(#{$navbar-padding-y} - #{$nav-tabs-border-width});

		&.active {
			border-bottom-color: $navbar-accent-color;
		}
	}

	.open .nav-link.active { // remove when a .dropdown is open
		border-bottom-color: transparent;
	}
}

// Brand/project name
.navbar-brand {
	flex: 0 0 auto;
	font-weight: bold;
	padding-right: $navbar-padding-x;
	white-space: nowrap;

	img {
		display: block;
	}

	@include media-breakpoint-up(lg) {
		font-size: 1.25rem;
	}
}

// Navbar button
.navbar .btn {
	font-size: 0.875rem;
	padding: 0.5rem 1rem;
	line-height: 1.5; // 20px
	margin-right: $navbar-padding-x;

	&.dropdown-toggle {
		padding-right: 0.5rem;
	}
}

// Navbar collapse
// used to remove navbar flex styles if the navigation collapses (i.e. in the header)
// TO DO: this hasn't been thoughtfully worked out to build other navs, only to reset
// items within the .header element; if building on later, double-check for header issues
.navbar-collapse {
	@include media-breakpoint-down(md) {
		display: block;
		font-size: 1rem;
		padding: 0;

		.nav-link {
			border-bottom: 0;
		}
	}
}

// Navbar themes
.navbar-light {
	background-color:  $gray-11;
	border-bottom: 0;
}
//.navbar-dark {}

// Navbar toggle
// (keeping Bootstrap classes, but used to interact with custom data-toggle="headernav")
.navbar-toggler {
	display: inline-block;
	vertical-align: middle;
	padding: 0.5rem $spacer-grid;
	background: transparent;
	border: 0;
	line-height: 1;
}

.navbar-toggler-icon {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 16px;
	padding-top: 7px;
	padding-bottom: 8px;
	background-color: $body-color;
	background-clip: content-box;
	transition: background-color 0.2s linear;

	&::before,
	&::after {
		content: '';
		position: absolute;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $body-color;
		transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1);
	}

	&::before {
		top: 0;
	}

	&::after {
		bottom: 1px;
	}

	.navbar-toggler-open & {
		background-color: transparent;

		&::before {
			top: 7px;
			transform: rotate(-45deg);
		}

		&::after {
			bottom: 8px;
			transform: rotate(45deg);
		}
	}
}

[data-navbar-overflow] {
	&:not(.navbar-overflow) {
		overflow: hidden; // remove scrollbars until initalized
	}	
	animation-duration: 0.001s;
	animation-name: navbarInserted; // detect if an overflow has been inserted via javascript animation detection
}

@keyframes navbarInserted {  
	from { opacity: 0.99; }
	to { opacity: 1; }  
}
