// myLT Theme Footer

.footer-mylt {
	@include transition(all 0.2s linear);
	background: $gray-3;
	padding: 2em 0 6em;

	ul {
		margin: 0;
		padding: 0;

		li {
			display: inline-block;

			&::before {
				content: none;
			}
		}
	}
}


/*----------------------------------------
Social Links */

.footer-social {

	@include media-breakpoint-up(md) {
		float: right;
		margin-left: 1.5em;
		text-align: right;

		li {
			margin-left: 4px;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	ul {
		font-size: 1em;
		margin-bottom: 1.5em;
		text-align: center;
	}

	li {
		margin: 0 3px;
	}

	a {
		border: 1px solid $white;
		border-radius: 50%;
		color: $white;
		display: block;
		height: 34px;
		line-height: 30px;
		text-align: center;
		width: 34px;

		&:visited {
			color: $white;
		}

		&:focus,
		&:hover {
			background: $white;
			color: $gray-2;
		}

		&:active {
			background: $gray-8;
			border-color: $gray-8;
			color: $gray-2;
		}
	}
}


/*----------------------------------------
Footer Navigation */

.footer-navigation {

	ul {
		font-size: 0.688em;
		line-height: 2.5em;
		margin-bottom: 1.5em;
		text-align: center;

		@include media-breakpoint-up(md) {
			line-height: 1.75em;
			margin-bottom: 0.25em;
			text-align: left;
		}
	}

	li {
		margin: 0 5px;

		@include media-breakpoint-up(md) {
			margin: 0 8px 0 0;
		}

		a {
			color: $white;
			font-weight: bold;
			letter-spacing: 0.05em;
			text-transform: uppercase;

			&:visited {
				color: $white;
			}

			&:focus {
				color: $link-rev-hover;
			}

			&:hover {
				color: $link-rev-hover;
			}

			&:active {
				color: $link-rev-active;
			}
		}
	}
}


/*----------------------------------------
Legal Navigation */

.footer-legal {

	ul {
		font-size: 0.75em;
		line-height: 1.625em;
		text-align: center;

		@include media-breakpoint-up(md) {
			text-align: left;
		}
	}

	li {

		&:not(:last-child) {
			margin-right: 0.375em;
		}

		a {
			color: $gray-8;

			&:visited {
				color: $gray-8;
			}

			&:focus {
				color: $white;
			}

			&:hover {
				color: $white;
			}

			&:active {
				color: $gray-10;
			}
		}
	}
}


/*----------------------------------------
Copyright & Legal */

.copyright {
	color: $gray-8;
	font-size: 0.75em;
	line-height: 1.625em;
	text-align: center;
	margin-bottom: 0;

	@include media-breakpoint-up(md) {
		text-align: left;
	}

	span {
		display: block;

		@include media-breakpoint-up(sm) {
			display: inline;
			white-space: nowrap;
		}

		@include media-breakpoint-up(md) {
			display: inline;
		}
	}
}


/*----------------------------------------
Off-Canvas */

.offcanvas {

	// Shift the footer to the left when the off-canvas is toggled
	.footer-mylt {
		transform: translateX(-100px);
	}
}
