// Date Nav
// Date nav bar (including datepicker trigger button)
// scss-lint:disable QualifyingElement

.date-nav {
	display: flex;

	.btn {
		flex: 0 0 auto;

		&:not(:last-of-type) {
			@include border-right-radius(0);
		}

		&:not(:first-of-type) {
			@include border-left-radius(0);
			margin-left: (-$input-btn-border-width);
		}

		&:focus,
		&.focus {
			z-index: 3;
		}
	}

	.datepicker-trigger {
		flex: 1 1 auto;
		overflow: hidden;

		.btn-icon-text {
			font-weight: normal;
			color: $gray-4;
		}

		@include hover-focus {
			.btn-icon-text {
				color: $gray-3;
			}
		}

		&:active {
			.btn-icon-text {
				color: $gray-2;
			}
		}

		// In Filter Mode, text is left-aligned and the icon is on the right
		.flex-container {
			display: flex;
			align-items: center;
			justify-content: space-between;

		}
	}

	// Header Mode
	&.date-nav-header {
		.datepicker-trigger .flex-container {
			justify-content: center;
		}

		.btn-icon-text {
			font-size: 0.875rem; // 14px
			flex: 0 0 auto;
		}
	}
}



// Custom Datepicker Styles
// Adapted from the CSS included with Datepicker for Bootstrap v1.5.0 (https://github.com/eternicode/bootstrap-datepicker)

.datepicker {
	border-radius: 4px;

	&.dropdown-menu {
		background-color: $white; // Override regular .dropdown-menu
		color: $gray-darker;
		font-size: 0.813rem; // 13px
		line-height: 1.53846154; // 20px
		z-index: $zindex-tooltip-modal;
		transform: none; // Override regular .dropdown-menu
		border: 1px solid $gray-8;
		margin: 2px 0 0;
	}

	&.dropdown-menu th,
	&.datepicker-inline th,
	&.dropdown-menu td,
	&.datepicker-inline td {
		padding: 0 5px;
	}

	table {
		border-collapse: separate; // Override shared for rounded borders to work
	}

	&-inline {
		width: 220px;
	}

	&-dropdown {
		top: 0;
		left: 0;
		padding: 4px;

		// Dropdown pointer/arrow
		&::before,
		&::after {
			content: '';
			display: inline-block;
			border-top: 0;
			position: absolute;
		}

		&::before {
			border-left: 7px solid transparent;
			border-right: 7px solid transparent;
			border-bottom: 7px solid $gray-8;
		}

		&::after {
			border-left: 6px solid transparent;
			border-right: 6px solid transparent;
			border-bottom: 6px solid $white;
		}

		// scss-lint:disable SingleLinePerProperty
		// scss-lint:disable EmptyLineBetweenBlocks
		&.datepicker-orient-left::before { left: 6px; }
		&.datepicker-orient-left::after { left: 7px; }
		&.datepicker-orient-right::before { right: 6px; }
		&.datepicker-orient-right::after { right: 7px; }
		&.datepicker-orient-bottom::before { top: -7px; }
		&.datepicker-orient-bottom::after { top: -5.5858px; }
		// scss-lint:enable SingleLinePerProperty
		// scss-lint:enable EmptyLineBetweenBlocks

		&.datepicker-orient-top::before {
			bottom: -7px;
			border-bottom: 0;
			border-top: 7px solid $gray-8;
		}

		&.datepicker-orient-top::after {
			bottom: -5.5858px;
			border-bottom: 0;
			border-top: 6px solid $white;
		}
	}

	> div {
		display: none;
	}

	table {
		margin: 0;
		-webkit-touch-callout: none;
		user-select: none;
	}

	th,
	td {
		text-align: center;
		width: 30px;
		height: 30px;
		border-radius: 4px;
		border: 0;

		// Disabled states (all)
		&.disabled,
		&.disabled:hover {
			// scss-lint:disable ImportantRule
			background: none !important;
			color: $gray-7 !important;
			cursor: default !important;
			// scss-lint:enable ImportantRule
		}
	}

	// Month/year heading (.datepicker-switch is the month/year heading that "zooms out" a level when clicked) and arrows, and day-of-week headings
	th {
		color: $gray-3;
		font-weight: 500;
		letter-spacing: 0.2px;

		&.datepicker-switch,
		&.prev,
		&.next {
			cursor: pointer;

			&:hover {
				background: $gray-11;
				color: $aqua-dark;
			}

			&:active {
				background: $gray-10;
				color: $aqua-darker;
			}
		}

		&.datepicker-switch {
			width: 145px;
		}

		// Arrows
		&.prev,
		&.next {
			color: $aqua;
			font-size: 0; // Hide chevron characters

			&::before { // Adapted from _icons.scss
				font-family: 'mylt-icons';
				font-size: 1.25rem;
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				position: relative;
				vertical-align: middle;
				transform: translateY(-0.05em);

				// Better Font Rendering ===========
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}

		&.prev::before {
			content: '\e61f'; // .ico-arrow-left-sm
		}

		&.next::before {
			content: '\e620'; // .ico-arrow-right-sm
		}
	}


	// Dates (and months/years in body of "zoomed out" view)
	td {
		// Date/month/year outside the current range
		&.old,
		&.new,
		span.old,
		span.new {
			color: $gray-7;
		}

		// Hover/focus
		&.day:hover,
		&.focused,
		span.day:hover,
		span.focused {
			background-color: $gray-11;
			color: $aqua-dark;
			cursor: pointer;
		}

		// Active (pressed)
		&.day:active,
		span:active {
			background: $gray-10;
			color: $aqua-darker;
		}

		// Today
		&.today {
			border: 1px solid rgba($black, 0.09);
			box-shadow: inset 0 0 0 1px rgba($white, 0.3);
			font-weight: bold;
		}

		// Selected date or month/year (.active *class*)
		&.active,
		span.active {
			color: $white;
			background-color: $aqua;
		}

		&.active:focus,
		&.active.focus,
		span.active:focus,
		span.active.focus {
			color: $white;
			background-color: $aqua-dark;
		}

		&.active:hover,
		&.active:active,
		&.active.active,
		span.active:hover,
		span.active:active,
		span.active.active {
			color: $white;
			background-color: $aqua-darker;
		}

		// Months and years in body of "zoomed out" view
		span {
			display: block;
			width: 23%;
			height: 54px;
			line-height: 54px;
			float: left;
			margin: 1%;
			cursor: pointer;
			border-radius: 4px;
		}
	}
}
