// Feature Cards
// These are the AEM card component, also used in its card decks and groups

.card-feature {
	background-color: transparent;
	text-align: center;
	border: 0;

	.card-img-top {
		margin-bottom: -1px;
	}

	.card-block {
		border: 1px solid $card-border-color;
		background-color: $white;

		@include media-breakpoint-up(md) {
			padding-top: 2rem;
			padding-bottom: 0.5rem;
		}

		@include media-breakpoint-up(lg) {
			padding: 3rem 3rem 1.5rem;
		}
	}

	.card-img-top + .card-block {
		border-top-color: $white;
	}

	.btn {
		margin-bottom: 0.5rem;
	}
}

a[href].card-feature::after { 
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

// Card groups
// Only set up to use .card-feature

.card-group {

	& + & {
		margin-top: -1px;
	}

	&:last-child {
		margin-bottom: $spacer;
	}

	.card-feature {
		font-size: 0.875rem; // 14px; smaller font to allow for 4-up
	}

	@include media-breakpoint-between(sm, md) {
		.card-feature {
			.card-img-top {
				margin-bottom: 0;
			}

			.card-block {
				display: flex;
				flex-flow: column nowrap;
				justify-content: center;
				border-top-color: $card-border-color;
			}
		}
	}

	@include media-breakpoint-up(sm) {
		.card-feature {
			display: flex;
			flex: 0 0 100%;
			flex-flow: row nowrap;
			align-items: flex-start;

			+ .card-feature {
				margin-left: 0; // override bootstrap default
			}

			.card-img-top,
			.card-img-top + .card-block {
				flex: 1 0 50%;
				max-width: 50%;
			}

			.card-block {
				flex: 1 0 100%;
				max-width: 100%;
				align-self: stretch;
			}
		}
	}

	@include media-breakpoint-up(md) {
		.card-feature {

			+ .card-feature {
				margin-top: -1px;
			}

			.card-img-top {
				flex: 1 0 30%;

				+ .card-block {
					flex: 1 0 70%;
					max-width: 70%;
				}
			}
		}
	}

	@include media-breakpoint-up(lg) {
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;

		.card-feature {
			flex-flow: column nowrap;
			flex: 1 1 33.33333%;
			max-width: 33.33333%;

			.card-img-top {
				max-width: 100%;
				flex: 0 1 auto;
				min-height: 1px;
				margin-bottom: -1px;

				+ .card-block {
					flex: 1 0 auto;
					min-height: 1px;
					max-width: 100%;
				}
			}

			.card-block {
				flex: 1 0 auto;
				min-height: 1px;
				padding-left: 1.5rem;
				padding-right: 1.5rem;
			}
		}

		.card-feature + .card-feature {
			margin-top: 0;
		}

		.card-feature:not(:last-child) .card-block {
			border-right-color: $white;
		}
	}
}

.card-deck,
.carousel-card {
	@include media-breakpoint-up(sm) {
		.card-feature {
			min-height: 1px;
			display: flex;
			flex-flow: column nowrap;

			.card-img-top {
				flex: 0 1 auto;
				min-height: 1px;
				margin-bottom: -1px;
			}

			.card-block {
				flex: 1 0 auto;
				min-height: 1px;
			}
		}
	}

	@include media-breakpoint-up(lg) {
		&.card-deck-three-up,
		&.card-deck-four-up {
			.card-block {
				padding-left: $spacer;
				padding-right: $spacer;
			}
		}
	}
}

.carousel-card {
	.card-feature:not(:first-child) .card-block {
		border-left: 0;
	}
}
